import { IJob, IJobCollaboration } from "../entities/job";
import { IApiList, IApiListParams, URI, UUID } from "./index";

export type CreateJobResponse = IJob;

export type IGetJobs = IApiList<IJob>;

export type AddJobContentRequest = string;

export interface IAddJobContentResponse {
  success: boolean;
}

export interface IJobContentRequest {
  role: string;
  uri: URI;
  readonly?: boolean;
  name?: string;
  size?: number;
  type?: string;
}

export interface ICreateJobRequest {
  type: string; // job-type id
  collaboration?: IJobCollaboration;
  contents: Array<IJobContentRequest>;
  [additional: string]: any;
}

export type GetJobContentResponse = string;

export interface IGetBlockedJobsRequest extends IApiListParams {
  status: "blocked";
  blocking_task: string;
}

export interface ICheckoutJobRequest {
  jobId: UUID;
}

export type CheckoutJobResponse = IJob;

export enum JobCheckinStatus {
  ABORTED = "aborted",
  COMPLETED = "completed",
  FAILED = "failed",
  CLAIMED = "claimed",
}

export interface ICheckinJobRequest {
  message?: string;
  status: JobCheckinStatus;
}

export interface ICheckinJobResponse {
  success: boolean;
}
