import React from "react";
import { isDataPointValid, isSupportValid } from "../../state/areAnswersValid";
import {
  IDataPoint,
  IDataPointGroup,
  ITaxonomy,
  TaxonomyEntry,
  TaxonomyEntryType,
} from "../../types";

interface IProps {
  dataPoint: IDataPoint | IDataPointGroup;
  index: number;
  taxonomy: ITaxonomy;
  taxonomyEntry: TaxonomyEntry;
}

export const InactiveQuestion = ({ dataPoint, index, taxonomy, taxonomyEntry }: IProps) => {
  const contentClasses = ["align-items-center", "d-flex"];
  let details = null;
  if (dataPoint.isGroup && taxonomyEntry.type === TaxonomyEntryType.Group) {
    if (taxonomyEntry.required && dataPoint.values.length < 1) {
      contentClasses.push("text-danger");
      details = <small>&#x2731; Required</small>;
    } else if (dataPoint.values.length > 0) {
      const invalidValue = dataPoint.values.some(v =>
        Object.keys(v).some(k =>
          v[k].some(
            innerValue =>
              !isDataPointValid(innerValue, taxonomy[k]) ||
              !isSupportValid(innerValue, taxonomy[k]),
          ),
        ),
      );
      if (invalidValue) {
        contentClasses.push("text-danger");
        details = <small>&#x2731; Invalid fields</small>;
      } else {
        contentClasses.push("text-success");
        details = <small>&#x2714; Complete</small>;
      }
    }
  } else if (!dataPoint.isGroup && taxonomyEntry.type !== TaxonomyEntryType.Group) {
    if (dataPoint.values.length > 0) {
      const missingSupport = dataPoint.values.filter(v => v.idRef === null);
      const emptyValues = dataPoint.values.filter(v => v.valueRaw.trim().length < 1);
      if (taxonomyEntry.requiresSupport && missingSupport.length > 0) {
        contentClasses.push("text-danger");
        details = <small>&#x2731; Requires support</small>;
      } else if (taxonomyEntry.required && emptyValues.length > 0) {
        contentClasses.push("text-danger");
        details = <small>&#x2731; Required</small>;
      } else {
        contentClasses.push("text-success");
        const text = `${dataPoint.values.length} answer${dataPoint.values.length > 1 ? "s" : ""}`;
        details = <small>&#x2714; {text}</small>;
      }
    } else if (taxonomyEntry.required) {
      contentClasses.push("text-danger");
      details = <small>&#x2731; Required</small>;
    }
  } else {
    throw new Error("Invalid state.");
  }
  return (
    <div className={contentClasses.join(" ")}>
      <div className="flex-fill text-truncate">
        <span className="question-title" title={dataPoint.label}>
          {index}. {dataPoint.label}
        </span>
      </div>
      {details}
    </div>
  );
};
InactiveQuestion.displayName = "InactiveQuestion";
