import { IDataPoint, IDataPointGroup, IDataPointValue, IState } from "../../types";
import { newDefaultValue } from "../util";

export interface IAddNewAnswerAction {
  copyValue: IDataPointValue<any> | null;
  groupIndex: number;
  groupKey: string;
  key: string;
  type: "ADD NEW ANSWER";
}

export const addNewAnswer = (state: IState, action: IAddNewAnswerAction): IState => {
  if (action.groupKey in state.dataPoints) {
    if (state.dataPoints[action.groupKey].isGroup) {
      const group = state.dataPoints[action.groupKey] as IDataPointGroup;
      if (
        group.values[action.groupIndex] === undefined ||
        !(action.key in group.values[action.groupIndex])
      ) {
        return state;
      }
      const newValue =
        action.copyValue === null
          ? newDefaultValue(group.dataPoints[action.key].type)
          : { ...action.copyValue };
      return {
        ...state,
        activeDataPoint: {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index: group.values[action.groupIndex][action.key].length,
          key: action.key,
        },
        dataPoints: {
          ...state.dataPoints,
          [action.groupKey]: {
            ...group,
            values: [
              ...group.values.slice(0, action.groupIndex),
              {
                ...group.values[action.groupIndex],
                [action.key]: [...group.values[action.groupIndex][action.key], newValue],
              },
              ...group.values.slice(action.groupIndex + 1),
            ],
          },
        },
      };
    } else if (action.groupKey === action.key) {
      const nonGroup = state.dataPoints[action.key] as IDataPoint;
      const newValue =
        action.copyValue === null ? newDefaultValue(nonGroup.type) : { ...action.copyValue };
      return {
        ...state,
        activeDataPoint: {
          groupIndex: 0,
          groupKey: action.key,
          index: nonGroup.values.length,
          key: action.key,
        },
        dataPoints: {
          ...state.dataPoints,
          [action.key]: {
            ...nonGroup,
            values: [...nonGroup.values, newValue],
          },
        },
      };
    }
  }
  return state;
};
