import { IWithHistory } from "../../../util/withHistory";
import { Point, Size } from "../Renderer/types";
import { IApiZone, IPage, IZone } from "../types";

export interface ISelection {
  endx: number;
  endy: number;
  startx: number;
  starty: number;
}

export enum ResizeHandlerPosition {
  TOP,
  RIGHT,
  BOTTOM,
  LEFT,
}

export enum ZoomLevel {
  "1x" = 1,
  "1.5x" = 1.5,
  "2x" = 2,
  "2.5x" = 2.5,
  "3x" = 3,
}

export enum CurrentAction {
  CREATING_ZONE,
  HOVERING_NOTHING,
  HOVERING_ZONE,
  MOVING,
  RESIZING_BOTTOM,
  RESIZING_LEFT,
  RESIZING_RIGHT,
  RESIZING_TOP,
  SELECTING,
}

export interface IPageState extends IPage {
  readonly activeZones: Array<number>;
  readonly currentAction: CurrentAction;
  readonly dimensions: Size;
  readonly firstDragEvent: boolean;
  readonly hoveredZone: number;
  readonly image: HTMLImageElement | null;
  readonly noiseReductionLevel: number;
  readonly previousMouseLocation: Point | null;
  readonly selection: ISelection | null;
  readonly zoneHistory: IWithHistory<Array<IZone>>;
}

export interface IState {
  readonly activePage: number;
  readonly autoShrink: boolean;
  readonly currentLabel: string;
  readonly pages: Array<IPageState>;
  readonly zoom: ZoomLevel;
}

interface IChangeCurrentLabel {
  label: string;
  type: "CHANGE CURRENT LABEL";
}
interface IChangeZoomLevelAction {
  type: "CHANGE ZOOM LEVEL";
  zoom: ZoomLevel;
}
interface IKeyPressedAction {
  ctrlKey: boolean;
  shiftKey: boolean;
  key: string;
  keyCode: number;
  preventDefault: () => void;
  type: "KEY PRESSED";
}
interface IMouseDownAction {
  ctrlKey: boolean;
  location: Point;
  resizeHandlerPosition: ResizeHandlerPosition | null;
  shiftKey: boolean;
  type: "MOUSE DOWN";
}
interface IMouseEnterAction {
  type: "MOUSE ENTER";
}
interface IMouseLeaveAction {
  type: "MOUSE LEAVE";
}
interface IMouseMoveAction {
  imageSize: Size;
  location: Point;
  type: "MOUSE MOVE";
}
interface IMouseUpAction {
  location: Point;
  type: "MOUSE UP";
}
interface IPopulateZonesAction {
  index: number;
  type: "POPULATE ZONES";
  zones: Array<IApiZone>;
}
interface IPopulatePersistedZonesAction {
  index: number;
  type: "POPULATE PERSISTED ZONES";
  zones: Array<IZone>;
}
interface ISelectPageAction {
  page: number;
  type: "SELECT PAGE";
}
interface ISetNoiseReductionLevelAction {
  image: HTMLImageElement;
  index: number;
  noiseReduction: number;
  type: "SET NOISE REDUCTION LEVEL";
}
interface IToggleAutoShrink {
  type: "TOGGLE AUTO SHRINK";
}
export type Action =
  | IChangeCurrentLabel
  | IChangeZoomLevelAction
  | IKeyPressedAction
  | IMouseDownAction
  | IMouseEnterAction
  | IMouseLeaveAction
  | IMouseMoveAction
  | IMouseUpAction
  | IPopulatePersistedZonesAction
  | IPopulateZonesAction
  | ISelectPageAction
  | ISetNoiseReductionLevelAction
  | IToggleAutoShrink;

export type Dispatch = (a: Action) => void;
