import { IState } from "../../types";

export interface ISelectActiveDataPointAction {
  groupIndex: number;
  groupKey: string;
  index: number;
  key: string;
  type: "SELECT ACTIVE DATA POINT";
}

export const selectActiveDataPoint = (
  state: IState,
  action: ISelectActiveDataPointAction,
): IState => {
  if (
    action.groupKey === state.activeDataPoint.groupKey &&
    action.groupIndex === state.activeDataPoint.groupIndex &&
    action.key === state.activeDataPoint.key &&
    action.index === state.activeDataPoint.index
  ) {
    return state;
  }
  return {
    ...state,
    activeDataPoint: {
      groupIndex: action.groupIndex,
      groupKey: action.groupKey,
      index: action.index,
      key: action.key,
    },
  };
};
