import { UserActions } from "../actions/user";
import { IUserState } from "../types";
import { runners } from "./handlers";

type State = Readonly<IUserState>;

export const user = (state: State, action: UserActions): State => {
  switch (action.type) {
    case "SET_APP_TITLE":
      return { ...state, appTitle: action.payload };
    case "SET_ROLE_WHITELIST":
      return { ...state, roleWhitelist: action.payload };
    case "SET_BLOCKINGTASK_KIND":
      return { ...state, blockingTaskKind: action.payload };
    case "TOGGLE_AUTOMATICALLY_LOADNEXT":
      return { ...state, automaticallyLoadNextJob: !state.automaticallyLoadNextJob };
    default:
      break;
  }

  return runners.userRunner(state, action);
};
