import cx from "classnames";
import React, { useMemo } from "react";

import { IDataPoint, IDataPointValue, TaxonomyEntry, TaxonomyEntryType } from "../../types";

import { isDataPointValid } from "../../state/areAnswersValid";
import { AddButton } from "../AddButton";
import { DuplicateButton } from "../DuplicateButton";
import { RemoveButton } from "../RemoveButton";
import { BooleanInput } from "./BooleanInput";
import { IInputComponentProps } from "./common";
import { DateTimeInput } from "./DateTimeInput";
import { EnumInput } from "./EnumInput";
import { NumberInput } from "./NumberInput";
import { PointerInput } from "./PointerInput";
import { StringInput } from "./StringInput";

interface IProps {
  active: boolean;
  dataPoint: IDataPoint;
  disableAdd: boolean;
  disableRemove: boolean;
  index: number;
  label: string;
  onAddNewAnswer: () => void;
  onDuplicateAnswer: () => void;
  onFocus: () => void;
  onRemoveAnswer: () => void;
  onUpdateAnswer: (value: any, valueRaw: string) => void;
  showLabel: boolean;
  showMultiple: boolean;
  taxonomyEntry: TaxonomyEntry;
  value: IDataPointValue<any>;
}

export const InputComponentMap = {
  boolean: BooleanInput,
  date: DateTimeInput,
  datetime: DateTimeInput,
  enum: EnumInput,
  number: NumberInput,
  pointer: PointerInput,
  selection: StringInput,
  string: StringInput,
  time: DateTimeInput,
};

export const Answer = ({
  active,
  dataPoint,
  disableAdd,
  disableRemove,
  index,
  label,
  onAddNewAnswer,
  onDuplicateAnswer,
  onFocus,
  onRemoveAnswer,
  onUpdateAnswer,
  showLabel,
  showMultiple,
  taxonomyEntry,
  value,
}: IProps) => {
  let inputComponent = null;
  let requiresSupport = false;
  if (taxonomyEntry.type !== TaxonomyEntryType.Group) {
    if (taxonomyEntry.required) {
      requiresSupport = taxonomyEntry.requiresSupport;
    } else if (taxonomyEntry.requiresSupport) {
      if (value.valueRaw.length > 0) {
        requiresSupport = true;
      }
    }
  }
  const invalid = useMemo(() => !isDataPointValid(value, taxonomyEntry), [value, taxonomyEntry]);
  if (dataPoint.type in InputComponentMap) {
    inputComponent = React.createElement<IInputComponentProps<any>>(
      InputComponentMap[dataPoint.type],
      {
        dataPointKey: dataPoint.key,
        index,
        invalid,
        onFocus,
        onUpdateAnswer,
        showAlerts: false,
        taxonomyEntry,
        value,
      },
    );
  }
  return (
    <div className={cx("grouped-row", { active })}>
      <div className="grouped-column column-support">
        <a
          className={cx({
            "text-danger": value.idRef === null && requiresSupport,
            "text-muted": value.idRef === null && !requiresSupport,
            "text-primary": value.idRef !== null,
          })}
          href="#"
          onClick={e => {
            e.preventDefault();
            onFocus();
          }}
          title="Support"
        >
          <i className="fa fa-arrow-left" />
        </a>
      </div>
      {showLabel && (
        <div
          className={cx("grouped-column", "column-label", { "text-danger": invalid })}
          onClick={() => {
            onFocus();
          }}
        >
          {label}
        </div>
      )}
      <div className={cx("grouped-column column-input", { "full-width": !showLabel })}>
        {inputComponent}
      </div>
      {showMultiple && (
        <>
          <div className="grouped-column column-add">
            <AddButton disabled={disableAdd} onClick={onAddNewAnswer} />
          </div>
          <div className="grouped-column column-duplicate">
            <DuplicateButton disabled={disableAdd} onClick={onDuplicateAnswer} />
          </div>
        </>
      )}
      <div className="grouped-column column-remove">
        <RemoveButton clear={disableRemove} onClick={onRemoveAnswer} />
      </div>
    </div>
  );
};
Answer.displayName = "Answers";
