import React from "react";
import { Form, FormGroup, Input, Label } from "reactstrap";

interface IProps {
  readonly loadNext: boolean;
  readonly onToggleLoadNext: () => void;
}

export const Options = ({ loadNext, onToggleLoadNext }: IProps) => {
  return (
    <div className="border-bottom p-2">
      <Form inline>
        <FormGroup check>
          <Input
            id="toggle-auto-next"
            type="checkbox"
            checked={loadNext}
            onChange={onToggleLoadNext}
          />
          <Label for="toggle-auto-next">Automatically load next job on save</Label>
        </FormGroup>
      </Form>
    </div>
  );
};
Options.displayName = "Options";
