import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import { IJob } from "@ilabs/core";

type ChildArgs = Readonly<{
  job: IJob | null;
  jobError: Error | null;
  token: string;
}>;

type Props = Readonly<{
  children: (args: ChildArgs) => React.ReactElement;
}>;

export const JobManager = ({ children }: Props) => {
  const [job, setJob] = useState<ChildArgs["job"]>(null);
  const [jobError, setJobError] = useState<ChildArgs["jobError"]>(null);
  const { jobId, token } = useParams();
  useEffect(() => {
    fetch(`https://api.innodata.com/v1/jobs/${jobId}`, {
      headers: {
        Authorization: `Basic ${token}`,
      },
      method: "GET",
    })
      .then(res => res.json())
      .then(res => {
        setJob(res.response);
        setJobError(null);
      })
      .catch(err => {
        setJob(null);
        setJobError(err);
      });
  }, [jobId]);
  return children({ job, jobError, token });
};
JobManager.displayName = "JobManager";
