/**
 * Removes a field from the input.
 * @param field string The field name as a string.
 * @param input any The input value that may contain the field.
 * @param recurse boolean (optional) If true, we recurse as deep as needed to remove the field.
 *                Defaults to true.
 * @return Returns the input without the field.
 */
export const removeFieldFromObject = (field: string, input: any, recurse: boolean = true): any => {
  if (input === null || typeof input !== "object") {
    return input;
  }
  if (Array.isArray(input)) {
    if (!recurse) {
      return input;
    }
    return input.map(e => removeFieldFromObject(field, e, true));
  }
  const toReturn = {};
  for (const key of Object.keys(input)) {
    if (key.trim().toLowerCase() === field) {
      continue;
    }
    toReturn[key] = recurse ? removeFieldFromObject(field, input[key], true) : input[key];
  }
  return toReturn;
};
