/**
 * Ensures that any objects (or sub-objects of any depth) have their keys in sorted order.
 * @param input The input object to be sorted. If the input is not an object, it will be returned
 *              directly. For arrays, each element of the array will be recursively processed by
 *              this function. For objects, the object is returned with the keys in sorted order.
 * @return Returns the input directly for non-objects and arrays, and objects with sorted keys.
 */
export const sortObjectKeys = <T>(input: T): T => {
  if (Array.isArray(input)) {
    return input.reduce((output, next, index) => {
      const newValue = sortObjectKeys(next);
      if (newValue === next) {
        return output;
      }
      if (output === input) {
        return [...output.slice(0, index), newValue, ...output.slice(index + 1)];
      }
      output[index] = newValue;
      return output;
    }, input);
  }
  if (typeof input === "object") {
    if (input === null) {
      return input;
    }
    const keys = Object.keys(input);
    for (let i = 0; i < keys.length; i++) {
      if (sortObjectKeys(input[keys[i]]) !== input[keys[i]] || (i > 0 && keys[i] < keys[i - 1])) {
        // either a value has changed in the recursive call, or the keys are out of order so in both
        // cases we simply sort the keys and return a new object reference
        const sortedKeys = keys.sort();
        return sortedKeys.reduce((prev, next) => {
          prev[next] = sortObjectKeys(input[next]);
          return prev;
        }, {}) as T;
      }
    }
    return input;
  }
  return input;
};
