import { IDataPointGroup, IState } from "../../types";
import { newDefaultValue } from "../util";

export interface IRemoveGroupAction {
  groupIndex: number;
  groupKey: string;
  type: "REMOVE GROUP";
}

export const removeGroup = (state: IState, action: IRemoveGroupAction): IState => {
  if (action.groupKey in state.dataPoints && state.dataPoints[action.groupKey].isGroup) {
    let group = state.dataPoints[action.groupKey] as IDataPointGroup;
    if (Object.keys(group.dataPoints).length < 1 || group.values[action.groupIndex] === undefined) {
      return state;
    }
    let activeDataPoint = state.activeDataPoint;
    if (group.values.length < 2) {
      activeDataPoint = {
        groupIndex: 0,
        groupKey: action.groupKey,
        index: 0,
        key: Object.keys(group.dataPoints)[0],
      };
      const newValue = {};
      for (const dataPointKey of Object.keys(group.dataPoints)) {
        newValue[dataPointKey] = [newDefaultValue(group.dataPoints[dataPointKey].type)];
      }
      group = {
        ...group,
        collapsedValues: group.collapsedValues.filter(v => v !== action.groupIndex),
        values: [newValue],
      };
    } else {
      activeDataPoint = {
        groupIndex:
          state.activeDataPoint.groupIndex >= action.groupIndex
            ? state.activeDataPoint.groupIndex - 1
            : state.activeDataPoint.groupIndex,
        groupKey: action.groupKey,
        index: 0,
        key: Object.keys(group.dataPoints)[0],
      };
      group = {
        ...group,
        collapsedValues: group.collapsedValues.filter(v => v !== action.groupIndex),
        values: [
          ...group.values.slice(0, action.groupIndex),
          ...group.values.slice(action.groupIndex + 1),
        ],
      };
    }
    return {
      ...state,
      activeDataPoint,
      dataPoints: {
        ...state.dataPoints,
        [action.groupKey]: group,
      },
    };
  }
  return state;
};
