const APP_STATE_KEY = "APP_STATE";
const getStorageKey = (appId: string, jobId: string): string =>
  `${APP_STATE_KEY}_${appId}_${jobId}`;

type NULL_RESPONSE = [null, null];
const nullResponse: NULL_RESPONSE = [null, null];
/**
 * Returns the persisted state or null if no state could be found.
 * @param version string The version string to versionize the state.
 * @return Partial<S> | null Returns the persisted (partial) state or null.
 */
export const getPersistentState = <S>(
  appId: string,
  jobId: string,
): [string, Partial<S>] | NULL_RESPONSE => {
  try {
    const result = window.localStorage.getItem(getStorageKey(appId, jobId));
    if (typeof result === "string") {
      const parsedResult = JSON.parse(result);
      if (
        typeof parsedResult === "object" &&
        parsedResult !== null &&
        !Array.isArray(parsedResult) &&
        typeof parsedResult.version === "string" &&
        typeof parsedResult.data !== "undefined"
      ) {
        return [parsedResult.version, parsedResult.data];
      }
      return nullResponse;
    }
    return nullResponse;
  } catch (err) {
    return nullResponse;
  }
};

/**
 * Persists the given state.
 * @param version string The version string to versionize the state.
 * @param state Partial<S> The (partial) state to persist.
 * @return boolean Returns true on success and false on failure.
 */
export const setPersistentState = <S>(
  appId: string,
  jobId: string,
  version: string,
  data: Partial<S>,
): boolean => {
  try {
    window.localStorage.setItem(getStorageKey(appId, jobId), JSON.stringify({ version, data }));
    return true;
  } catch (err) {
    return false;
  }
};
