import cx from "classnames";
import moment from "moment";
import React from "react";
import { Col, Collapse, Form, Row } from "reactstrap";

import { WorkbenchDateFormats } from "../../../../util/dateFormats";
import { StateCallbacks } from "../../state";
import { IDataPoint, IDataPointGroup, IState, ITaxonomy, TaxonomyEntryType } from "../../types";
import { AddButton } from "../AddButton";
import { Answer } from "../Answer";
import { DuplicateButton } from "../DuplicateButton";
import { RemoveButton } from "../RemoveButton";
import { QuestionTitle } from "./QuestionTitle";

interface IProps {
  readonly active: IState["activeDataPoint"];
  readonly group: IDataPointGroup;
  readonly index: number;
  readonly stateCallbacks: StateCallbacks;
  readonly taxonomy: ITaxonomy;
}

const getTitleOfCollapsedValues = (group: IDataPointGroup, index: number): string => {
  for (const dataPointKey of Object.keys(group.dataPoints)) {
    const dataPoint = group.dataPoints[dataPointKey] as IDataPoint;
    switch (dataPoint.type) {
      case TaxonomyEntryType.Date:
      case TaxonomyEntryType.DateTime:
      case TaxonomyEntryType.Time:
        for (const value of group.values[index][dataPointKey]) {
          if (value.valueRaw.length > 0) {
            return `${dataPoint.label}: ${moment(value.value).format(
              WorkbenchDateFormats[dataPoint.type],
            )}`;
          }
        }
        break;
      case TaxonomyEntryType.Number:
        for (const value of group.values[index][dataPointKey]) {
          if (value.valueRaw.length > 0) {
            return `${dataPoint.label}: ${value.value.toString(10)}`;
          }
        }
        break;
      case TaxonomyEntryType.Enum:
      case TaxonomyEntryType.Selection:
      case TaxonomyEntryType.String:
        for (const value of group.values[index][dataPointKey]) {
          if (value.valueRaw.length > 0) {
            return `${dataPoint.label}: ${value.value}`;
          }
        }
        break;
      default:
        break;
    }
  }
  return "(Empty)";
};

export const ActiveGroupQuestion = ({
  active,
  group,
  index,
  stateCallbacks: {
    onAddNewAnswer,
    onAddNewGroupAnswer,
    onDuplicateAnswer,
    onDuplicateGroupAnswer,
    onRemoveAnswer,
    onRemoveGroup,
    onSelectActiveDataPoint,
    onToggleGroupCollapse,
    onUpdateGroupedAnswer,
  },
  taxonomy,
}: IProps) => {
  const showMultiple = Object.keys(group.dataPoints).some(dp => group.dataPoints[dp].multiple);
  return (
    <Form
      onSubmit={e => {
        e.preventDefault();
      }}
    >
      <QuestionTitle index={index} label={group.label} />
      <Row>
        <Col xs={12}>
          {group.values.map((v, i) => {
            const collapsed = group.collapsedValues.includes(i);
            return (
              <div
                className={cx("grouped-container", {
                  border: group.multiple,
                  "border-secondary": group.multiple,
                })}
                key={`group-${group.key}-value-${i}`}
              >
                {group.multiple && (
                  <div className="d-flex">
                    <div
                      className={cx("group-title", "mr-auto", "text-muted", "text-truncate", {
                        visible: collapsed,
                      })}
                      onClick={() => {
                        if (!collapsed) {
                          return;
                        }
                        onToggleGroupCollapse(group.key, i);
                      }}
                    >
                      {getTitleOfCollapsedValues(group, i)}
                    </div>
                    <div className="mr-3">
                      <a
                        href="#"
                        onClick={e => {
                          e.preventDefault();
                          onToggleGroupCollapse(group.key, i);
                        }}
                        title="Collapse/Expand"
                      >
                        <i
                          className={cx("fa", {
                            "fa-caret-down": collapsed,
                            "fa-caret-up": !collapsed,
                          })}
                        />
                      </a>
                    </div>
                    <div className="mr-2">
                      <AddButton
                        disabled={false}
                        onClick={() => {
                          onAddNewGroupAnswer(group.key);
                        }}
                      />
                    </div>
                    <div className="mr-2">
                      <DuplicateButton
                        disabled={false}
                        onClick={() => {
                          onDuplicateGroupAnswer(group.key, v);
                        }}
                      />
                    </div>
                    <div>
                      <RemoveButton
                        clear={group.values.length < 2}
                        onClick={() => {
                          onRemoveGroup(group.key, i);
                        }}
                      />
                    </div>
                  </div>
                )}
                <Collapse isOpen={!collapsed}>
                  {group.keyOrder.map(dataPointKey =>
                    v[dataPointKey].map((innerValue, j) => (
                      <Answer
                        active={
                          active.groupKey === group.key &&
                          active.groupIndex === i &&
                          active.key === dataPointKey &&
                          active.index === j
                        }
                        dataPoint={group.dataPoints[dataPointKey] as IDataPoint}
                        disableAdd={!group.dataPoints[dataPointKey].multiple}
                        disableRemove={
                          !group.dataPoints[dataPointKey].multiple || v[dataPointKey].length < 2
                        }
                        key={`answer-${j}`}
                        index={j}
                        label={group.dataPoints[dataPointKey].label}
                        onAddNewAnswer={() => {
                          onAddNewAnswer(i, group.key, dataPointKey);
                        }}
                        onDuplicateAnswer={() => {
                          onDuplicateAnswer(i, group.key, dataPointKey, innerValue);
                        }}
                        onFocus={() => {
                          onSelectActiveDataPoint(group.key, i, dataPointKey, j);
                        }}
                        onRemoveAnswer={() => {
                          if (dataPointKey in taxonomy) {
                            onRemoveAnswer(
                              group.key,
                              i,
                              dataPointKey,
                              j,
                              taxonomy[dataPointKey].type,
                            );
                          }
                        }}
                        onUpdateAnswer={(value: any, valueRaw: string) => {
                          onUpdateGroupedAnswer(group.key, i, dataPointKey, j, value, valueRaw);
                        }}
                        showLabel
                        showMultiple={showMultiple}
                        taxonomyEntry={taxonomy[dataPointKey]}
                        value={innerValue}
                      />
                    )),
                  )}
                </Collapse>
              </div>
            );
          })}
        </Col>
      </Row>
    </Form>
  );
};
ActiveGroupQuestion.displayName = "ActiveGroupQuestion";
