import React from "react";

import { FormikProps } from "formik";

import { Subtext } from "../../../util/Form/Subtext";

export interface IFormValues {
  name: string;
  email: string;
  password: string;
}

export type Props = Readonly<FormikProps<IFormValues>>;

export function RegisterForm(props: Props) {
  const {
    handleSubmit,
    errors,
    isSubmitting,
    dirty,
    handleReset,
    values,
    handleChange,
    touched,
    handleBlur,
  } = props;

  return (
    <form onSubmit={handleSubmit}>
      <div className="form-group">
        <label htmlFor="name">Name</label>
        <input
          className="form-control"
          id="name"
          onBlur={handleBlur}
          onChange={handleChange}
          placeholder="full name"
          tabIndex={10}
          type="text"
          value={values.name}
        />
        <Subtext errors={errors} fieldKey="name" touched={touched} />
      </div>
      <div className="form-group">
        <label htmlFor="name">Email</label>
        <input
          className="form-control"
          id="email"
          onBlur={handleBlur}
          onChange={handleChange}
          placeholder="email"
          tabIndex={11}
          type="email"
          value={values.email}
        />
        <Subtext
          errors={errors}
          helperText="We'll never share your email with anyone else."
          fieldKey="email"
          touched={touched}
        />
      </div>
      <div className="form-group">
        <label htmlFor="name">Password</label>
        <input
          className="form-control"
          id="password"
          onBlur={handleBlur}
          onChange={handleChange}
          placeholder="password"
          tabIndex={12}
          type="password"
          value={values.password}
        />
        <Subtext errors={errors} fieldKey="password" touched={touched} />
      </div>
      <div className="btn-toolbar">
        <button className="btn btn-primary" disabled={isSubmitting} tabIndex={20} type="submit">
          Sign up
        </button>
        <button
          className="btn btn-outline-secondary ml-4"
          disabled={!dirty || isSubmitting}
          onClick={handleReset}
          tabIndex={21}
          type="button"
        >
          Reset
        </button>
      </div>
    </form>
  );
}
