import cx from "classnames";
import React, { memo } from "react";
import { ListGroup } from "reactstrap";

import { StateCallbacks } from "../state";
import { IState, ITaxonomy } from "../types";
import { Question } from "./Question";

type Props = Readonly<{
  review: boolean;
  state: IState;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}>;

export const Questions = memo(({ review, state, stateCallbacks, taxonomy }: Props) => (
  <ListGroup
    className={cx("h-100", "overflow-auto", { "border-top": !review, "border-left": review })}
    flush
  >
    {state.keyOrder.map((key, i) => {
      if (!(key in state.dataPoints)) {
        console.warn(`Key: ${key} not found in data points.`);
        return null;
      }
      return (
        <Question
          active={state.activeDataPoint}
          dataPoint={state.dataPoints[key]}
          key={key}
          index={i + 1}
          stateCallbacks={stateCallbacks}
          taxonomy={taxonomy}
        />
      );
    })}
  </ListGroup>
));
Questions.displayName = "Questions";
