import { Dispatch } from "react";

import { IUser } from "@ilabs/core";

import { IRegisterUserResult, reauthenticateUser, registerUser } from "../api";
import { AuthLevel, IUserState } from "../types";
import { ActionsUnion, createAction } from "./helpers";

export const REQUEST_GENERATE_USER = "REQUEST_GENERATE_USER";
export const RECEIVE_GENERATE_USER = "RECEIVE_GENERATE_USER";
export const RequestGenerateUser = () => createAction(REQUEST_GENERATE_USER);
export const ReceiveGenerateUser = (payload: IRegisterUserResult) =>
  createAction(RECEIVE_GENERATE_USER, payload);

export interface ISetCurrentUserPayload {
  authKey?: string;
  user: IUser;
}

export const SET_CURRENT_USER = "SET_CURRENT_USER";
export const SetCurrentUser = (payload: ISetCurrentUserPayload) =>
  createAction(SET_CURRENT_USER, payload);

export const SET_CURRENT_AUTHLEVEL = "SET_CURRENT_AUTHLEVEL";
export const SetCurrentAuthLevel = (payload: AuthLevel) =>
  createAction(SET_CURRENT_AUTHLEVEL, payload);

export const CLEAR_CURRENT_USER = "CLEAR_CURRENT_USER";
export const ClearCurrentUser = () => createAction(CLEAR_CURRENT_USER);

export const TOGGLE_ROOT_SIDEBAR = "TOGGLE_ROOT_SIDEBAR";
export const ToggleRootSidebar = () => createAction(TOGGLE_ROOT_SIDEBAR);

export const SET_APP_TITLE = "SET_APP_TITLE";
export const SetAppTitle = (payload: string) => createAction(SET_APP_TITLE, payload);

export const SET_ROLE_WHITELIST = "SET_ROLE_WHITELIST";
export const SetRoleWhitelist = (payload: string[]) => createAction(SET_ROLE_WHITELIST, payload);

export const SET_BLOCKINGTASK_KIND = "SET_BLOCKINGTASK_KIND";
export const SetBlockingTaskKind = (payload: string) =>
  createAction(SET_BLOCKINGTASK_KIND, payload);

export const SET_LOAD_NEXT = "SET_AUTOMATICALLY_LOADNEXT";
export const SetLoadNext = (next: boolean) => createAction(SET_LOAD_NEXT, next);
export const TOGGLE_LOAD_NEXT = "TOGGLE_AUTOMATICALLY_LOADNEXT";
export const ToggleLoadNext = () => createAction(TOGGLE_LOAD_NEXT);

export const JobActions = {
  ClearCurrentUser,
  ReceiveGenerateUser,
  RequestGenerateUser,
  SetAppTitle,
  SetBlockingTaskKind,
  SetCurrentAuthLevel,
  SetCurrentUser,
  SetLoadNext,
  SetRoleWhitelist,
  ToggleLoadNext,
  ToggleRootSidebar,
};

export type UserActions = ActionsUnion<typeof JobActions>;

export const useActions = (state: IUserState, dispatch: Dispatch<UserActions>) => {
  return {
    FetchGenerateUser: async () => {
      dispatch(RequestGenerateUser());
      dispatch(ReceiveGenerateUser(await registerUser({ demo: true })));
    },
    Reauthenticate: async (authKey: string) => {
      const { user, errors } = await reauthenticateUser(authKey);

      if (errors !== null) {
        dispatch(SetCurrentAuthLevel(AuthLevel.unauthenticated));

        return;
      }

      if (user === null) {
        return;
      }

      dispatch(SetCurrentUser({ authKey, user }));
    },
    SetAppTitle: (appTitle: string) => {
      dispatch(SetAppTitle(appTitle));
    },
    SetBlockingTaskKind: (kind: string) => {
      dispatch(SetBlockingTaskKind(kind));
    },
    SetLoadNext: (next: boolean) => {
      dispatch(SetLoadNext(next));
    },
    SetRoleWhitelist: (roleWhitelist: string[]) => {
      dispatch(SetRoleWhitelist(roleWhitelist));
    },
    SetTitle: (suffix: string) => {
      document.title = `${state.appTitle} - ${suffix}`;
    },
    ToggleLoadNext: () => {
      dispatch(ToggleLoadNext());
    },
  };
};
