import { Action, IState } from "../state";
import { page } from "./page";

const CURRENT_LABEL_MAP = {
  "1": "text",
  "2": "image",
  "3": "table",
  "4": "math",
};

export const reducer = (state: IState, action: Action): IState => {
  console.log("Action:", action);
  switch (action.type) {
    case "SELECT PAGE":
      if (action.page === state.activePage) {
        return state;
      }
      if (!state.pages[action.page]) {
        throw new Error("Invalid page number.");
      }
      return {
        ...state,
        activePage: action.page,
      };
    case "CHANGE CURRENT LABEL": {
      if (action.label === "") {
        return state;
      }
      const updatedZonePage = page(state.autoShrink, action.label)(
        state.pages[state.activePage],
        action,
      );
      let pages = state.pages;
      if (updatedZonePage !== state.pages[state.activePage]) {
        pages = [
          ...state.pages.slice(0, state.activePage),
          updatedZonePage,
          ...state.pages.slice(state.activePage + 1),
        ];
      }
      return {
        ...state,
        currentLabel: action.label,
        pages,
      };
    }
    case "CHANGE ZOOM LEVEL": {
      if (action.zoom === state.zoom) {
        return state;
      }
      return {
        ...state,
        zoom: action.zoom,
      };
    }
    case "TOGGLE AUTO SHRINK": {
      return {
        ...state,
        autoShrink: !state.autoShrink,
      };
    }
    case "KEY PRESSED": {
      if (action.key in CURRENT_LABEL_MAP) {
        action.preventDefault();
        const updatedZonePage = page(state.autoShrink, CURRENT_LABEL_MAP[action.key])(
          state.pages[state.activePage],
          { label: CURRENT_LABEL_MAP[action.key], type: "CHANGE CURRENT LABEL" },
        );
        let pages = state.pages;
        if (updatedZonePage !== state.pages[state.activePage]) {
          pages = [
            ...state.pages.slice(0, state.activePage),
            updatedZonePage,
            ...state.pages.slice(state.activePage + 1),
          ];
        }
        return {
          ...state,
          currentLabel: CURRENT_LABEL_MAP[action.key],
          pages,
        };
      }
      break;
    }
    case "SET NOISE REDUCTION LEVEL": {
      return {
        ...state,
        pages: [
          ...state.pages.slice(0, action.index),
          {
            ...state.pages[action.index],
            image: action.image,
            noiseReductionLevel: action.noiseReduction,
          },
          ...state.pages.slice(action.index + 1),
        ],
      };
    }
  }
  const updatedPage = page(state.autoShrink, state.currentLabel)(
    state.pages[state.activePage],
    action,
  );
  if (updatedPage === state.pages[state.activePage]) {
    return state;
  }
  // if we made any changes to the zones, mark the whole page as modified
  if (updatedPage.zones !== state.pages[state.activePage].zones) {
    updatedPage.modified = true;
  }
  let currentLabel = state.currentLabel;
  if (updatedPage.activeZones.length > 0) {
    currentLabel = updatedPage.activeZones.slice(1).reduce((prev, next) => {
      if (prev !== updatedPage.zones[next].label) {
        return "";
      }
      return prev;
    }, updatedPage.zones[updatedPage.activeZones[0]].label);
  }
  return {
    ...state,
    currentLabel,
    pages: [
      ...state.pages.slice(0, state.activePage),
      updatedPage,
      ...state.pages.slice(state.activePage + 1),
    ],
  };
};
