import React from "react";
import { Button } from "reactstrap";

import { JobCount } from "./JobCount";

interface IProps {
  readonly currentJobsOnPage: number;
  readonly currentPage: number;
  readonly fetching: boolean;
  readonly loadPage: (page: number) => void;
  readonly totalJobs: number;
  readonly totalPages: number;
}

export const Pagination = ({
  currentJobsOnPage,
  currentPage,
  fetching,
  loadPage,
  totalJobs,
  totalPages,
}: IProps) => {
  return (
    <div className="border-bottom p-2">
      <div className="d-flex justify-content-between align-items-center">
        <Button
          color="secondary"
          disabled={fetching || currentPage <= 0}
          onClick={() => {
            loadPage(Math.max(0, currentPage - 1));
          }}
          size="sm"
          type="button"
        >
          {"<<"}
        </Button>
        <div>
          Page {currentPage + 1} of {totalPages}
        </div>
        <Button
          color="secondary"
          disabled={fetching || currentPage >= totalPages - 1}
          onClick={() => {
            loadPage(Math.min(totalPages, currentPage + 1));
          }}
          size="sm"
          type="button"
        >
          {">>"}
        </Button>
      </div>
      <JobCount currentPageCount={currentJobsOnPage} totalCount={totalJobs} visible={!fetching} />
    </div>
  );
};
Pagination.displayName = "Pagination";
