import React from "react";

import { IJob, IUser, UUID } from "@ilabs/core";

interface IProps {
  name: string;
}

export function Greeting({ name }: IProps) {
  return <p>Hello, {name}!</p>;
}

export const greeting = (name: string) => console.log(`Hello, ${name}!`);

export enum JobCheckoutState {
  NO_JOB_SELECTED,
  JOB_SELECTED,
  JOB_CHECKING_OUT,
  JOB_CHECKED_OUT,
  JOB_FETCHING_CONTENT,
  JOB_READY,
  JOB_CHECKING_IN,
  JOB_CHECKED_IN,
}

type CheckinJobAborted = (message?: string) => Promise<void>;
type CheckinJobFailure = (message: string) => Promise<void>;
type CheckinJobSuccess = (message?: string) => Promise<void>;
type DeleteContent = (id: UUID) => Promise<void>;
type FetchContent = (id: UUID) => Promise<any>;
type SaveContent = (id: UUID, contents: string) => Promise<void>;

export interface IRenderJobOptions<C = string> {
  actions: {
    checkinJobAborted: CheckinJobAborted;
    checkinJobFailure: CheckinJobFailure;
    checkinJobSuccess: CheckinJobSuccess;
    deleteContent: DeleteContent;
    fetchContent: FetchContent;
    saveContent: SaveContent;
  };
  state: {
    contents: C | null;
    job: IJob | null;
    jobCheckoutState: JobCheckoutState;
    user: IUser | null;
  };
}
