import { IUser } from "@ilabs/core";

import { FetchLevel, IApiData } from "./main";

export interface IUserState {
  authLevel: AuthLevel;
  user: IApiData<IUser | null>;
  userAuth: string | null;
  hideSidebar: boolean;
  appTitle: string;
  roleWhitelist: string[];
  blockingTaskKind: string | null;
  automaticallyLoadNextJob: boolean;
}

export enum AuthLevel {
  initial,
  authenticated,
  unauthenticated,
}

export const defaultUserState: IUserState = {
  appTitle: "",
  authLevel: AuthLevel.initial,
  automaticallyLoadNextJob: false,
  blockingTaskKind: null,
  hideSidebar: false,
  roleWhitelist: [],
  user: {
    data: null,
    errors: {},
    fetchLevel: FetchLevel.initial,
  },
  userAuth: null,
};
