import React from "react";
import { Button, ButtonGroup, ButtonToolbar } from "reactstrap";

interface IProps {
  aborting: boolean;
  canSave: boolean;
  onSave: () => Promise<void>;
  saving: boolean;
}

export const ActionToolbar = ({ aborting, canSave, onSave, saving }: IProps) => {
  return (
    <ButtonToolbar className="actions d-flex justify-content-end p-2">
      <ButtonGroup>
        <Button
          color="primary"
          disabled={aborting || saving || !canSave}
          onClick={onSave}
          type="button"
        >
          Save
        </Button>
      </ButtonGroup>
    </ButtonToolbar>
  );
};
ActionToolbar.displayName = "ActionToolbar";
