import React from "react";
import { ListGroupItem } from "reactstrap";

import { StateCallbacks } from "../../state";
import { IDataPoint, IDataPointGroup, IState, ITaxonomy } from "../../types";
import { ActiveGroupQuestion } from "./ActiveGroupQuestion";
import { ActiveQuestion } from "./ActiveQuestion";
import { InactiveQuestion } from "./InactiveQuestion";

interface IProps {
  active: IState["activeDataPoint"];
  dataPoint: IDataPoint | IDataPointGroup;
  index: number;
  stateCallbacks: StateCallbacks;
  taxonomy: ITaxonomy;
}

export const Question = ({ active, dataPoint, index, stateCallbacks, taxonomy }: IProps) => {
  let contents = null;
  if (active.groupKey !== dataPoint.key) {
    contents = (
      <InactiveQuestion
        dataPoint={dataPoint}
        index={index}
        taxonomyEntry={taxonomy[dataPoint.key]}
        taxonomy={taxonomy}
      />
    );
  } else {
    if (dataPoint.isGroup) {
      contents = (
        <ActiveGroupQuestion
          active={active}
          group={dataPoint}
          index={index}
          stateCallbacks={stateCallbacks}
          taxonomy={taxonomy}
        />
      );
    } else {
      contents = (
        <ActiveQuestion
          active={active}
          dataPoint={dataPoint}
          index={index}
          stateCallbacks={stateCallbacks}
          taxonomy={taxonomy}
        />
      );
    }
  }
  return (
    <ListGroupItem
      onClick={() => {
        stateCallbacks.onSelectDataPoint(dataPoint.key);
      }}
    >
      {contents}
    </ListGroupItem>
  );
};
Question.displayName = "Question";
