import React, { useEffect, useMemo } from "react";

import { useImage } from "../../../util/useImage";
import { selectNoiseReduction } from "../helpers";
import { Action } from "../StateManager/state";
import { URI } from "../types";

interface IProps {
  active: boolean;
  confidence: number | null;
  dispatch: (action: Action) => void;
  highConfidenceThreshold: number;
  index: number;
  modified: boolean;
  noiseReductionLevel: number;
  onFocus: () => void;
  src: URI;
  total: number;
}

const CONFIDENCE_COLOR_MAP = {
  high: "rgba(0.0, 255.0, 0.0, 0.15)",
  low: "rgba(255.0, 0.0, 0.0, 0.15)",
};

export const PreviewImage = ({
  active,
  confidence,
  dispatch,
  highConfidenceThreshold,
  index,
  modified,
  noiseReductionLevel,
  onFocus,
  src,
  total,
}: IProps) => {
  const [image, status, data] = useImage(src, true);
  const confidenceLevel: "low" | "high" | null = useMemo(() => {
    if (confidence === null) {
      return null;
    }
    return confidence >= highConfidenceThreshold ? "high" : "low";
  }, [confidence, highConfidenceThreshold, modified]);
  const previewImageStyles = useMemo(() => {
    if (!image || status !== "loaded") {
      return {};
    }
    const backgroundImages = [];
    if (confidenceLevel !== null) {
      const fill = CONFIDENCE_COLOR_MAP[confidenceLevel];
      backgroundImages.push(`linear-gradient(${fill}, ${fill})`);
    }
    backgroundImages.push(`url(${data || image.src}`);
    return {
      backgroundImage: backgroundImages.join(","),
      backgroundPosition: "center center",
      backgroundRepeat: "no-repeat",
      backgroundSize: "contain",
    };
  }, [confidenceLevel, data, image, src, status]);
  const previewClassName = useMemo(() => {
    if (active) {
      return "image-preview active";
    }
    return "image-preview";
  }, [active]);
  useEffect(() => {
    if (image && status === "loaded" && noiseReductionLevel < 0) {
      dispatch({
        image,
        index,
        noiseReduction: selectNoiseReduction(image),
        type: "SET NOISE REDUCTION LEVEL",
      });
    }
  }, [status]);
  return (
    <div onClick={onFocus}>
      <div className={previewClassName}>
        {status === "loaded" ? (
          <div style={previewImageStyles} />
        ) : (
          <div className="d-flex align-items-center">
            <div className="ml-auto mr-auto spinner-border" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>
        )}
        <span>{`Page ${index + 1} of ${total}`}</span>
      </div>
    </div>
  );
};
