import moment from "moment";
import React from "react";

import { InnodomDateFormats } from "../../../util/dateFormats";
import { InnodomBlock } from "../../util/InnodomBlock";
import { extractFontInformation } from "../../util/InnodomBlock/useInnodomContent";
import {
  IBaseDataPoint,
  IDataPoint,
  IDataPointGroup,
  IDataPointValue,
  IState,
  ITaxonomy,
  TaxonomyEntryType,
} from "../types";
import { newDefaultValue } from "./util";

const TAG_CONTENT = "inno:content";
export const TAG_DATAPOINT = "inno:datapoint";
export const TAG_META = "inno:meta";
export const ATTR_CONFIDENCE = "confidence";
export const ATTR_KEY = "key";
const ATTR_ID = "id";
export const ATTR_ID_REF = "idref";

/**
 * Extracts a set of content blocks from Innodom format.
 * @param content Element The parsed Innodom <inno:content> element.
 * @return Returns an array of objects with an ID and a rendered Innodom block.
 */
const extractInitialBlocks = (content: Element): IState["blocks"] => {
  const blocks = [];
  const fontInformation = extractFontInformation(content);
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < content.childNodes.length; i++) {
    if (content.childNodes[i] instanceof Element) {
      const childNode = content.childNodes[i] as Element;
      let id = (childNode.getAttribute(ATTR_ID) || "").trim();
      if (id.length < 1) {
        id = `block-${i}`;
        childNode.setAttribute(ATTR_ID, id);
      }
      blocks.push({
        id,
        rendered: React.createElement(InnodomBlock, { block: childNode, fontInformation }),
        text: childNode.textContent || "",
      });
    }
  }
  return blocks;
};

interface IExtractedKeysAndIndexes {
  groupIndex: number;
  groupKey: string;
  index: number;
  key: string;
}
/**
 * Given a dot-separated data point key, extracts the data point key, group key, and both indexes.
 * Note: keys are extracted in lower case format for case-insensitive matching.
 * @param inputKey string The input string.
 * @return IExtractedKeysAndIndexes Returns up to the four parts of the key.
 */
const extractKeysAndIndexes = (inputKey: string): IExtractedKeysAndIndexes => {
  const keySplit = inputKey.split(".").map(s => s.trim().toLowerCase());
  let groupKey = "";
  let groupIndex = 0;
  let key = "";
  let index = 0;
  if (keySplit.length < 1 || keySplit.length > 4) {
    throw new Error(`Invalid data point key: "${inputKey}".`);
  }
  if (keySplit.length === 1) {
    key = keySplit[0];
  } else if (keySplit.length === 2) {
    key = keySplit[0];
    index = parseInt(keySplit[1], 10);
    if (isNaN(index)) {
      groupKey = keySplit[0];
      key = keySplit[1];
      index = 0;
    }
  } else if (keySplit.length === 3) {
    groupKey = keySplit[0];
    groupIndex = parseInt(keySplit[1], 10);
    if (isNaN(groupIndex)) {
      groupIndex = 0;
      key = keySplit[1];
      index = parseInt(keySplit[2], 0);
      if (isNaN(index)) {
        throw new Error(`Invalid data point key: "${inputKey}".`);
      }
    } else {
      key = keySplit[2];
    }
  } else {
    groupKey = keySplit[0];
    key = keySplit[2];
    groupIndex = parseInt(keySplit[1], 10);
    index = parseInt(keySplit[3], 10);
    if (isNaN(groupIndex) || isNaN(index)) {
      throw new Error(`Invalid data point key: "${inputKey}".`);
    }
  }
  return {
    groupIndex,
    groupKey,
    index,
    key,
  };
};

interface IExtractedDataPointValue<T> extends IExtractedKeysAndIndexes {
  value: IDataPointValue<T>;
}
/**
 * For a given <inno:datapoint> element, we map to our in-memory IDataPointValue model.
 * @param node Element the <inno:datapoint> element.
 * @return IExtractedDataPointValue Returns the data point value and the extract pieces from the
 *         key.
 */
const extractDataPointValueFromNode = (node: Element): IExtractedDataPointValue<any> => {
  let confidence = parseFloat(node.getAttribute(ATTR_CONFIDENCE) || "");
  if (isNaN(confidence)) {
    confidence = 0;
  }
  confidence = Math.max(0, Math.min(1, confidence));
  const idRef = (node.getAttribute(ATTR_ID_REF) || "").trim();
  const textContent = typeof node.textContent === "string" ? node.textContent.trim() : "";
  return {
    ...extractKeysAndIndexes(node.getAttribute(ATTR_KEY) || ""),
    value: {
      confidence,
      idRef: idRef.length > 0 ? idRef : null,
      value: textContent,
      valueRaw: textContent,
    },
  };
};

/**
 * Maps the contents of the <inno:metadata> nodes to an object where the keys are the data point
 * keys and the values are the extracted models.
 * @param meta Element The <inno:metadata> element.
 * @return Returns the extract models as values in an object, where the keys are the data point keys
 */
const mapNodesToDataPoints = (meta: Element): { [key: string]: IExtractedDataPointValue<any> } => {
  const nodes = {};
  const dataPointNodes = meta.getElementsByTagName(TAG_DATAPOINT);
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < dataPointNodes.length; i++) {
    const key = (dataPointNodes[i].getAttribute(ATTR_KEY) || "").trim().toLowerCase();
    if (key.length > 0) {
      nodes[key] = extractDataPointValueFromNode(dataPointNodes[i]);
    }
  }
  return nodes;
};

/**
 * A reducer function producer that maps the incoming taxonomy to our data points model.
 * @param taxonomy ITaxonomy The input taxonomy.
 * @return Returns a reducer function thatproduces the data point model.
 */
const taxonomyToDataPointMap = (taxonomy: ITaxonomy) => (
  prev: IState["dataPoints"],
  key: string,
): IState["dataPoints"] => {
  const current = taxonomy[key];
  if (current.type === TaxonomyEntryType.Group) {
    // skip this group definition...we will build it when we find our first data point that
    // belongs to the group
    return prev;
  }
  const newEntry: IBaseDataPoint = {
    isGroup: false,
    key,
    label: current.question || current.description || key,
    multiple: typeof current.multiple === "boolean" ? current.multiple : true,
    type: current.type,
  };
  let foundGroup = false;
  if (Array.isArray(current.groups)) {
    for (const groupElement of current.groups) {
      if (typeof groupElement !== "string") {
        continue;
      }
      const group = groupElement.trim();
      if (group === "") {
        // an empty group name indicates the data point should exist outside of any group as well
        prev[key] = { ...newEntry, isGroup: false, values: [] };
        continue;
      }
      if (!(group in taxonomy) || taxonomy[group].type !== TaxonomyEntryType.Group) {
        continue;
      }
      foundGroup = true;
      const groupEntry: IDataPointGroup =
        group in prev
          ? (prev[group] as IDataPointGroup)
          : {
              collapsedValues: [],
              dataPoints: {},
              isGroup: true,
              key: group,
              keyOrder: [],
              label: taxonomy[group].description || group,
              multiple: taxonomy[group].multiple,
              type: TaxonomyEntryType.Group,
              values: [],
            };
      groupEntry.dataPoints[key] = { ...newEntry, isGroup: false };
      prev[group] = groupEntry;
    }
  }
  if (!foundGroup) {
    prev[key] = { ...newEntry, isGroup: false, values: [] };
  }
  return prev;
};

/**
 * Converts a raw data point with a string value to a properly typed value
 * for some of the taxonomy types.
 * @param value IDataPointValue<any> The raw data point value.
 * @param type TaxonomyEntryType The desired target type.
 * @return Returns the normalized data point value against the type.
 */
const normalizeDataPointValue = (
  value: IDataPointValue<any>,
  type: TaxonomyEntryType,
): IDataPointValue<any> => {
  switch (type) {
    case TaxonomyEntryType.Boolean:
      return {
        ...value,
        value: value.valueRaw.toLowerCase() === "true",
      };
    case TaxonomyEntryType.Number: {
      const numberValue = parseFloat(value.valueRaw);
      return {
        ...value,
        value: isNaN(numberValue) ? 0 : numberValue,
      };
    }
    case TaxonomyEntryType.Date:
    case TaxonomyEntryType.DateTime:
    case TaxonomyEntryType.Time:
      return {
        ...value,
        value: moment(value.valueRaw, InnodomDateFormats[type]).toDate(),
      };
    default:
      return value;
  }
};

/**
 * Cleans up the data point state tree by filtering undefined values and adding missing keys
 * where needed.
 * @param dataPoints IState["dataPoints"] The dirty incoming state.
 * @param taxonomy ITaxonomy The initial taxonomy.
 * @return IState["dataPoints"] Returns the normalized and sanitized data point state.
 */
const normalizeDataPoints = (
  dataPoints: IState["dataPoints"],
  taxonomy: ITaxonomy,
): IState["dataPoints"] => {
  Object.keys(dataPoints).forEach(key => {
    if (dataPoints[key].isGroup) {
      // handle group logic
      const updatedGroup = dataPoints[key] as IDataPointGroup;
      // ensure every object has a key for each of the group members
      const defaultValues = {};
      for (const member of Object.keys(updatedGroup.dataPoints)) {
        defaultValues[member] = [];
      }
      dataPoints[key] = {
        ...updatedGroup,
        keyOrder: Object.keys(taxonomy).filter(k => k in updatedGroup.dataPoints),
        values: updatedGroup.values
          .map((v): any => {
            if (v === undefined) {
              return undefined;
            }
            const toReturn = {};
            // ensure every object has a key for each of the group members
            for (const member of Object.keys(updatedGroup.dataPoints)) {
              if (!(member in v)) {
                toReturn[member] = [newDefaultValue(taxonomy[member].type)];
                continue;
              }
              toReturn[member] = v[member]
                .map((m): any => {
                  if (m === undefined) {
                    return undefined;
                  }
                  return normalizeDataPointValue(m, updatedGroup.dataPoints[member].type);
                })
                .filter(m => m !== undefined);
            }
            return toReturn;
          })
          .filter(v => v !== undefined),
      };
      return;
    }
    let nonGroup = dataPoints[key] as IDataPoint;
    nonGroup = {
      ...nonGroup,
      values: nonGroup.values
        .map((v): any => {
          if (v === undefined) {
            return undefined;
          }
          return normalizeDataPointValue(v, nonGroup.type);
        })
        .filter(v => v !== undefined),
    };
    dataPoints[key] = nonGroup;
  });
  return dataPoints;
};

/**
 * For the given <inno:metadata> element and taxonomy, builds the entire data point models.
 * @param meta Element The <inno:metadata> element.
 * @param taxonomy ITaxonomy The input taxonomy.
 * @return IState["dataPoints"] Returns a data points model ready for the state.
 */
const extractDataPoints = (meta: Element, taxonomy: ITaxonomy): IState["dataPoints"] => {
  const dataPoints = Object.keys(taxonomy).reduce(taxonomyToDataPointMap(taxonomy), {});
  // extract the data points from the metadata into the models
  const nodes = mapNodesToDataPoints(meta);
  for (const nodeKey of Object.keys(nodes)) {
    const { groupIndex, groupKey, index, key, value } = nodes[nodeKey];
    if (groupKey !== "") {
      if (!(groupKey in dataPoints) || !dataPoints[groupKey].isGroup) {
        continue;
      }
      const updatedGroup = dataPoints[groupKey] as IDataPointGroup;
      if (updatedGroup.values[groupIndex] === undefined) {
        updatedGroup.values[groupIndex] = {};
      }
      if (!(key in updatedGroup.values[groupIndex])) {
        updatedGroup.values[groupIndex][key] = [];
      }
      updatedGroup.values[groupIndex][key][index] = value;
      dataPoints[groupKey] = updatedGroup;
      continue;
    }
    if (!(key in dataPoints) || dataPoints[key].isGroup) {
      continue;
    }
    dataPoints[key].values[index] = value;
  }
  return normalizeDataPoints(dataPoints, taxonomy);
};

export const getDefaultState = (taxonomy: ITaxonomy, xml: string): IState => {
  const parser = new DOMParser();
  const xmlDoc = parser.parseFromString(xml, "text/xml");
  const errors = xmlDoc.getElementsByTagName("parsererror");
  if (errors.length > 0) {
    throw new Error(`Error parsing XML: ${new XMLSerializer().serializeToString(xmlDoc)}`);
  }
  // return the root of the XML document
  const { documentElement } = xmlDoc;
  if (documentElement && documentElement instanceof Element) {
    let blocks: IState["blocks"] = [];
    let dataPoints: IState["dataPoints"] = {};
    const content = documentElement.getElementsByTagName(TAG_CONTENT);
    if (content.length > 0) {
      blocks = extractInitialBlocks(content[0]);
    }
    const metadata = documentElement.getElementsByTagName(TAG_META);
    if (metadata.length > 0) {
      dataPoints = extractDataPoints(metadata[0], taxonomy);
      // remove the metadata from its parent
      metadata[0].remove();
    }
    return {
      activeDataPoint: {
        groupIndex: -1,
        groupKey: "",
        index: -1,
        key: "",
      },
      blocks,
      dataPoints,
      keyOrder: Object.keys(taxonomy).filter(k => k in dataPoints),
      xmlRoot: xmlDoc,
    };
  }
  throw new Error("Invalid or empty XML.");
};
