import { IListedUser } from "./user";

export enum TeamRole {
  Member = "member",
  Admin = "admin",
}

export interface ITeamMember extends IListedUser {
  email: string;
  role: TeamRole;
  team_id: string;
}

export interface IListedTeam {
  belongs_to_team: boolean;
  can_manage: boolean;
  id: string;
  member_count: number;
  name: string;
  private: boolean;
}

export interface ITeam extends IListedTeam {}
