import { AuthenticationMethods, IUser } from "@ilabs/core";
import { withFormik, WithFormikConfig } from "formik";
import * as Yup from "yup";

import { loginUser } from "../../../../api";
import { IFormValues, LoginForm } from "../../../../components/Workbench/App/Viewport/LoginForm";
import { UserRules } from "../../../../validator-rules";

interface IFormProps {
  onUserLogin: (user: IUser) => void;
}

const config: WithFormikConfig<IFormProps, IFormValues> = {
  handleSubmit: async (values, { setSubmitting, setErrors, props }) => {
    const { user, errors } = await loginUser({
      ...values,
      authentication_method: AuthenticationMethods.Password,
    });
    if (errors !== null) {
      setErrors(errors);
      setSubmitting(false);

      return;
    }

    setSubmitting(false);
    props.onUserLogin(user!);
  },
  mapPropsToValues: () => {
    return {
      password: "",
      username: "",
    };
  },
  validationSchema: Yup.object().shape<IFormValues>({
    password: UserRules.password,
    username: UserRules.email,
  }),
};

export const LoginFormFormContainer = withFormik(config)(LoginForm);
