import { IDataPoint, IDataPointGroup, IState } from "../../types";
import { addNewDefaultValue, addNewDefaultValueToGroup, isNonEmptyValue } from "../util";

export interface ISelectQuestionAction {
  key: string;
  type: "SELECT QUESTION";
}

export const selectNewQuestion = (state: IState, action: ISelectQuestionAction): IState => {
  if (action.key === state.activeDataPoint.groupKey || !(action.key in state.dataPoints)) {
    return state;
  }
  let activeDataPoint = state.activeDataPoint;
  if (state.dataPoints[action.key].isGroup) {
    const group = state.dataPoints[action.key] as IDataPointGroup;
    if (Object.keys(group.dataPoints).length < 1) {
      return state;
    }
    activeDataPoint = {
      groupIndex: 0,
      groupKey: action.key,
      index: 0,
      key: Object.keys(group.dataPoints)[0],
    };
  } else {
    activeDataPoint = {
      groupIndex: 0,
      groupKey: action.key,
      index: 0,
      key: action.key,
    };
  }
  let dataPoints = state.dataPoints;
  // check if the new data point we are moving to has an empty set of values
  if (action.key in dataPoints && dataPoints[action.key].values.length < 1) {
    if (dataPoints[action.key].isGroup) {
      const group = dataPoints[action.key] as IDataPointGroup;
      dataPoints = {
        ...dataPoints,
        [action.key]: addNewDefaultValueToGroup(group),
      };
    } else {
      const nonGroup = dataPoints[action.key] as IDataPoint;
      dataPoints = {
        ...dataPoints,
        [action.key]: addNewDefaultValue(nonGroup),
      };
    }
  }
  // check if we need to filter the old data point
  if (state.activeDataPoint.groupKey in dataPoints) {
    if (dataPoints[state.activeDataPoint.groupKey].isGroup) {
      const oldGroup = dataPoints[state.activeDataPoint.groupKey] as IDataPointGroup;
      const newValues = [];
      const collapsedValues = [];
      for (let i = 0; i < oldGroup.values.length; i++) {
        const oldValue = oldGroup.values[i];
        let newValue = oldValue;
        for (const dataPointKey of Object.keys(oldValue)) {
          const filtered = oldValue[dataPointKey].filter(isNonEmptyValue);
          if (
            filtered.length < oldValue[dataPointKey].length &&
            oldValue[dataPointKey].length > 1
          ) {
            if (filtered.length > 0) {
              newValue = {
                ...newValue,
                [dataPointKey]: filtered,
              };
            } else {
              newValue = {
                ...newValue,
                [dataPointKey]: [oldValue[dataPointKey][0]],
              };
            }
          }
        }
        const nonEmptyValue = Object.keys(newValue).some(k => newValue[k].some(isNonEmptyValue));
        if (nonEmptyValue) {
          if (oldGroup.collapsedValues.includes(i)) {
            collapsedValues.push(newValues.length);
          }
          newValues.push(newValue);
        }
      }
      dataPoints = {
        ...dataPoints,
        [state.activeDataPoint.groupKey]: {
          ...oldGroup,
          collapsedValues,
          values: newValues,
        },
      };
    } else {
      const oldNonGroup = dataPoints[state.activeDataPoint.groupKey] as IDataPoint;
      const filteredValues = oldNonGroup.values.filter(isNonEmptyValue);
      if (filteredValues.length < oldNonGroup.values.length) {
        dataPoints = {
          ...dataPoints,
          [state.activeDataPoint.groupKey]: {
            ...oldNonGroup,
            values: filteredValues,
          },
        };
      }
    }
  }
  return {
    ...state,
    activeDataPoint,
    dataPoints,
  };
};
