import React from "react";
import { Input } from "reactstrap";

import { IInputComponentProps } from "./common";

export const PointerInput = ({
  dataPointKey,
  index,
  onFocus,
  onUpdateAnswer,
  taxonomyEntry,
  value,
}: IInputComponentProps<string>) => {
  const invalid = taxonomyEntry.required && value.idRef === null;
  const valid = value.idRef !== null;
  const inputId = `input-${dataPointKey}-${index}`;
  return (
    <div className="d-flex align-items-center">
      {value.value && <i className="fa fa-angle-double-left mr-2 text-muted" />}
      <Input
        autoComplete="off"
        className="text-truncate"
        id={inputId}
        invalid={invalid}
        onChange={e => {
          onUpdateAnswer(e.target.value, e.target.value);
        }}
        onFocus={() => {
          onFocus(dataPointKey, index);
        }}
        placeholder="(Select a reference)"
        plaintext
        readOnly
        valid={valid}
        value={value.value}
      />
      {value.value && <i className="fa fa-angle-double-right ml-2 text-muted" />}
    </div>
  );
};
