import React from "react";

import { FormikProps } from "formik";

import { Subtext } from "../../../util/Form/Subtext";

export interface IFormValues {
  username: string;
  password: string;
}

export type Props = Readonly<FormikProps<IFormValues>>;

export function LoginForm(props: Props) {
  const {
    handleSubmit,
    errors,
    isSubmitting,
    dirty,
    handleReset,
    values,
    handleChange,
    touched,
    handleBlur,
  } = props;

  return (
    <form onSubmit={handleSubmit}>
      <div className="form-group">
        <label htmlFor="name">Email address</label>
        <input
          className="form-control"
          id="username"
          onBlur={handleBlur}
          onChange={handleChange}
          placeholder="email"
          tabIndex={10}
          type="email"
          value={values.username}
        />
        <Subtext
          errors={errors}
          helperText="We'll never share your email with anyone else."
          fieldKey="username"
          touched={touched}
        />
      </div>
      <div className="form-group">
        <label htmlFor="name">Password</label>
        <input
          className="form-control"
          id="password"
          onBlur={handleBlur}
          onChange={handleChange}
          placeholder="password"
          tabIndex={11}
          type="password"
          value={values.password}
        />
        <Subtext errors={errors} fieldKey="password" touched={touched} />
      </div>
      <div className="btn-toolbar">
        <button className="btn btn-primary" disabled={isSubmitting} tabIndex={20} type="submit">
          Login
        </button>
        <button
          type="button"
          className="btn btn-outline-secondary ml-4"
          disabled={!dirty || isSubmitting}
          tabIndex={21}
          onClick={handleReset}
        >
          Reset
        </button>
      </div>
    </form>
  );
}
