import moment = require("moment");

import { InnodomDateFormats } from "../../util/dateFormats";
import {
  IDateTaxonomyEntry,
  IDateTimeTaxonomyEntry,
  IEnumTaxonomyEntry,
  INumberTaxonomyEntry,
  IStringTaxonomyEntry,
  ITaxonomy,
  TaxonomyEntry,
  TaxonomyEntryType,
} from "./types";

const normalizeDatetime = <T extends IDateTaxonomyEntry | IDateTimeTaxonomyEntry>(entry: T): T => {
  if ("min" in entry) {
    if (typeof entry.min === "string") {
      entry.min = moment(entry.min, InnodomDateFormats[entry.type]).toDate();
    } else if (!(entry.min instanceof Date)) {
      throw new Error(`Invalid minimum value for ${entry.type}.`);
    }
  }
  if ("max" in entry) {
    if (typeof entry.max === "string") {
      entry.max = moment(entry.max, InnodomDateFormats[entry.type]).toDate();
    } else if (!(entry.max instanceof Date)) {
      throw new Error(`Invalid maximum value for ${entry.type}.`);
    }
  }
  if (entry.min instanceof Date && entry.max instanceof Date) {
    if (entry.min > entry.max) {
      throw new Error("Minimum date cannot exceed maximum date.");
    }
  }
  return entry;
};

const normalizeEnum = (entry: IEnumTaxonomyEntry): IEnumTaxonomyEntry => {
  if (!Array.isArray(entry.options)) {
    throw new Error("Invalid options for enum, must be an array of strings.");
  }
  entry.options.forEach((option: any) => {
    if (typeof option !== "string") {
      throw new Error("Invalid options for enum, must be an array of strings.");
    }
  });
  return entry;
};

const normalizeNumber = (entry: INumberTaxonomyEntry): INumberTaxonomyEntry => {
  if ("min" in entry) {
    if (typeof entry.min === "string") {
      entry.min = parseFloat(entry.min);
    } else if (typeof entry.min !== "number") {
      throw new Error("Invalid minimum value for number.");
    }
  }
  if ("max" in entry) {
    if (typeof entry.max === "string") {
      entry.max = parseFloat(entry.max);
    } else if (typeof entry.max !== "number") {
      throw new Error("Invalid maximum value for number.");
    }
  }
  if (typeof entry.min === "number" && typeof entry.max === "number") {
    if (entry.min > entry.max) {
      throw new Error("Number minimum cannot exceed number maximum.");
    }
  }
  return entry;
};

const normalizeString = (entry: IStringTaxonomyEntry): IStringTaxonomyEntry => {
  if ("minLength" in entry) {
    if (typeof entry.minLength === "string") {
      entry.minLength = parseFloat(entry.minLength);
    } else if (typeof entry.minLength !== "number") {
      throw new Error("Invalid minimum length value for string.");
    }
  }
  if ("maxLength" in entry) {
    if (typeof entry.maxLength === "string") {
      entry.maxLength = parseFloat(entry.maxLength);
    } else if (typeof entry.maxLength !== "number") {
      throw new Error("Invalid maximum value for number.");
    }
  }
  if (typeof entry.minLength === "number" && typeof entry.maxLength === "number") {
    if (entry.minLength > entry.maxLength) {
      throw new Error("String minimum length cannot exceed string maximum length.");
    }
  }
  return entry;
};

const isValidTaxonomyEntryType = (input: any): input is TaxonomyEntryType => {
  if (typeof input === "string") {
    return Object.values(TaxonomyEntryType).includes(input as any);
  }
  return false;
};

const isTaxonomyEntry = (input: any): input is TaxonomyEntry => {
  if (typeof input === "object" && !Array.isArray(input) && input !== null) {
    if (!isValidTaxonomyEntryType(input.type)) {
      console.warn('Invalid or missing field "type"');
      return false;
    }
    if (input.type === TaxonomyEntryType.Group) {
      return typeof input.description === "string";
    }
    if (
      typeof input.question !== "string" &&
      input.question !== null &&
      input.question !== undefined
    ) {
      console.warn("Invalid type for question.");
      return false;
    }
    try {
      ["required", "requiresSupport"].forEach(field => {
        if (typeof input[field] === "boolean") {
          return;
        }
        if (typeof input[field] === "string") {
          const value = input[field].trim().toLowerCase();
          if (value === "false") {
            input[field] = false;
            return;
          }
          if (value === "true") {
            input[field] = true;
            return;
          }
        }
        throw new Error(`Invalid value for ${field}.`);
      });
    } catch (err) {
      console.warn(err);
      return false;
    }
    return true;
  }
  return false;
};

const validKeyRegex = new RegExp(/^(\w)+$/);
const reduceInputTaxonomy = (input: { [key: string]: any }) => (
  tax: ITaxonomy,
  next: string,
): ITaxonomy => {
  const key = next.trim().toLowerCase();
  if (!validKeyRegex.test(key)) {
    throw new Error(
      `Invalid taxonomy key: "${key}". Taxonomy keys can only contains letters, numbers, and underscores.`,
    );
  } else if (!isNaN(parseFloat(key))) {
    throw new Error(`Taxonomy key: "${key}" cannot start with a number.`);
  }
  if (!isTaxonomyEntry(input[next])) {
    return tax;
  }
  const entry = input[next];
  try {
    if (entry.type === TaxonomyEntryType.Group) {
      tax[key] = {
        ...entry,
        multiple: typeof entry.multiple === "boolean" ? entry.multiple : true,
        required: typeof entry.required === "boolean" ? entry.required : true,
      };
      return tax;
    }
    switch (entry.type) {
      case TaxonomyEntryType.Date:
      case TaxonomyEntryType.DateTime:
        tax[key] = normalizeDatetime(entry);
        break;
      case TaxonomyEntryType.Enum:
        tax[key] = normalizeEnum(entry);
        break;
      case TaxonomyEntryType.Number:
        tax[key] = normalizeNumber(entry);
        break;
      case TaxonomyEntryType.Selection:
      case TaxonomyEntryType.String:
        tax[key] = normalizeString(entry);
        break;
      default:
        tax[key] = entry;
        break;
    }
    tax[key] = {
      ...entry,
      ...tax[key],
      multiple: typeof entry.multiple === "boolean" ? entry.multiple : false,
      required: typeof entry.required === "boolean" ? entry.required : true,
      requiresSupport: typeof entry.requiresSupport === "boolean" ? entry.requiresSupport : true,
    };
  } catch (err) {
    delete tax[key];
    console.warn(err.message);
  }
  return tax;
};

export const validateTaxonomy = (input: any): ITaxonomy => {
  if (typeof input !== "object") {
    throw new Error("Invalid taxonomy");
  }
  if (input === null || Array.isArray(input)) {
    throw new Error("Invalid taxonomy");
  }
  return Object.keys(input).reduce(reduceInputTaxonomy(input), {});
};
