import React from "react";

import { IFontInformation, useInnodomContents } from "./useInnodomContent";

interface IProps {
  block: Element;
  fontInformation?: IFontInformation;
}

export const InnodomBlock = ({ block, fontInformation }: IProps) => {
  const contents = useInnodomContents(block, fontInformation);
  return <div>{contents}</div>;
};
InnodomBlock.displayName = "InnodomBlock";
