import cx from "classnames";
import React from "react";

interface IProps {
  disabled: boolean;
  onClick: () => void;
}

export const DuplicateButton = ({ disabled, onClick }: IProps) => (
  <a
    className={cx({ "text-primary": !disabled, "text-muted": disabled })}
    href="#"
    onClick={e => {
      e.preventDefault();
      if (!disabled) {
        onClick();
      }
    }}
    title="Duplicate"
  >
    <i className="fa fa-copy" />
  </a>
);
DuplicateButton.displayName = "DuplicateButton";
