import React, { useContext, useEffect } from "react";

import useLocalStorage from "@rooks/use-localstorage";
import { Link, Redirect, RouteComponentProps } from "react-router-dom";

import { ClearCurrentUser } from "../../../../actions/user";
import { UserContext } from "../../../../context-containers";
import { AuthLevel } from "../../../../types";

export interface IOwnProps extends RouteComponentProps<{}> {}

export type Props = Readonly<IOwnProps>;

export function Logout(props: Props) {
  // hooks
  const {
    actions: { SetTitle },
    state: { authLevel },
    dispatch,
  } = useContext(UserContext)!;
  const [, , removeAuthKey] = useLocalStorage("auth-key", "");

  useEffect(() => SetTitle("Logout"), []);

  if (authLevel !== AuthLevel.authenticated) {
    return <Redirect to="/" />;
  }

  return (
    <div className="flex-fill p-3">
      <div className="alert alert-primary">
        <h4>Welcome!</h4>
        <p>Are you sure you want to log out?</p>
        <hr />
        <div className="btn-toolbar">
          <button
            type="button"
            className="btn btn-primary"
            onClick={() => {
              removeAuthKey();
              dispatch(ClearCurrentUser());
            }}
          >
            Log Out
          </button>
          <Link to="/" className="btn btn-outline-secondary ml-4">
            Back
          </Link>
        </div>
      </div>
    </div>
  );
}
