import * as HTTPStatus from "http-status";

import {
  IApiErrorResponse,
  IErrors,
  IGenerateUserRequest,
  ILoginUserRequest,
  IRegisterUserRequest,
  IUser,
  ReauthenticateUserResponse,
  RegisterUserResponse,
} from "@ilabs/core";

import { apiEndpointV1, request, Routes } from "./index";

export interface IRegisterUserResult {
  user: IUser | null;
  errors: IErrors | null;
}

export const registerUser = async (
  req: IGenerateUserRequest | IRegisterUserRequest,
): Promise<IRegisterUserResult> => {
  const res = await request<Routes.Users, "POST">({
    body: req,
    method: "POST",
    url: `${apiEndpointV1}/users`,
  });

  switch (res.status) {
    case HTTPStatus.BAD_REQUEST:
      const apiError: IApiErrorResponse = (res.body!.response as IApiErrorResponse)!;
      const field = apiError.field === null ? "error" : apiError.field;

      return {
        errors: { [field]: apiError.message },
        user: null,
      };
    case HTTPStatus.CREATED:
      return {
        errors: null,
        user: res.body!.response as RegisterUserResponse,
      };
    default:
      return {
        errors: { error: "Unknown response code" },
        user: null,
      };
  }
};

export type LoginUserResult = IRegisterUserResult;

export const loginUser = async (req: ILoginUserRequest): Promise<LoginUserResult> => {
  const res = await request<Routes.UserLogin, "POST">({
    body: req,
    method: "POST",
    url: `${apiEndpointV1}/users/login`,
  });

  switch (res.status) {
    case HTTPStatus.BAD_REQUEST:
      const apiError: IApiErrorResponse = (res.body!.response as IApiErrorResponse)!;
      const field = apiError.field === null ? "error" : apiError.field;

      return {
        errors: { [field]: apiError.message },
        user: null,
      };
    case HTTPStatus.OK:
      return {
        errors: null,
        user: res.body!.response as RegisterUserResponse,
      };
    default:
      return {
        errors: { error: "Unknown response code" },
        user: null,
      };
  }
};

export type ReauthenticateUserResult = LoginUserResult;

export const reauthenticateUser = async (authKey: string): Promise<ReauthenticateUserResult> => {
  const res = await request<Routes.Reauthenticate, "GET">({
    headers: new Headers({
      authorization: `Basic ${authKey}`,
      "content-type": "application/json",
    }),
    method: "GET",
    url: `${apiEndpointV1}/users/me`,
  });

  switch (res.status) {
    case HTTPStatus.BAD_REQUEST:
      const apiError: IApiErrorResponse = (res.body!.response as IApiErrorResponse)!;
      const field = apiError.field === null ? "error" : apiError.field;

      return {
        errors: { [field]: apiError.message },
        user: null,
      };
    case HTTPStatus.OK:
      return {
        errors: null,
        user: res.body!.response as ReauthenticateUserResponse,
      };
    default:
      return {
        errors: { error: "Unknown response code" },
        user: null,
      };
  }
};
