import React from "react";

import { IErrors } from "@ilabs/core";

const renderError = (key: string, value: string, i: number) => {
  return (
    <li key={i}>
      {key}: {value}
    </li>
  );
};

interface IProps {
  errors: IErrors;
}

export function ErrorList({ errors }: IProps) {
  if (Object.keys(errors).length === 0) {
    return null;
  }

  return (
    <ul style={{ marginBottom: "0" }}>
      {Object.keys(errors).map((k, i) => renderError(k, errors[k], i))}
    </ul>
  );
}
