import React from "react";
import { FormFeedback, Input } from "reactstrap";

import { INumberTaxonomyEntry } from "../../types";
import { IInputComponentProps } from "./common";

export const NumberInput = ({
  dataPointKey,
  index,
  invalid,
  onFocus,
  onUpdateAnswer,
  showAlerts,
  taxonomyEntry,
  value,
}: IInputComponentProps<number>) => {
  const entry = taxonomyEntry as INumberTaxonomyEntry;
  const inputId = `input-${dataPointKey}-${index}`;
  const valid = !invalid && taxonomyEntry.required;
  return (
    <>
      <Input
        id={inputId}
        invalid={invalid}
        max={typeof entry.max === "number" ? entry.max : undefined}
        min={typeof entry.min === "number" ? entry.min : undefined}
        onChange={e => {
          onUpdateAnswer(parseFloat(e.target.value), e.target.value);
        }}
        onFocus={() => {
          onFocus(dataPointKey, index);
        }}
        step="any"
        type="number"
        valid={valid}
        value={value.value !== null ? value.value.toString(10) : ""}
      />
      {showAlerts && invalid && (
        <FormFeedback>Value is missing or is not a valid number.</FormFeedback>
      )}
    </>
  );
};
NumberInput.displayName = "NumberInput";
