import React from "react";
import { FormFeedback, Input } from "reactstrap";

import { ISelectionTaxonomyEntry, IStringTaxonomyEntry, TaxonomyEntryType } from "../../types";
import { IInputComponentProps } from "./common";

export const StringInput = ({
  dataPointKey,
  index,
  invalid,
  onFocus,
  onUpdateAnswer,
  showAlerts,
  taxonomyEntry,
  value,
}: IInputComponentProps<string>) => {
  const entry = taxonomyEntry as (IStringTaxonomyEntry | ISelectionTaxonomyEntry);
  const valid = !invalid && taxonomyEntry.required;
  const isSelection = entry.type === TaxonomyEntryType.Selection;
  const inputId = `input-${dataPointKey}-${index}`;
  const feedbackMessage = isSelection
    ? "Select some text from the document to populate the answer."
    : "Value cannot be left blank.";
  return (
    <>
      <Input
        autoComplete="off"
        id={inputId}
        invalid={invalid}
        maxLength={typeof entry.maxLength === "number" ? entry.maxLength : undefined}
        minLength={typeof entry.minLength === "number" ? entry.minLength : undefined}
        onChange={e => {
          onUpdateAnswer(e.target.value, e.target.value);
        }}
        onFocus={() => {
          onFocus(dataPointKey, index);
        }}
        readOnly={isSelection}
        type="text"
        valid={valid}
        value={value.value}
      />
      {showAlerts && invalid && <FormFeedback>{feedbackMessage}</FormFeedback>}
    </>
  );
};
StringInput.displayName = "StringInput";
