import React from "react";
import { ZoomLevel } from "../../StateManager/state";

interface IProps {
  readonly activeZones: Array<number>;
  readonly autoShrink: boolean;
  readonly currentLabel: string;
  readonly disabled: boolean;
  readonly onChangeZoom: (zoom: ZoomLevel) => void;
  readonly onDelete: () => void;
  readonly onSave: () => void;
  readonly onToggleAutoShrink: () => void;
  readonly onUpdateCurrentLabel: (label: string) => void;
  readonly saving: boolean;
  readonly zoom: ZoomLevel;
}

export const Toolbar = ({
  activeZones,
  autoShrink,
  currentLabel,
  disabled,
  onChangeZoom,
  onDelete,
  onSave,
  onToggleAutoShrink,
  onUpdateCurrentLabel,
  saving,
  zoom,
}: IProps) => {
  return (
    <div className="toolbar">
      <div className="toolbar-group">
        <div className="d-inline-block mr-3">
          <form className="form-inline">
            <label className="form-label mr-1" htmlFor="label-selection">
              Label:
            </label>
            <select
              className="form-control mr-3"
              disabled={disabled}
              id="label-selection"
              onChange={e => {
                onUpdateCurrentLabel(e.currentTarget.value);
              }}
              value={currentLabel}
            >
              <option value="text">(1) Text</option>
              <option value="image">(2) Image</option>
              <option value="table">(3) Table</option>
              <option value="math">(4) Math</option>
            </select>
            <div className="form-check mr-2">
              <input
                checked={autoShrink}
                className="form-check-input"
                id="toggle-autoshrink"
                onChange={onToggleAutoShrink}
                type="checkbox"
              />
              <label className="form-check-label" htmlFor="toggle-autoshrink">
                Auto shrink?
              </label>
            </div>
            <label className="form-label mr-1" htmlFor="zoom-selection">
              Zoom:
            </label>
            <select
              className="form-control mr-3"
              disabled={disabled}
              id="zoom-selection"
              onChange={e => {
                const newZoomValue = Number.parseFloat(e.target.value);
                for (const zoomLevel of Object.keys(ZoomLevel)) {
                  if (ZoomLevel[zoomLevel] === newZoomValue) {
                    onChangeZoom(ZoomLevel[zoomLevel]);
                    return;
                  }
                }
              }}
              value={zoom.toString()}
            >
              <option value="1">100%</option>
              <option value="1.5">150%</option>
              <option value="2">200%</option>
              <option value="2.5">250%</option>
              <option value="3">300%</option>
            </select>
          </form>
        </div>
        <button
          className="btn btn-danger"
          disabled={disabled || activeZones.length < 1}
          onClick={onDelete}
        >
          Delete (D)
        </button>
      </div>
      <div className="toolbar-group">
        <button className="btn btn-primary" disabled={saving} onClick={onSave}>
          Save
          {saving && (
            <>
              <span
                aria-hidden="true"
                className="spinner-border spinner-border-sm ml-2"
                role="status"
              />
              <span className="sr-only">Saving&hellip;</span>
            </>
          )}
        </button>
      </div>
    </div>
  );
};
