import {
  CheckoutJobResponse,
  CreateDocumentResponse,
  CreateJobResponse,
  GetJobContentResponse,
  IAddJobContentResponse,
  IApiErrorResponse,
  IApiListParams,
  IApiResponse,
  ICheckinJobRequest,
  ICheckinJobResponse,
  ICheckoutJobRequest,
  ICreateJobRequest,
  IGenerateUserRequest,
  IGetBlockedJobsRequest,
  IGetDocuments,
  IGetJobs,
  IGetJobTypes,
  IJob,
  ILoginUserRequest,
  IRegisterUserRequest,
  LoginUserResponse,
  ReauthenticateUserResponse,
  RegisterUserResponse,
} from "@ilabs/core";

import {
  apiEndpoint,
  gather,
  GatherMethod,
  gatherWithQuery,
  IGatherOptions,
  IGatherQueryOptions,
  IGatherResult,
} from "../index";

export const apiEndpointV1 = `${apiEndpoint}/v1`;

export enum Routes {
  BlockedJobs = "blockedJobs",
  Documents = "documents",
  Job = "job",
  JobCheckin = "JobCheckin",
  JobCheckout = "jobCheckout",
  JobContent = "jobContent",
  Jobs = "jobs",
  JobTypes = "jobTypes",
  Reauthenticate = "reauthenticate",
  Users = "users",
  UserLogin = "userLogin",
}

interface IApiMap {
  [Routes.Documents]: {
    request: {
      GET: IApiListParams;
      POST: ArrayBuffer;
    };
    response: {
      GET: IGetDocuments;
      POST: CreateDocumentResponse | IApiErrorResponse;
    };
  };
  [Routes.Jobs]: {
    request: {
      GET: IApiListParams;
      POST: ICreateJobRequest;
    };
    response: {
      GET: IGetJobs;
      POST: CreateJobResponse | IApiErrorResponse;
    };
  };
  [Routes.Job]: {
    request: {
      GET: null;
      POST: null;
    };
    response: {
      GET: IJob;
      POST: null;
    };
  };
  [Routes.JobTypes]: {
    request: {
      GET: null;
      POST: null;
    };
    response: {
      GET: IGetJobTypes;
      POST: null;
    };
  };
  [Routes.Users]: {
    request: {
      GET: null;
      POST: IGenerateUserRequest | IRegisterUserRequest;
    };
    response: {
      GET: null;
      POST: RegisterUserResponse | IApiErrorResponse;
    };
  };
  [Routes.UserLogin]: {
    request: {
      GET: null;
      POST: ILoginUserRequest;
    };
    response: {
      GET: null;
      POST: LoginUserResponse | IApiErrorResponse;
    };
  };
  [Routes.Reauthenticate]: {
    request: {
      GET: null;
      POST: null;
    };
    response: {
      GET: ReauthenticateUserResponse | IApiErrorResponse;
      POST: null;
    };
  };
  [Routes.BlockedJobs]: {
    request: {
      GET: IGetBlockedJobsRequest | undefined;
      POST: null;
    };
    response: {
      GET: IGetJobs;
      POST: null;
    };
  };
  [Routes.JobCheckout]: {
    request: {
      GET: null;
      POST: ICheckoutJobRequest;
    };
    response: {
      GET: null;
      POST: CheckoutJobResponse | IApiErrorResponse;
    };
  };
  [Routes.JobCheckin]: {
    request: {
      GET: null;
      POST: ICheckinJobRequest;
    };
    response: {
      GET: null;
      POST: ICheckinJobResponse | IApiErrorResponse;
    };
  };
  [Routes.JobContent]: {
    request: {
      GET: null;
      POST: string;
    };
    response: {
      GET: GetJobContentResponse;
      POST: IAddJobContentResponse | IApiErrorResponse;
    };
  };
}

type ApiEndpoint = keyof IApiMap;

type ApiRequest<
  U extends ApiEndpoint,
  M extends keyof IApiMap[U]["request"]
> = IApiMap[U]["request"][M];
export type ApiResponse<
  U extends ApiEndpoint,
  M extends keyof IApiMap[U]["response"]
> = IApiMap[U]["response"][M];

export const request = async <U extends ApiEndpoint, M extends GatherMethod>(
  opts: IGatherOptions<ApiRequest<U, M>>,
): Promise<IGatherResult<IApiResponse<ApiResponse<U, M>>>> => gather(opts);

export const requestWithQuery = async <U extends ApiEndpoint, M extends GatherMethod>(
  opts: IGatherQueryOptions<ApiRequest<U, M>>,
): Promise<IGatherResult<IApiResponse<ApiResponse<U, M>>>> => gatherWithQuery(opts);

export * from "./documents";
export * from "./jobs";
export * from "./jobTypes";
export * from "./users";
