import React from "react";
import { Spinner } from "reactstrap";

import { IUser } from "@ilabs/core";
import { IJobState } from "../../../../../../types";
import { Job } from "./Job";

interface IProps {
  readonly checkedOutJobId: string | null;
  readonly currentTime: number;
  readonly disableActions: boolean;
  readonly fetching: boolean;
  readonly jobs: IJobState["blockedJobs"]["data"];
  readonly onAbort: (id: string) => () => void;
  readonly user: IUser;
}

export const Contents = ({
  checkedOutJobId,
  currentTime,
  disableActions,
  fetching,
  jobs,
  onAbort,
  user,
}: IProps) => {
  if (fetching) {
    return (
      <div className="m-2 text-center">
        <Spinner />
      </div>
    );
  }
  if (jobs.length < 1) {
    return <div className="alert alert-primary m-2">No jobs found.</div>;
  }
  return (
    <div className="list-group">
      {jobs.map((j, i) => (
        <Job
          current={checkedOutJobId === j.id}
          currentTime={currentTime}
          disableActions={disableActions}
          job={j}
          key={`job-${j.id}`}
          onAbort={onAbort(j.id)}
          user={user}
        />
      ))}
    </div>
  );
};
Contents.displayName = "Contents";
