import * as HTTPStatus from "http-status";

import {
  CreateDocumentResponse,
  IApiErrorResponse,
  IApiListParams,
  IAuthenticatedRequest,
  IDocument,
  IErrors,
} from "@ilabs/core";

import { apiEndpointV1, request, requestWithQuery, Routes } from "./index";

export interface ICreateDocumentRequest extends IAuthenticatedRequest {
  name: string;
  body: ArrayBuffer;
}

export interface ICreateDocumentResult {
  name: string;
  document: IDocument | null;
  errors: IErrors | null;
}

export const createDocument = async (
  req: ICreateDocumentRequest,
): Promise<ICreateDocumentResult> => {
  const res = await request<Routes.Documents, "POST">({
    body: req.body,
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/octet-stream",
      "x-name": req.name,
    }),
    method: "POST",
    url: `${apiEndpointV1}/documents`,
  });

  switch (res.status) {
    case HTTPStatus.UNAUTHORIZED:
    case HTTPStatus.BAD_REQUEST:
      return {
        document: null,
        errors: { error: (res.body!.response as IApiErrorResponse)!.message },
        name: req.name,
      };
    case HTTPStatus.CREATED:
      const document = res.body!.response as CreateDocumentResponse;

      return {
        document,
        errors: null,
        name: req.name,
      };
    default:
      return {
        document: null,
        errors: { error: "Unknown response code" },
        name: req.name,
      };
  }
};

export interface IGetDocumentsRequest {
  query?: IApiListParams;
  authKey: string;
}

export const getDocuments = async (req: IGetDocumentsRequest): Promise<IDocument[]> => {
  const res = await requestWithQuery<Routes.Documents, "GET">({
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/json",
    }),
    method: "GET",
    query: req.query,
    url: `${apiEndpointV1}/documents`,
  });

  return res.body!.response.list;
};
