import { IJobContent, JobCheckinStatus, UUID } from "@ilabs/core";
import React, { ReactElement, useContext, useEffect } from "react";
import { Redirect, RouteComponentProps } from "react-router-dom";

import useInterval from "use-interval";
import { ClearCheckoutNextJob } from "../../../../../actions/job";
import { addJobContent, checkoutJob, deleteJobContent, getJobContent } from "../../../../../api";
import { JobContext, UserContext } from "../../../../../context-containers";
import { IRenderJobOptions, JobCheckoutState } from "../../../../../helpers";
import { AuthLevel, FetchLevel } from "../../../../../types";
import { ErrorList, Loading } from "../../../../util";

// number of milliseconds between job checkout refreshes
const CHECKOUT_REFRESH_INTERVAL = 60 * 1000; // refresh the checkout every minute

export interface IRouteParams {
  jobId: UUID;
}

export interface IOwnProps<C> extends RouteComponentProps<IRouteParams> {
  renderJob: (opts: IRenderJobOptions<C>) => ReactElement;
}

export type Props<C> = Readonly<IOwnProps<C>>;

export function ViewJob<C>(props: Props<C>) {
  // props
  const {
    renderJob,
    match: {
      params: { jobId },
    },
    history,
  } = props;

  // hooks
  const {
    actions: { FetchCheckinJob, FetchCheckoutJob, FetchGetJobContent, FetchCheckoutNextJob },
    dispatch,
    state: { checkedInJob, checkedOutJob, jobContent, lastFailedCheckoutJobId, nextCheckedOutJob },
  } = useContext(JobContext)!;
  const {
    actions: { SetTitle },
    state: { user, userAuth, authLevel, blockingTaskKind, roleWhitelist, automaticallyLoadNextJob },
  } = useContext(UserContext)!;

  const jobCheckoutState = JobCheckoutState.NO_JOB_SELECTED;

  useEffect(() => {
    (async () => {
      if (userAuth === null) {
        return;
      }

      switch (checkedOutJob.fetchLevel) {
        case FetchLevel.initial:
          await FetchCheckoutJob(userAuth, jobId);

          return;
        case FetchLevel.success:
        case FetchLevel.failure:
          if (lastFailedCheckoutJobId !== null && jobId === lastFailedCheckoutJobId) {
            return;
          }

          if (checkedOutJob.data === null) {
            await FetchCheckoutJob(userAuth, jobId);

            return;
          }

          if (checkedOutJob.data.id === jobId) {
            return;
          }

          await FetchCheckoutJob(userAuth, jobId);

          return;
        default:
          return;
      }
    })();
  }, [userAuth, jobId, checkedOutJob.fetchLevel, lastFailedCheckoutJobId]);

  useEffect(() => {
    (async () => {
      if (userAuth === null) {
        return;
      }

      if (checkedOutJob.data === null) {
        return;
      }

      if (checkedOutJob.data.id !== jobId) {
        return;
      }

      if (checkedOutJob.fetchLevel !== FetchLevel.success) {
        return;
      }

      switch (jobContent.fetchLevel) {
        case FetchLevel.initial:
        case FetchLevel.prompted:
          const input = checkedOutJob.data.contents.find((c: IJobContent) =>
            roleWhitelist.includes(c.role),
          );
          if (typeof input === "undefined") {
            return;
          }

          if (input.uri.length === 0) {
            return;
          }

          await FetchGetJobContent(userAuth!, input.uri);

          return;
        default:
          return;
      }
    })();
  }, [userAuth, jobId, checkedOutJob.fetchLevel, jobContent.fetchLevel]);

  useEffect(() => {
    (async () => {
      if (userAuth === null) {
        return;
      }

      if (checkedOutJob.data === null) {
        return;
      }

      if (checkedOutJob.data.id !== jobId) {
        return;
      }

      if (checkedOutJob.fetchLevel !== FetchLevel.success) {
        return;
      }

      if (checkedInJob.fetchLevel !== FetchLevel.success) {
        return;
      }

      switch (nextCheckedOutJob.fetchLevel) {
        case FetchLevel.prompted:
          if (automaticallyLoadNextJob) {
            await FetchCheckoutNextJob(userAuth, blockingTaskKind!);
          }

          return;
        case FetchLevel.fetching:
          SetTitle("Checking for new job");

          return;
        default:
          return;
      }
    })();
  }, [userAuth, jobId, checkedOutJob.fetchLevel, checkedInJob.fetchLevel]);

  useEffect(() => {
    (async () => {
      if (nextCheckedOutJob.fetchLevel !== FetchLevel.success) {
        return;
      }

      dispatch(ClearCheckoutNextJob());

      if (nextCheckedOutJob.data === null) {
        history.push("/");

        return;
      }

      SetTitle(`Redirecting to new job ${nextCheckedOutJob.data.id}`);
      history.push(`/job/${nextCheckedOutJob.data.id}`);

      return;
    })();
  }, [nextCheckedOutJob.fetchLevel]);

  useInterval(
    () => {
      if (userAuth !== null && jobId !== null && checkedOutJob.fetchLevel === FetchLevel.success) {
        checkoutJob({ authKey: userAuth, jobId });
      }
    },
    CHECKOUT_REFRESH_INTERVAL,
    false,
  );

  if (authLevel !== AuthLevel.authenticated) {
    return <Redirect to="/login" />;
  }

  switch (checkedOutJob.fetchLevel) {
    case FetchLevel.success:
      break;
    case FetchLevel.initial:
    case FetchLevel.fetching:
    case FetchLevel.prompted:
      SetTitle("Loading job");

      return <Loading text="Loading job." />;
    case FetchLevel.failure:
      SetTitle("Failed to check-out job");

      return (
        <div className="alert alert-danger">
          <h4>Failed to check-out job!</h4>
          <ErrorList errors={checkedOutJob.errors} />
        </div>
      );
    default:
      return <p>Could not load job!</p>;
  }

  switch (jobContent.fetchLevel) {
    case FetchLevel.success:
      break;
    case FetchLevel.initial:
    case FetchLevel.fetching:
    case FetchLevel.prompted:
      SetTitle("Loading job-contents");

      return <Loading text="Loading job-contents." />;
    case FetchLevel.failure:
      SetTitle("Failed to load job-contents!");

      return (
        <div className="alert alert-danger">
          <h4>Failed to load job-contents!</h4>
          <ErrorList errors={jobContent.errors} />
        </div>
      );
    default:
      SetTitle("Could not load job-contents!");

      return <p>Could not load job-contents!</p>;
  }

  if (checkedOutJob.data === null) {
    return <p>Job {jobId} did not have any corresponding data!</p>;
  }

  switch (checkedInJob.fetchLevel) {
    case FetchLevel.initial:
      SetTitle(`Editing job ${checkedOutJob.data.id}`);

      break;
    case FetchLevel.fetching:
      SetTitle(`Checking in job ${checkedOutJob.data.id}`);

      break;
    case FetchLevel.success:
      return <div className="alert alert-success">Job was successfully checked in!</div>;
    default:
      return (
        <div className="alert alert-danger">
          <h4>Failed to check in!</h4>
          <ErrorList errors={checkedInJob.errors} />
        </div>
      );
  }

  return renderJob({
    actions: {
      checkinJobAborted: async (message?: string) => {
        if (userAuth && checkedOutJob) {
          await FetchCheckinJob(userAuth, checkedOutJob.data!.id, {
            message: message || "",
            status: JobCheckinStatus.ABORTED,
          });
        }
      },
      checkinJobFailure: async (message: string) => {
        if (userAuth && checkedOutJob) {
          await FetchCheckinJob(userAuth, checkedOutJob.data!.id, {
            message,
            status: JobCheckinStatus.FAILED,
          });
        }
      },
      checkinJobSuccess: async (message?: string) => {
        if (userAuth && checkedOutJob) {
          await FetchCheckinJob(userAuth, checkedOutJob.data!.id, {
            message: message || "",
            status: JobCheckinStatus.COMPLETED,
          });
        }
      },
      deleteContent: async (id: UUID) => {
        if (userAuth && checkedOutJob) {
          const input = checkedOutJob.data!.contents.find(c => c.id === id);
          if (input && input.uri) {
            await deleteJobContent({ authKey: userAuth, uri: input.uri });
          }
        }
        throw new Error("Unable to delete job content.");
      },
      fetchContent: async (id: UUID): Promise<string> => {
        if (userAuth && checkedOutJob) {
          const input = checkedOutJob.data!.contents.find(c => c.id === id);
          if (input && input.uri) {
            const result = await getJobContent({ authKey: userAuth, uri: input.uri });
            if (result.data) {
              return result.data;
            }
          }
        }
        throw new Error("Unable to retrieve job content.");
      },
      saveContent: async (id: UUID, contents: string) => {
        if (userAuth && checkedOutJob) {
          const input = checkedOutJob.data!.contents.find(c => c.id === id);
          if (input && input.uri) {
            await addJobContent({ authKey: userAuth, body: contents, uri: input.uri });
            return;
          }
        }
        throw new Error("Unable to save job content.");
      },
    },
    state: {
      contents: jobContent.data!,
      job: checkedOutJob.data,
      jobCheckoutState,
      user: user ? user.data! : null,
    },
  });
}
