import React, { memo } from "react";

import { URI, UUID } from "@ilabs/core";
import { mapToApiPage } from "./helpers";
import { PagePreview } from "./PagePreview";
import { Renderer } from "./Renderer";
import { Size } from "./Renderer/types";
import { StateManager } from "./StateManager";
import { IApiPage } from "./types";
import { ZoneLoader } from "./ZoneLoader";

interface IProps {
  readonly fetchJobContent: (id: UUID) => Promise<IApiPage>;
  readonly highConfidenceThreshold: number;
  readonly highResImageURIs: Array<URI>;
  readonly jobId: string;
  readonly lowResImageURIs: Array<URI>;
  readonly pageConfidences: Array<number>;
  readonly pageDimensions: Array<Size>;
  readonly pageZoneIds: Array<UUID>;
  readonly pageZoneURIs: Array<URI>;
  readonly onSave: (pages: Array<IApiPage>, modifiedPages: Array<number>) => Promise<void>;
  readonly review: boolean;
  readonly saving: boolean;
  readonly startingPage: number;
}

export const Zoning = memo(
  ({
    fetchJobContent,
    highConfidenceThreshold,
    highResImageURIs,
    jobId,
    lowResImageURIs,
    pageConfidences,
    pageDimensions,
    pageZoneIds,
    pageZoneURIs,
    onSave,
    review,
    saving,
    startingPage,
  }: IProps) => (
    <StateManager
      initialPages={pageZoneURIs}
      jobId={jobId}
      pageDimensions={pageDimensions}
      review={review}
      startingPage={startingPage}
    >
      {({ dispatch, persistedZones, state }) => (
        <ZoneLoader
          dispatch={dispatch}
          fetchJobContent={fetchJobContent}
          page={state.pages[state.activePage]}
          pageZoneIds={pageZoneIds}
          persistedZones={persistedZones}
        >
          <div className="zoning">
            <div className="contents">
              <Renderer
                dispatch={dispatch}
                onDelete={() => {
                  // simulate the user pressing "D"
                  dispatch({
                    ctrlKey: false,
                    key: "d",
                    keyCode: 68,
                    // tslint:disable-next-line: no-empty
                    preventDefault: () => {},
                    shiftKey: false,
                    type: "KEY PRESSED",
                  });
                }}
                onSave={() => {
                  onSave(
                    state.pages.map(mapToApiPage),
                    state.pages.reduce(
                      (prev, page, index) => {
                        if (page.modified) {
                          prev.push(index);
                        }
                        return prev;
                      },
                      [] as Array<number>,
                    ),
                  );
                }}
                pageImageURIs={highResImageURIs}
                review={review}
                saving={saving}
                state={state}
              />
              <PagePreview
                activePage={state.activePage}
                dispatch={dispatch}
                highConfidenceThreshold={highConfidenceThreshold}
                onSelectPage={index => {
                  dispatch({
                    page: index,
                    type: "SELECT PAGE",
                  });
                }}
                pageConfidences={pageConfidences}
                pageImageURIs={lowResImageURIs}
                pages={state.pages}
              />
            </div>
          </div>
        </ZoneLoader>
      )}
    </StateManager>
  ),
);
Zoning.displayName = "Zoning";
