import React from "react";

import cx from "classnames";
import { FormikErrors, FormikTouched } from "formik";

interface ISubtextProps<V> {
  helperText?: string;
  fieldKey: keyof V;
  errors: FormikErrors<V>;
  touched: FormikTouched<V>;
}

export function Subtext<T>({ helperText, fieldKey, errors, touched }: ISubtextProps<T>) {
  const shouldShowError = touched[fieldKey] && errors[fieldKey];
  const text: string | undefined = shouldShowError ? `Error: ${errors[fieldKey]}` : helperText;

  if (typeof text === "undefined") {
    return null;
  }

  const classes = cx("form-text", {
    "text-danger": shouldShowError,
    "text-muted": !shouldShowError,
  });

  return <small className={classes}>{text}</small>;
}
