import React from "react";

import { IJob, JobStatus } from "@ilabs/core";

interface IStatusLabelMap {
  [status: string]: string;
}

const statusLabelMap: IStatusLabelMap = {
  [JobStatus.WAITING]: "Pending",
  [JobStatus.RUNNING]: "Running",
  [JobStatus.NOT_STARTED]: "Not Started",
  [JobStatus.BLOCKED]: "Blocked",
};

const renderStatusLabel = (status: JobStatus) => {
  if (!(status in statusLabelMap)) {
    return status;
  }

  return statusLabelMap[status];
};

interface IStatusBadgeMap {
  [status: string]: string;
}

const statusBadgeMap: IStatusBadgeMap = {
  [JobStatus.WAITING]: "badge-warning",
  [JobStatus.RUNNING]: "badge-warning",
  [JobStatus.COMPLETED]: "badge-success",
  [JobStatus.FAILED]: "badge-danger",
  [JobStatus.BLOCKED]: "badge-info",
};

const renderStatusBadge = (status: JobStatus) => {
  if (!(status in statusBadgeMap)) {
    return "badge-secondary";
  }

  return statusBadgeMap[status];
};

interface IOwnProps {
  job: IJob;
}

type Props = Readonly<IOwnProps>;

export function JobStatusBadge(props: Props) {
  return (
    <span className={`badge ${renderStatusBadge(props.job.status)}`}>
      {renderStatusLabel(props.job.status)}
    </span>
  );
}
