import {
  ReceiveGenerateUser,
  SetCurrentAuthLevel,
  SetCurrentUser,
  UserActions,
} from "../../actions/user";
import { AuthLevel, FetchLevel, IUserState } from "../../types";
import { IKindHandlers, Runner } from "./index";

const handlers: IKindHandlers<IUserState, UserActions> = {
  authlevel: {
    current: {
      set: (state: IUserState, action: ReturnType<typeof SetCurrentAuthLevel>): IUserState => {
        return { ...state, authLevel: action.payload };
      },
    },
  },
  sidebar: {
    root: {
      toggle: (state: IUserState): IUserState => {
        return {
          ...state,
          hideSidebar: !state.hideSidebar,
        };
      },
    },
  },
  user: {
    current: {
      clear: (state: IUserState) => {
        return {
          ...state,
          authLevel: AuthLevel.unauthenticated,
          user: {
            ...state.user,
            data: null,
            errors: {},
            fetchLevel: FetchLevel.initial,
          },
          userAuth: null,
        };
      },
      set: (state: IUserState, action: ReturnType<typeof SetCurrentUser>): IUserState => {
        const { user } = action.payload;

        const userAuth = ((): string => {
          if (action.payload.authKey) {
            return action.payload.authKey;
          }

          return btoa(`${user.is_demo ? user.api_keys.test : user.api_keys.live}:`);
        })();

        return {
          ...state,
          authLevel: AuthLevel.authenticated,
          user: { ...state.user, data: user },
          userAuth,
        };
      },
    },
    generate: {
      receive: (state: IUserState, action: ReturnType<typeof ReceiveGenerateUser>): IUserState => {
        if (action.payload.errors !== null) {
          return {
            ...state,
            user: {
              ...state.user,
              data: null,
              errors: action.payload.errors,
              fetchLevel: FetchLevel.failure,
            },
          };
        }

        const {
          is_demo,
          api_keys: { live: liveKey, test: testKey },
        } = action.payload.user!;
        const userAuth = btoa(`${is_demo ? testKey : liveKey}:`);

        return {
          ...state,
          user: {
            ...state.user,
            data: action.payload.user,
            errors: {},
            fetchLevel: FetchLevel.success,
          },
          userAuth,
        };
      },
      request: (state: IUserState): IUserState => {
        return {
          ...state,
          user: { ...state.user, errors: {}, fetchLevel: FetchLevel.fetching },
        };
      },
    },
  },
};

export const run: Runner<IUserState, UserActions> = (
  state: IUserState,
  action: UserActions,
): IUserState => {
  const [kind, verb, task] = action.type
    .split("_")
    .reverse()
    .map(v => v.toLowerCase());
  if (!(kind in handlers)) {
    return state;
  }
  const kindHandlers = handlers[kind];

  if (!(verb in kindHandlers)) {
    return state;
  }
  const verbHandlers = kindHandlers[verb];

  if (!(task in verbHandlers)) {
    return state;
  }
  const taskHandler = verbHandlers[task];

  return taskHandler(state, action);
};
