import { UUID } from "@ilabs/core";
import { ReactElement, useEffect } from "react";
import { Dispatch, IPageState } from "./StateManager/state";
import { IApiPage, IZone } from "./types";

interface IProps {
  children: ReactElement;
  dispatch: Dispatch;
  fetchJobContent: (id: string) => Promise<IApiPage>;
  page: IPageState;
  pageZoneIds: Array<UUID>;
  persistedZones: Array<Array<IZone> | null> | null;
}
export const ZoneLoader = ({
  children,
  dispatch,
  fetchJobContent,
  page,
  pageZoneIds,
  persistedZones,
}: IProps) => {
  useEffect(() => {
    const { index, loaded } = page;
    if (loaded) {
      return;
    }
    if (persistedZones !== null) {
      const zones = persistedZones[index];
      if (zones !== null && Array.isArray(zones)) {
        dispatch({
          index,
          type: "POPULATE PERSISTED ZONES",
          zones,
        });
        return;
      }
    }
    fetchJobContent(pageZoneIds[index])
      .then(({ predictions }: IApiPage) => {
        dispatch({
          index,
          type: "POPULATE ZONES",
          zones: predictions,
        });
      })
      .catch(err => {
        console.error(err);
      });
  }, [page.index, page.loaded, persistedZones]);
  return children;
};
