import { UUID } from "../contracts";

export interface IListedUser {
  email: string;
  id: UUID;
  name: string;
}

export enum ApiKeyMode {
  LIVE = "live",
  TEST = "test",
}

export type UserApiKeys = { [key in ApiKeyMode]: string | null };

export interface IUser extends IListedUser {
  api_keys: UserApiKeys;
  email: string;
  is_demo: boolean;
}
