import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";

import { JobManager } from "./JobManager";
import { MappingReview } from "./MappingReview";

export const App = () => {
  return (
    <div className="overflow-hidden vh-100 vw-100">
      <HashRouter>
        <Switch>
          <Route path="/:token/jobs/:jobId">
            <JobManager>
              {({ job, jobError, token }) => {
                if (jobError) {
                  return <div>{jobError.message}</div>;
                } else if (job === null) {
                  return <div>Loading...</div>;
                }
                return <MappingReview job={job} token={token} />;
              }}
            </JobManager>
          </Route>
          <Route path="*">
            <p>Page not found.</p>
          </Route>
        </Switch>
      </HashRouter>
    </div>
  );
};
App.displayName = "App";
