import React, { useEffect, useState } from "react";

import { IJob } from "@ilabs/core";
import { Mapping } from "@ilabs/ui";

type Props = Readonly<{
  job: IJob;
  token: string;
}>;

const MAPPING_REVIEWED_XML = "mapping-reviewed-xml";
const MAPPING_TAXONOMY_JSON = "mapping-taxonomy-json";

export const MappingReview = ({ job, token }: Props) => {
  const [taxonomy, setTaxonomy] = useState(null);
  const [innodom, setInnodom] = useState<string | null>(null);
  const [error, setError] = useState<Error | null>(null);
  useEffect(() => {
    const innodomContent = job.contents.find(c => c.role === MAPPING_REVIEWED_XML);
    if (!innodomContent) {
      setError(new Error("Unable to find reviewed Innodom."));
      return;
    }
    fetch(innodomContent.uri, {
      headers: {
        Authorization: `Basic ${token}`,
      },
      method: "GET",
    })
      .then(res => res.text())
      .then(res => {
        setInnodom(res);
      })
      .catch(setError);
  }, [job.id, token]);
  useEffect(() => {
    const taxonomyContent = job.contents.find(c => c.role === MAPPING_TAXONOMY_JSON);
    if (!taxonomyContent) {
      setError(new Error("Unable to find mapping taxonomy."));
      return;
    }
    fetch(taxonomyContent.uri, {
      headers: {
        Authorization: `Basic ${token}`,
      },
      method: "GET",
    })
      .then(res => res.json())
      .then(res => {
        setTaxonomy(res);
      })
      .catch(setError);
  }, [job.id, token]);
  if (error) {
    return <div>Error: {error.message}</div>;
  }
  if (!taxonomy || !innodom) {
    return <div>Loading...</div>;
  }
  return (
    <Mapping
      aborting={false}
      jobId={job.id}
      onSave={async () => {
        console.warn("This should never be called.");
      }}
      rawTaxonomy={taxonomy}
      review={true}
      saving={false}
      xml={innodom}
    />
  );
};
MappingReview.displayName = "MappingReview";
