import React, { useContext, useEffect, useState } from "react";
import { RouteComponentProps } from "react-router";
import useInterval from "use-interval";

import { UUID } from "@ilabs/core";
import { JobContext, UserContext } from "../../../../../../context-containers";
import { Contents } from "./Contents";
import { Options } from "./Options";
import { Pagination } from "./Pagination";
import { useJobListRefresh } from "./useJobListRefresh";

export interface IRouteParams {
  jobId: UUID;
}

export interface IOwnProps extends RouteComponentProps<IRouteParams> {}

export type Props = Readonly<IOwnProps>;

const JOBS_PER_PAGE = 15;

export function Sidebar({ history }: Props) {
  const {
    actions: { ToggleLoadNext },
    state: { authLevel, automaticallyLoadNextJob, userAuth, blockingTaskKind, user },
  } = useContext(UserContext)!;
  const {
    actions: { AbortJob, FetchBlockedJobs },
    state: { blockedJobs, checkedOutJob, totalJobs },
  } = useContext(JobContext)!;
  const [actionsDisabled, setActionsDisabled] = useState(false);
  const [currentPage, setCurrentPage] = useState(0);
  const [fetching, setFetching] = useState(true);
  const loadPage = (page: number, force: boolean = false) => {
    if (!userAuth || !blockingTaskKind || (!force && page === currentPage)) {
      return;
    }
    setCurrentPage(page);
    setFetching(true);
    FetchBlockedJobs(userAuth, blockingTaskKind, {
      limit: JOBS_PER_PAGE,
      offset: JOBS_PER_PAGE * page,
    })
      .then(() => setFetching(false))
      .catch(() => setFetching(false));
  };
  useEffect(() => {
    loadPage(0, true);
  }, []);
  useJobListRefresh(
    authLevel,
    userAuth,
    blockingTaskKind,
    blockedJobs,
    FetchBlockedJobs,
    currentPage,
    JOBS_PER_PAGE,
  );

  const [currentTime, setCurrentTime] = useState(Math.round(new Date().getTime() / 1000));
  useInterval(() => {
    setCurrentTime(Math.round(new Date().getTime() / 1000));
  }, 0.5 * 1000);

  const onComplete = (redirect: boolean) => () => {
    setActionsDisabled(false);
    if (redirect) {
      history.push("/");
    }
  };
  const onAbort = (id: string) => () => {
    if (userAuth !== null) {
      setActionsDisabled(true);
      const current = checkedOutJob.data !== null && checkedOutJob.data.id === id;
      setTimeout(() => {
        AbortJob(userAuth, id)
          .then(onComplete(current))
          .catch(onComplete(current));
      }, 0);
    }
  };
  return (
    <div className="sidebar">
      <div className="sidebar-header">
        <Pagination
          currentJobsOnPage={blockedJobs.data.length}
          currentPage={currentPage}
          fetching={fetching || actionsDisabled}
          loadPage={loadPage}
          totalJobs={totalJobs}
          totalPages={Math.max(1, Math.ceil(totalJobs / JOBS_PER_PAGE))}
        />
        <Options loadNext={automaticallyLoadNextJob} onToggleLoadNext={ToggleLoadNext} />
      </div>
      <Contents
        checkedOutJobId={checkedOutJob.data === null ? null : checkedOutJob.data.id}
        currentTime={currentTime}
        disableActions={actionsDisabled}
        fetching={fetching}
        jobs={blockedJobs.data}
        onAbort={onAbort}
        user={user!.data!}
      />
    </div>
  );
}
