import React from "react";

import { Action, IPageState } from "../StateManager/state";
import { URI } from "../types";
import { PreviewImage } from "./PreviewImage";

interface IProps {
  readonly activePage: number;
  readonly dispatch: (action: Action) => void;
  readonly highConfidenceThreshold: number;
  readonly pageConfidences: Array<number>;
  readonly pageImageURIs: Array<URI>;
  readonly pages: Array<IPageState>;
  readonly onSelectPage: (index: number) => void;
}

export const PagePreview = ({
  activePage,
  dispatch,
  highConfidenceThreshold,
  onSelectPage,
  pageConfidences,
  pageImageURIs,
  pages,
}: IProps) => (
  <div className="page-preview">
    {pageImageURIs.map((uri: URI, index: number) => (
      <PreviewImage
        active={index === activePage}
        confidence={pageConfidences[index]}
        dispatch={dispatch}
        highConfidenceThreshold={highConfidenceThreshold}
        index={index}
        key={`page-${index}`}
        modified={pages[index].modified}
        noiseReductionLevel={pages[index].noiseReductionLevel}
        onFocus={() => {
          onSelectPage(index);
        }}
        src={uri}
        total={pages.length}
      />
    ))}
  </div>
);
