export interface IAddJobContentRequest extends IAuthenticatedRequest {
  uri: string;
  body: AddJobContentRequest;
}

export interface IAddJobContentResult {
  response: IAddJobContentResponse | null;
  errors: IErrors | null;
}

export const addJobContent = async (req: IAddJobContentRequest): Promise<IAddJobContentResult> => {
  const res = await request<Routes.JobContent, "POST">({
    body: req.body,
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/octet-stream",
      "x-workbench": "true",
    }),
    method: "POST",
    url: req.uri,
  });

  switch (res.status) {
    case HTTPStatus.UNAUTHORIZED:
    case HTTPStatus.BAD_REQUEST:
      return {
        errors: { error: (res.body!.response as IApiErrorResponse)!.message },
        response: null,
      };
    case HTTPStatus.CREATED:
      return {
        errors: null,
        response: res.body!.response as IAddJobContentResponse,
      };
    default:
      return {
        errors: { error: "Unknown response code" },
        response: null,
      };
  }
};

export interface IGetJobContentRequest {
  authKey: string;
  uri: string;
}

export interface IGetJobContentResponse {
  data: string | null;
  errors: IErrors | null;
}

export const getJobContent = async (
  req: IGetJobContentRequest,
): Promise<IGetJobContentResponse> => {
  const res = await gather<null, string | IApiResponse<IApiErrorResponse>>({
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
    }),
    method: "GET",
    url: req.uri,
  });

  switch (res.status) {
    case HTTPStatus.UNAUTHORIZED:
    case HTTPStatus.BAD_REQUEST:
      return {
        data: null,
        errors: { error: "failed to fetch job-contents" },
      };
    case HTTPStatus.OK:
      return {
        data: res.body as string,
        errors: null,
      };
    case HTTPStatus.NOT_FOUND:
      return {
        data: null,
        errors: { error: (res.body! as IApiResponse<IApiErrorResponse>).response.message },
      };
    default:
      return {
        data: null,
        errors: { error: "unknown response code" },
      };
  }
};

