export interface ICheckoutJobOptions {
  authKey: string;
  jobId: UUID;
}

export interface ICheckoutJobResult {
  response: CheckoutJobResponse | null;
  errors: IErrors | null;
  jobId: UUID;
}

export const checkoutJob = async (req: ICheckoutJobOptions): Promise<ICheckoutJobResult> => {
  const res = await request<Routes.JobCheckout, "POST">({
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/json",
    }),
    method: "POST",
    url: `${apiEndpointV1}/jobs/${req.jobId}/checkout`,
  });

  switch (res.status) {
    case HTTPStatus.BAD_REQUEST:
      const apiError: IApiErrorResponse = (res.body!.response as IApiErrorResponse)!;
      const field = apiError.field === null ? "error" : apiError.field;

      return {
        errors: { [field]: apiError.message },
        jobId: req.jobId,
        response: null,
      };
    case HTTPStatus.OK:
      return {
        errors: null,
        jobId: req.jobId,
        response: res.body!.response as CheckoutJobResponse,
      };
    case HTTPStatus.NOT_FOUND:
      return {
        errors: { error: "job not found" },
        jobId: req.jobId,
        response: null,
      };
    default:
      return {
        errors: { error: "unknown response code" },
        jobId: req.jobId,
        response: null,
      };
  }
};

export interface ICheckoutNextJobOptions {
  authKey: string;
  jobType: string;
}

export interface ICheckoutNextJobResult {
  response: CheckoutJobResponse | null;
  errors: IErrors | null;
  hasData: boolean;
}

export const checkoutNextJob = async (
  req: ICheckoutNextJobOptions,
): Promise<ICheckoutNextJobResult> => {
  const res = await request<Routes.JobCheckout, "POST">({
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/json",
    }),
    method: "POST",
    url: `${apiEndpointV1}/jobs/next/checkout/${req.jobType}`,
  });

  switch (res.status) {
    case HTTPStatus.BAD_REQUEST:
      const apiError: IApiErrorResponse = (res.body!.response as IApiErrorResponse)!;
      const field = apiError.field === null ? "error" : apiError.field;

      return {
        errors: { [field]: apiError.message },
        hasData: false,
        response: null,
      };
    case HTTPStatus.OK:
      return {
        errors: null,
        hasData: true,
        response: res.body!.response as CheckoutJobResponse,
      };
    case HTTPStatus.NOT_FOUND:
      return {
        errors: null,
        hasData: false,
        response: null,
      };
    default:
      return {
        errors: { error: "unknown response code" },
        hasData: false,
        response: null,
      };
  }
};

