import cx from "classnames";
import React from "react";

import { IBlock } from "../types";

interface IProps {
  block: IBlock;
  blockId: string;
  selected: boolean;
  onSelectBlock: () => void;
}

export const Block = ({ block, blockId, onSelectBlock, selected }: IProps) => {
  const blockClasses = cx("block", { selected });
  return (
    <div className={blockClasses} id={blockId} onClick={onSelectBlock}>
      {block.rendered}
    </div>
  );
};
Block.displayName = "Block";
