import React, { createContext, Dispatch, useMemo, useReducer } from "react";

import { useActions, UserActions } from "../actions/user";
import { user } from "../reducers";
import { defaultUserState, IUserState } from "../types";

export interface IUserContext {
  actions: ReturnType<typeof useActions>;
  dispatch: Dispatch<UserActions>;
  state: IUserState;
}

export const UserContext = createContext<IUserContext | null>(null);

interface IOwnProps {
  children: React.ReactChild;
  initialLoadNext: boolean;
}

type IProps = Readonly<IOwnProps>;

export function UserProvider({ children, initialLoadNext }: IProps) {
  const defaultReducerState = useMemo(
    () => ({
      ...defaultUserState,
      automaticallyLoadNextJob: initialLoadNext,
    }),
    [defaultUserState, initialLoadNext],
  );
  const [state, dispatch] = useReducer(user, defaultReducerState);
  const actions = useActions(state, dispatch);

  return (
    <UserContext.Provider value={{ state, dispatch, actions }}>{children}</UserContext.Provider>
  );
}
