import React, { useContext } from "react";

import cx from "classnames";
import { RouteComponentProps } from "react-router";
import { Link } from "react-router-dom";
import { ToggleRootSidebar } from "../../../actions/user";

import { UserContext } from "../../../context-containers";
import { AuthLevel } from "../../../types";

export interface IOwnProps extends RouteComponentProps {
  appTitle: string;
}

export type Props = Readonly<IOwnProps>;

function UserActions(props: Props) {
  // props
  const { location } = props;

  // hooks
  const {
    state: { authLevel },
  } = useContext(UserContext)!;

  switch (authLevel) {
    case AuthLevel.initial:
      return null;
    case AuthLevel.authenticated:
      break;
    case AuthLevel.unauthenticated:
      return (
        <>
          <li className="nav-item">
            <Link
              to="/register"
              className={cx({ "nav-link": true, active: location.pathname === "/register" })}
            >
              Register
              <i className="fa fa-edit ml-2" />
            </Link>
          </li>
          <li className="nav-item">
            <Link
              to="/login"
              className={cx({ "nav-link": true, active: location.pathname === "/login" })}
            >
              Login
              <i className="fa fa-sign-in ml-2" />
            </Link>
          </li>
        </>
      );
  }

  return (
    <>
      <li className="nav-item">
        <Link
          to="/logout"
          className={cx({ "nav-link": true, active: location.pathname === "/logout" })}
        >
          Logout
          <i className="fa fa-sign-out ml-2" />
        </Link>
      </li>
    </>
  );
}

function SubActions(props: Props) {
  // hooks
  const {
    dispatch,
    state: { hideSidebar },
  } = useContext(UserContext)!;

  return (
    <button
      type="button"
      className="btn btn-outline-light"
      onClick={() => dispatch(ToggleRootSidebar())}
    >
      <span
        aria-hidden="true"
        className={hideSidebar ? "icon-arrow-right" : "icon-arrow-left"}
        style={{ fontSize: "12px" }}
      />
    </button>
  );
}

export function Navbar(props: Props) {
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      {SubActions(props)}
      <Link to="/" className="navbar-brand ml-2">
        {props.appTitle}
      </Link>
      <div className="collapse navbar-collapse">
        <ul className="navbar-nav ml-auto">{UserActions(props)}</ul>
      </div>
    </nav>
  );
}
