import React, { ReactElement, useContext } from "react";

import { Route, RouteComponentProps, Switch } from "react-router-dom";

import { UserContext } from "../../../context-containers";
import { IRenderJobOptions } from "../../../helpers";
import { LoginRouteContainer } from "../../../route-containers/Workbench/App/Viewport/Login";
import { LogoutRouteContainer } from "../../../route-containers/Workbench/App/Viewport/Logout";
import { RegisterRouteContainer } from "../../../route-containers/Workbench/App/Viewport/Register";
import { RootRouteContainer } from "../../../route-containers/Workbench/App/Viewport/Root";
import { AuthLevel } from "../../../types";
import { Loading } from "../../util";

export interface IOwnProps<C> extends RouteComponentProps<{}> {
  renderJob: (t: IRenderJobOptions<C>) => ReactElement;
  appTitle: string;
}

export type Props<C> = Readonly<IOwnProps<C>>;

export function Viewport<C>(props: Props<C>) {
  // props
  const { renderJob, appTitle } = props;

  // hooks
  const {
    state: { authLevel },
  } = useContext(UserContext)!;

  if (authLevel === AuthLevel.initial) {
    return (
      <div className="flex-fill p-3">
        <Loading text="Loading" />
      </div>
    );
  }

  return (
    <Switch>
      <Route exact={true} path="/login" component={LoginRouteContainer} />
      <Route exact={true} path="/logout" component={LogoutRouteContainer} />
      <Route exact={true} path="/register" component={RegisterRouteContainer} />
      <Route>
        <RootRouteContainer appTitle={appTitle} renderJob={renderJob} />
      </Route>
    </Switch>
  );
}
