import { RefObject, useEffect } from "react";

export const centerOnZoom = (scrollRef: RefObject<HTMLDivElement>, zoom: number) =>
  useEffect(() => {
    const { current } = scrollRef;
    if (!current) {
      return;
    }
    current.scrollTo((current.scrollWidth - current.clientWidth) / 2, 0);
  }, [zoom]);
