import useLocalStorage from "@rooks/use-localstorage";
import React, { ReactElement } from "react";
import { HashRouter as ReactRouter } from "react-router-dom";

import { JobProvider } from "../context-containers/Job";
import { UserProvider } from "../context-containers/User";
import { IRenderJobOptions } from "../helpers";
import { AppRouteContainer } from "../route-containers/Workbench/App";

export interface IOwnProps<C> {
  children: (t: IRenderJobOptions<C>) => ReactElement;
  appTitle: string;
  basename: string;
  roleWhitelist: string[];
  blockingTaskKind: string;
}

export type Props<C> = Readonly<IOwnProps<C>>;

export function Workbench<Contents>(props: Props<Contents>) {
  // props
  const { appTitle, basename, roleWhitelist, blockingTaskKind, children } = props;
  const [loadNext] = useLocalStorage("load-next", "false");
  return (
    <JobProvider>
      <UserProvider initialLoadNext={loadNext === "true"}>
        <ReactRouter basename={basename}>
          <AppRouteContainer
            appTitle={appTitle}
            renderJob={children}
            roleWhitelist={roleWhitelist}
            blockingTaskKind={blockingTaskKind}
          />
        </ReactRouter>
      </UserProvider>
    </JobProvider>
  );
}
