import { useMemo } from "react";

import {
  IDataPoint,
  IDataPointGroup,
  IDataPointValue,
  IState,
  ITaxonomy,
  TaxonomyEntry,
  TaxonomyEntryType,
} from "../types";

/**
 * Returns whether or not the given value is valid based on the requiresSupport logic.
 * @param value IDataPointValue<any> The value to test.
 * @param tax TaxonomyEntry The taxonomy entry to use as a judge.
 * @return boolean Returns true if the support is valid and false otherwise.
 */
export const isSupportValid = (value: IDataPointValue<any>, tax: TaxonomyEntry): boolean => {
  // groups never require support
  if (tax.type === TaxonomyEntryType.Group) {
    return true;
  }
  // check if the taxonomy entry requires support
  if (!tax.requiresSupport) {
    return true;
  }
  // if the value is empty and not required, then we skip requesting support
  if (!tax.required && (value.value === null || value.valueRaw.length < 1)) {
    return true;
  }
  // otherwise, return whether or not we have a non-null idRef
  return value.idRef !== null;
};

/**
 * Returns whether the given value is valid based on the required logic.
 * @param value IDataPointValue<any> The value to test.
 * @param tax TaxonomyEntry The taxonomy entry to use as a judge.
 * @return boolean Returns true if the value is acceptable, and false otherwise.
 */
export const isDataPointValid = (value: IDataPointValue<any>, tax: TaxonomyEntry): boolean => {
  // if the data point is not required, we don't need to check anything
  if (!tax.required) {
    return true;
  }
  // ensure the string representation of the value is non-empty
  return value.valueRaw.length > 0;
};

/**
 * Returns whether or not all the given values (of the same type) are valid.
 * @param values Array<IDataPointValue<any>> The list of input values.
 * @param tax TaxonomyEntry The taxonomy entry to use as a judge.
 * @return boolean Returns true if all the given values are valid, and false otherwise.
 */
const allValuesValid = (values: Array<IDataPointValue<any>>, tax: TaxonomyEntry): boolean =>
  !values.some(v => !isDataPointValid(v, tax) || !isSupportValid(v, tax));

/**
 * Returns whether the given group is valid.
 * @param group IDataPointGroup The given data point group.
 * @param taxonomy ITaxonomy The taxonomy to judge against.
 * @return boolean Returns true if the group is valid and false otherwise.
 */
const isGroupValid = (group: IDataPointGroup, taxonomy: ITaxonomy): boolean => {
  const taxonomyEntry = taxonomy[group.key];
  const memberDataPointKeys = Object.keys(group.dataPoints);
  for (const value of group.values) {
    for (const memberDataPointKey of memberDataPointKeys) {
      if (!(memberDataPointKey in value)) {
        return false;
      }
      if (taxonomyEntry.required && value[memberDataPointKey].length < 1) {
        return false;
      }
      if (!allValuesValid(value[memberDataPointKey], taxonomy[memberDataPointKey])) {
        return false;
      }
    }
  }
  return true;
};

/**
 * Returns whether the given non-group is valid.
 * @param nonGroup IDataPoint A non-grouped data point input value.
 * @param taxonomyEntry ITaxonomy The taxonomy entry to judge against.
 * @return boolean Returns true if the data point is valid and false otherwise.
 */
const isNonGroupValid = (nonGroup: IDataPoint, taxonomyEntry: TaxonomyEntry): boolean => {
  if (taxonomyEntry.type === TaxonomyEntryType.Group) {
    return false;
  }
  return allValuesValid(nonGroup.values, taxonomyEntry);
};

/**
 * Returns whether the given state is valid and can be saved.
 * @param dataPoints IState["dataPoints"] The current set of data points.
 * @param taxonomy The current taxonomy.
 * @return Returns true if the given data points are completely valid and false otherwise.
 */
export const areAnswersValid = (dataPoints: IState["dataPoints"], taxonomy: ITaxonomy) =>
  useMemo(() => {
    const dataPointKeys = Object.keys(dataPoints);
    for (const dataPointKey of dataPointKeys) {
      const dataPoint = dataPoints[dataPointKey];
      const taxonomyEntry = taxonomy[dataPointKey];
      if (taxonomyEntry.required && dataPoint.values.length < 1) {
        return false;
      }
      if (dataPoint.isGroup && !isGroupValid(dataPoint, taxonomy)) {
        return false;
      } else if (!dataPoint.isGroup && !isNonGroupValid(dataPoint, taxonomyEntry)) {
        return false;
      }
    }
    return true;
  }, [dataPoints]);
