import React, { RefObject, useRef } from "react";
import ReactResizeDetector from "react-resize-detector";

import { URI } from "@ilabs/core";
import { Action, IState, ZoomLevel } from "../StateManager/state";
import { Canvas } from "./Canvas";
import { centerOnZoom } from "./centerOnZoom";
import { Toolbar } from "./Toolbar";

interface IProps {
  readonly dispatch: (action: Action) => void;
  readonly onDelete: () => void;
  readonly onSave: () => void;
  readonly pageImageURIs: Array<URI>;
  readonly review: boolean;
  readonly saving: boolean;
  readonly state: IState;
}
interface IResizeDetectorCallbackArgs {
  height: number;
  width: number;
}
export const Renderer = ({
  dispatch,
  onDelete,
  onSave,
  pageImageURIs,
  review,
  saving,
  state,
}: IProps) => {
  const scrollRef: RefObject<HTMLDivElement> = useRef(null);
  const { loaded } = state.pages[state.activePage];
  // if the user changes the zoom, we recentre the canvas on the screen
  centerOnZoom(scrollRef, state.zoom);
  return (
    <div className="renderer">
      {!review && (
        <Toolbar
          activeZones={state.pages[state.activePage].activeZones}
          autoShrink={state.autoShrink}
          currentLabel={state.currentLabel}
          disabled={saving}
          onChangeZoom={(zoom: ZoomLevel) => {
            dispatch({
              type: "CHANGE ZOOM LEVEL",
              zoom,
            });
          }}
          onDelete={onDelete}
          onSave={onSave}
          onToggleAutoShrink={() => {
            dispatch({
              type: "TOGGLE AUTO SHRINK",
            });
          }}
          onUpdateCurrentLabel={label => {
            dispatch({
              label: label.length > 0 ? label : "text",
              type: "CHANGE CURRENT LABEL",
            });
          }}
          saving={saving}
          zoom={state.zoom}
        />
      )}
      <div className="flex-fill flex-grow pt-2 pb-2">
        <ReactResizeDetector handleHeight handleWidth>
          {({ height, width }: IResizeDetectorCallbackArgs) => (
            <div
              style={{
                height: `${height}px`,
                width: `${width}px`,
              }}
            >
              <div
                ref={scrollRef}
                style={{
                  height: "100%",
                  overflow: "auto",
                  position: "relative",
                  width: "100%",
                }}
              >
                <Canvas
                  disabled={saving || review}
                  dispatch={dispatch}
                  height={height}
                  imageURI={pageImageURIs[state.activePage]}
                  state={state}
                  width={width}
                  zoom={state.zoom}
                />
              </div>
              {!loaded && (
                <div className="loading-wrapper">
                  <div className="spinner-border m-auto text-light" role="status">
                    <span className="sr-only">Loading...</span>
                  </div>
                </div>
              )}
            </div>
          )}
        </ReactResizeDetector>
      </div>
    </div>
  );
};
