import React, { ReactElement, useContext } from "react";

import { Redirect, Route, RouteComponentProps, Switch } from "react-router";

import { UserContext } from "../../../../context-containers";
import { IRenderJobOptions } from "../../../../helpers";
import { MainRouteContainer } from "../../../../route-containers/Workbench/App/Viewport/Root/Main";
// tslint:disable-next-line:max-line-length
import { SidebarRouteContainer } from "../../../../route-containers/Workbench/App/Viewport/Root/Sidebar";
// tslint:disable-next-line:max-line-length
import { ViewJobRouteContainer } from "../../../../route-containers/Workbench/App/Viewport/Root/ViewJob";
import { AuthLevel } from "../../../../types";
import { NotFound } from "../../../util";
import { ErrorBoundary } from "../../../util/ErrorBoundary";

function renderMain<C>(appTitle: string, renderJob: (t: IRenderJobOptions<C>) => ReactElement) {
  return (
    <Switch>
      <Route exact={true} path="/">
        <MainRouteContainer appTitle={appTitle} />
      </Route>
      <Route path="/job/:jobId">
        <ViewJobRouteContainer renderJob={renderJob} />
      </Route>
      <Route component={NotFound} />
    </Switch>
  );
}

export interface IOwnProps<C> extends RouteComponentProps<{}> {
  renderJob: (t: IRenderJobOptions<C>) => ReactElement;
  appTitle: string;
}

export type Props<C> = Readonly<IOwnProps<C>>;

export function Root<C>(props: Props<C>) {
  // props
  const { renderJob, appTitle } = props;

  // hooks
  const {
    state: { authLevel, hideSidebar },
  } = useContext(UserContext)!;

  if (authLevel !== AuthLevel.authenticated) {
    return <Redirect to="/login" />;
  }

  return (
    <>
      <div className={hideSidebar ? "hidden" : ""} id="job-list">
        <Switch>
          <Route path="/job/:jobId" component={SidebarRouteContainer} />
          <Route component={SidebarRouteContainer} />
        </Switch>
      </div>
      <div id="workbench-contents">
        <ErrorBoundary>{renderMain(appTitle, renderJob)}</ErrorBoundary>
      </div>
    </>
  );
}
