import cx from "classnames";
import moment from "moment";
import React from "react";

import { IJob, IUser } from "@ilabs/core";
import { Link } from "react-router-dom";

interface IProps {
  readonly current: boolean;
  readonly currentTime: number;
  readonly disableActions: boolean;
  readonly job: IJob;
  readonly onAbort: () => void;
  readonly user: IUser;
}

export const Job = ({ current, currentTime, disableActions, job, onAbort, user }: IProps) => {
  let claimedBy = null;
  if (
    job.claimed_by !== null &&
    job.claimed_by.claimed_until >= currentTime &&
    job.claimed_by.id !== user.id
  ) {
    claimedBy = (
      <li>
        <span className="text-dark">Claimed by:</span> {job.claimed_by.name}
      </li>
    );
  }
  let actions = null;
  if (job.claimed_by !== null && job.claimed_by.id === user.id) {
    actions = (
      <li className="actions d-flex flex-row">
        <button
          className="btn btn-sm btn-secondary"
          disabled={disableActions}
          onClick={e => {
            e.preventDefault();
            e.stopPropagation();
            if (!disableActions) {
              const confirm = window.confirm("Are you sure you want to abort this job?");
              if (confirm) {
                onAbort();
              }
            }
          }}
          type="button"
        >
          Abort
        </button>
      </li>
    );
  }
  return (
    <li className={cx({ "list-group-item": true, "sidebar-job-container": true, current })}>
      <Link to={`/job/${job.id}`} className="sidebar-job">
        <ul className="job-metadata">
          <li className="name">{job.name}</li>
          <li>
            <small className="text-dark">
              <i aria-hidden="true" className="fa fa-calendar" />{" "}
              {moment.unix(job.date_created).format("MMM D, YYYY h:mm A")}
            </small>
          </li>
          {claimedBy}
          {actions}
        </ul>
      </Link>
    </li>
  );
};
Job.displayName = "Job";
