import { InnodomDateFormats } from "../../../util/dateFormats";
import { IDataPoint, IDataPointGroup, IState, ITaxonomy, TaxonomyEntryType } from "../types";
import { ATTR_CONFIDENCE, ATTR_ID_REF, ATTR_KEY, TAG_DATAPOINT, TAG_META } from "./getDefaultState";

const ATTR_FORMAT = "format";

const insertDataPoint = (
  root: Document,
  meta: Element,
  type: TaxonomyEntryType,
  key: string,
  confidence: number,
  idRef: string | null,
  value: string,
) => {
  const newDataPointTag = root.createElement(TAG_DATAPOINT);
  newDataPointTag.setAttribute(ATTR_KEY, key);
  newDataPointTag.setAttribute(ATTR_CONFIDENCE, confidence.toString(10));
  if (idRef !== null) {
    newDataPointTag.setAttribute(ATTR_ID_REF, idRef);
  }
  if (type !== TaxonomyEntryType.Pointer) {
    newDataPointTag.appendChild(root.createTextNode(value));
  }
  switch (type) {
    case TaxonomyEntryType.Date:
    case TaxonomyEntryType.DateTime:
    case TaxonomyEntryType.Time:
      newDataPointTag.setAttribute(ATTR_FORMAT, InnodomDateFormats[type]);
      break;
    default:
      break;
  }
  meta.appendChild(newDataPointTag);
};

export const insertDataPoints = (
  root: Document,
  dataPoints: IState["dataPoints"],
  taxonomy: ITaxonomy,
) => {
  const metaTags = root.getElementsByTagName(TAG_META);
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < metaTags.length; i++) {
    metaTags[i].remove();
  }
  const newMetaTag = root.createElement(TAG_META);
  Object.keys(dataPoints).forEach((key: string) => {
    const taxonomyEntry = taxonomy[key];
    if (!dataPoints[key].isGroup) {
      const nonGroup = dataPoints[key] as IDataPoint;
      nonGroup.values.forEach((value, index) => {
        insertDataPoint(
          root,
          newMetaTag,
          taxonomyEntry.type,
          taxonomyEntry.multiple ? `${key}.${index + 1}` : key,
          value.confidence,
          value.idRef,
          value.valueRaw,
        );
      });
      return;
    }
    const group = dataPoints[key] as IDataPointGroup;
    group.values.forEach((groupValue, groupIndex) => {
      const groupPrefix = taxonomyEntry.multiple ? `${key}.${groupIndex + 1}.` : `${key}.`;
      Object.keys(groupValue).forEach(dataPointKey => {
        const innerEntry = taxonomy[dataPointKey];
        for (let i = 0; i < groupValue[dataPointKey].length; i++) {
          const value = groupValue[dataPointKey][i];
          insertDataPoint(
            root,
            newMetaTag,
            innerEntry.type,
            innerEntry.multiple
              ? `${groupPrefix}${dataPointKey}.${i + 1}`
              : `${groupPrefix}${dataPointKey}`,
            value.confidence,
            value.idRef,
            value.valueRaw,
          );
        }
      });
    });
  });
  root.documentElement.appendChild(newMetaTag);
};
