import { useEffect, useState } from "react";

/**
 * The original code for this hook was pulled from:
 * https://github.com/konvajs/use-image/blob/ba3aab4bd46bb9b6f4ca8a44d1beb199b9c8250f/index.js
 *
 * We have made a number of modifications and improvements. Namely, we have converted the code to
 * typescript and added type safeties as a result. Furthermore, the state is now tracked as a cache
 * that includes all URIs that this hook has seen so we do not load the same URI twice.
 */

