type ImageStatus = "loading" | "loaded" | "failed";
interface IImageCache {
  [uri: string]: {
    data: string | undefined;
    image: HTMLImageElement | undefined;
  };
}

let globalImageCache: IImageCache | undefined;

export function useImage(
  url: string,
  crossOrigin: boolean = false,
): [HTMLImageElement | undefined, ImageStatus, string | undefined] {
  if (!globalImageCache) {
    globalImageCache = {};
  }
  const imageCache: IImageCache = globalImageCache;
  const [status, setState] = useState<ImageStatus>(url in imageCache ? "loaded" : "loading");
  useEffect(() => {
    if (!url || url in imageCache) {
      return;
    }
    const img = document.createElement("img");
    function onload() {
      if (!globalImageCache) {
        return;
      }
      const canvas = document.createElement("canvas");
      canvas.height = img.naturalHeight;
      canvas.width = img.naturalWidth;
      const context = canvas.getContext("2d", { alpha: false });
      context!.drawImage(img, 0, 0);
      globalImageCache[url] = {
        data: canvas.toDataURL(),
        image: img,
      };
      setState("loaded");
    }
    function onerror() {
      if (globalImageCache) {
        delete globalImageCache[url];
      }
      setState("failed");
    }
    img.addEventListener("load", onload);
    img.addEventListener("error", onerror);
    if (crossOrigin) {
      img.crossOrigin = "Anonymous";
    }
    img.src = url;
  }, [url, crossOrigin]);
  let data;
  let image;
  if (url in imageCache) {
    data = imageCache[url].data;
    image = imageCache[url].image;
  }
  return [image, status, data];
}
