import useInterval from "use-interval";

import { IJobContext } from "../../../../../../context-containers/Job";
import { AuthLevel, FetchLevel, IJobState } from "../../../../../../types";

export const useJobListRefresh = <C>(
  authLevel: AuthLevel,
  userAuth: string | null,
  blockingTaskKind: string | null,
  blockedJobs: IJobState["blockedJobs"],
  FetchBlockedJobs: IJobContext<C>["actions"]["FetchBlockedJobs"],
  currentPage: number,
  jobsPerPage: number,
) => {
  const jobListRefresh = () => {
    (async () => {
      if (authLevel !== AuthLevel.authenticated) {
        return;
      }

      if (userAuth === null) {
        return;
      }

      if (blockingTaskKind === null) {
        return;
      }

      if ([FetchLevel.initial, FetchLevel.success].includes(blockedJobs.fetchLevel) === false) {
        return;
      }

      await FetchBlockedJobs(userAuth, blockingTaskKind, {
        limit: jobsPerPage,
        offset: jobsPerPage * currentPage,
      });
    })();
  };
  useInterval(jobListRefresh, 2.5 * 1000);
};
