import cx from "classnames";
import React from "react";

interface IProps {
  clear: boolean;
  onClick: () => void;
}

const REMOVE_MESSAGE = "Are you sure you want to remove this field?";
const CLEAR_MESSAGE = "Are you sure you want to clear this field?";

export const RemoveButton = ({ clear, onClick }: IProps) => (
  <a
    className="text-danger"
    href="#"
    onClick={e => {
      e.preventDefault();
      if (window.confirm(clear ? CLEAR_MESSAGE : REMOVE_MESSAGE)) {
        onClick();
      }
    }}
    title="Remove"
  >
    <i className={cx("fa", { "fa-trash": !clear, "fa-times": clear })} />
  </a>
);
RemoveButton.displayName = "RemoveButton";
