import React, { ReactElement, useContext, useEffect } from "react";

import useLocalStorage from "@rooks/use-localstorage";
import { RouteComponentProps } from "react-router-dom";

import { SetCurrentAuthLevel } from "../../actions/user";
import { UserContext } from "../../context-containers";
import { IRenderJobOptions } from "../../helpers";
import { NavbarRouteContainer } from "../../route-containers/Workbench/App/Navbar";
import { ViewportRouteContainer } from "../../route-containers/Workbench/App/Viewport";
import { AuthLevel } from "../../types";

export interface IOwnProps<C> extends RouteComponentProps {
  appTitle: string;
  roleWhitelist: string[];
  blockingTaskKind: string;
  renderJob: (t: IRenderJobOptions<C>) => ReactElement;
}

export type Props<C> = Readonly<IOwnProps<C>>;

export function App<C>(props: Props<C>) {
  // props
  const { renderJob, appTitle, roleWhitelist, blockingTaskKind } = props;

  // hooks
  const {
    actions: { Reauthenticate, SetAppTitle, SetRoleWhitelist, SetBlockingTaskKind },
    dispatch,
    state: { authLevel, automaticallyLoadNextJob, userAuth },
  } = useContext(UserContext)!;

  const [authKey, setAuthKey] = useLocalStorage("auth-key", "");
  const [, setLoadNext] = useLocalStorage("load-next", automaticallyLoadNextJob.toString());

  useEffect(() => {
    SetAppTitle(appTitle);
    SetRoleWhitelist(roleWhitelist);
    SetBlockingTaskKind(blockingTaskKind);
  }, []);

  useEffect(() => {
    if (authKey === null || authKey.length === 0) {
      dispatch(SetCurrentAuthLevel(AuthLevel.unauthenticated));

      return;
    }

    if (authLevel !== AuthLevel.initial) {
      return;
    }

    (async () => {
      await Reauthenticate(authKey);
    })();
  }, []);

  useEffect(() => {
    if (authLevel !== AuthLevel.authenticated) {
      return;
    }

    if (userAuth === null) {
      return;
    }

    setAuthKey(userAuth);
  }, [authLevel]);

  useEffect(() => {
    setLoadNext(automaticallyLoadNextJob ? "true" : "false");
  }, [automaticallyLoadNextJob]);

  return (
    <div id="app">
      <NavbarRouteContainer appTitle={props.appTitle} />
      <div id="viewport">
        <ViewportRouteContainer appTitle={appTitle} renderJob={renderJob} />
      </div>
    </div>
  );
}
