import React, { ReactNode, useMemo } from "react";

import { mode as getMode, stdDev as getStdDev } from "@ilabs/core";

// top level tags
const TAG_TEXT = "inno:text";
const TAG_IMAGE = "inno:image";
const TAG_MATH = "inno:math";
const TAG_TABLE = "inno:table";

const TAG_BLOCK = "inno:block";

// text tags
const TAG_BREAK = "inno:br";
const TAG_ITALICS = "inno:i";
const TAG_BOLD = "inno:b";
const TAG_UNDERLINE = "inno:u";
const TAG_SUPERSCRIPT = "inno:sup";
const TAG_SUBSCRIPT = "inno:sub";
const TAG_TCY = "inno:tcy";
const TAG_FONT = "inno:font";
const ATTR_FONT_SIZE = "font-size";
const TAG_COLOR = "inno:color";
const ATTR_RGB = "rgb";
const TAG_NON_EXTRACT = "inno:nec";

export enum FontClass {
  Smaller = "font-size-smaller",
  Small = "font-size-small",
  Normal = "font-size-normal",
  Large = "font-size-large",
  Larger = "font-size-larger",
}

export interface IFontInformation {
  lowerBound: number;
  mode: number;
  upperBound: number;
}

const extractSizeFromFontElement = (el: Element): number =>
  parseFloat((el.getAttribute(ATTR_FONT_SIZE) || "").trim().replace(/pt$/, ""));

export const extractFontInformation = (root: Element): IFontInformation | undefined => {
  const fontTags = root.getElementsByTagName(TAG_FONT);
  const fontSizes: Array<number> = [];
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < fontTags.length; i++) {
    const fontSize = extractSizeFromFontElement(fontTags[i]);
    if (!isNaN(fontSize)) {
      fontSizes.push(fontSize);
    }
  }
  const mode = getMode(fontSizes);
  if (mode === undefined) {
    return undefined;
  }
  const stdDev = getStdDev(fontSizes);
  if (stdDev === undefined) {
    return { lowerBound: mode, mode, upperBound: mode };
  }
  return { lowerBound: mode - 2 * stdDev, mode, upperBound: mode + 2 * stdDev };
};

const mapFontSizeToClass = (size: number, fontInformation: IFontInformation): FontClass => {
  if (isNaN(size) || size === fontInformation.mode) {
    return FontClass.Normal;
  }
  if (size > fontInformation.mode) {
    return size > fontInformation.upperBound ? FontClass.Larger : FontClass.Large;
  }
  return size < fontInformation.lowerBound ? FontClass.Smaller : FontClass.Small;
};

/**
 * Extract text from the <ns0:text> tag. Possible sub tags include:
 * <br> line break
 * <i> italics
 * <b> bold
 * <u> underlined
 * <sup> superscript
 * <sub> subscript
 * <tcy> weird chinese thing
 * <font> font information
 * <color> text foreground
 * <nec> non-extractable
 * @param el Element The <ns0:text> element.
 * @param keyPrepend A key to prend for unique React keys.
 * @return Array<ReactNode> Returns a renderable list of React nodes.
 */
const extractTextContent = (
  el: Element,
  keyPrepend: string,
  fontInformation: IFontInformation | undefined,
): Array<ReactNode> => {
  const { childNodes } = el;
  let contents: Array<ReactNode> = [];
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < childNodes.length; i++) {
    const child = childNodes[i];
    if (child instanceof Element) {
      const key = `${keyPrepend}${child.nodeName}-${contents.length}`;
      const innerContents = extractTextContent(child, `${key}-`, fontInformation);
      switch (child.nodeName) {
        // map to a line break
        case TAG_BREAK: {
          // for our use case, we do not need to preserve the line breaks due to natural wrapping
          // in the original document...rather we can allow the paragraph text to flow naturally
          // instead for better readability
          contents.push(" ");
          break;
        }
        // map to strong bold text
        case TAG_BOLD: {
          contents.push(
            <span className="font-weight-bold" key={key}>
              {innerContents}
            </span>,
          );
          break;
        }
        // map to italics
        case TAG_ITALICS: {
          contents.push(
            <span className="font-italic" key={key}>
              {innerContents}
            </span>,
          );
          break;
        }
        // map to underline
        case TAG_UNDERLINE: {
          contents.push(<u key={key}>{innerContents}</u>);
          break;
        }
        // map to subscript
        case TAG_SUBSCRIPT: {
          contents.push(<sub key={key}>{innerContents}</sub>);
          break;
        }
        // map to superscript
        case TAG_SUPERSCRIPT: {
          contents.push(<sup key={key}>{innerContents}</sup>);
          break;
        }
        // map to color
        case TAG_COLOR: {
          const rgb = (child.getAttribute(ATTR_RGB) || "").trim().toLowerCase();
          if (rgb.length < 1) {
            contents = contents.concat(innerContents);
            break;
          }
          contents.push(
            <span key={key} style={{ color: rgb }}>
              {innerContents}
            </span>,
          );
          break;
        }
        case TAG_NON_EXTRACT: {
          contents.push(<span key={key}>&#x25A1;</span>);
          break;
        }
        case TAG_FONT: {
          if (fontInformation !== undefined) {
            contents.push(
              <span
                key={key}
                className={mapFontSizeToClass(extractSizeFromFontElement(child), fontInformation)}
              >
                {innerContents}
              </span>,
            );
            break;
          }
          contents = contents.concat(innerContents);
          break;
        }
        // for the following tags, we generally ignore it and just push the inner contents
        case TAG_TCY: {
          contents = contents.concat(innerContents);
          break;
        }
        default: {
          contents = contents.concat(innerContents);
          console.warn("Unhandled tag in text:", child.nodeName);
          break;
        }
      }
      continue;
    }
    contents.push(child.textContent);
  }
  return contents;
};

export const useInnodomContents = (
  el: Element,
  fontInformation: IFontInformation | undefined,
): Array<ReactNode> =>
  useMemo((): Array<ReactNode> => {
    const blocks = Array.from(el.getElementsByTagName(TAG_BLOCK));
    switch (el.nodeName) {
      case TAG_TEXT:
        return blocks.reduce(
          (prev, next, index) => {
            return prev.concat(extractTextContent(next, `block-${index}-`, fontInformation));
          },
          [] as Array<ReactNode>,
        );
      case TAG_IMAGE:
      case TAG_TABLE:
      case TAG_MATH:
        console.warn("Unimplemented tag type:", el.nodeName);
        return [];
      default:
        console.warn("Unknown block:", el.nodeName);
        return [];
    }
  }, [el.outerHTML]);
