export const REQUEST_GET_BLOCKEDJOBS = "REQUEST_GET_BLOCKEDJOBS";
export const RequestGetBlockedJobs = () => createAction(REQUEST_GET_BLOCKEDJOBS);
export const RECEIVE_GET_BLOCKEDJOBS = "RECEIVE_GET_BLOCKEDJOBS";
export const ReceiveGetBlockedJobs = (payload: IApiList<IJob>) =>
  createAction(RECEIVE_GET_BLOCKEDJOBS, payload);

export const REQUEST_CHECKOUT_JOB = "REQUEST_CHECKOUT_JOB";
export const RequestCheckoutJob = () => createAction(REQUEST_CHECKOUT_JOB);
export const RECEIVE_CHECKOUT_JOB = "RECEIVE_CHECKOUT_JOB";
export const ReceiveCheckoutJob = (payload: ICheckoutJobResult) =>
  createAction(RECEIVE_CHECKOUT_JOB, payload);

export const REQUEST_CHECKOUT_NEXTJOB = "REQUEST_CHECKOUT_NEXTJOB";
export const RequestCheckoutNextJob = () => createAction(REQUEST_CHECKOUT_NEXTJOB);
export const RECEIVE_CHECKOUT_NEXTJOB = "RECEIVE_CHECKOUT_NEXTJOB";
export const ReceiveCheckoutNextJob = (payload: ICheckoutNextJobResult) =>
  createAction(RECEIVE_CHECKOUT_NEXTJOB, payload);
export const CLEAR_CHECKOUT_NEXTJOB = "CLEAR_CHECKOUT_NEXTJOB";
export const ClearCheckoutNextJob = () => createAction(CLEAR_CHECKOUT_NEXTJOB);

export const REQUEST_CHECKIN_JOB = "REQUEST_CHECKIN_JOB";
export const RequestCheckinJob = () => createAction(REQUEST_CHECKIN_JOB);
export const RECEIVE_CHECKIN_JOB = "RECEIVE_CHECKIN_JOB";
export const ReceiveCheckinJob = (payload: ICheckinJobResult) =>
  createAction(RECEIVE_CHECKIN_JOB, payload);

export const REQUEST_GET_JOBCONTENT = "REQUEST_GET_JOBCONTENT";
export const RECEIVE_GET_JOBCONTENT = "RECEIVE_GET_JOBCONTENT";
export const RequestGetJobContent = () => createAction(REQUEST_GET_JOBCONTENT);
export const ReceiveGetJobContent = (payload: IGetJobContentResponse) =>
  createAction(RECEIVE_GET_JOBCONTENT, payload);

export const JobActions = {
  ChangeJobtype,
  ClearCheckoutNextJob,
  ParseDroppedFiles,
  ReceiveAbortJob,
  ReceiveCheckinJob,
  ReceiveCheckoutJob,
  ReceiveCheckoutNextJob,
  ReceiveCreateDocuments,
  ReceiveCreateJobs,
  ReceiveDroppedFiles,
  ReceiveGetBlockedJobs,
  ReceiveGetDocuments,
  ReceiveGetJobContent,
  ReceiveGetJobs,
  ReceiveGetJobtypes,
  RequestAbortJob,
  RequestCheckinJob,
  RequestCheckoutJob,
  RequestCheckoutNextJob,
  RequestCreateDocuments,
  RequestCreateJobs,
  RequestGetBlockedJobs,
  RequestGetDocuments,
  RequestGetJobContent,
  RequestGetJobs,
  RequestGetJobtypes,
};

export type JobActions = ActionsUnion<typeof JobActions>;

export const useActions = (state: IJobState, dispatch: Dispatch<JobActions>) => {
  const fileReader = generateFileReader(state.fileReaderOptions);

  return {
    AbortJob: async (authKey: string, jobId: string): Promise<void> => {
      dispatch(RequestAbortJob());
      const result = await checkinJob({
        authKey,
        body: {
          status: JobCheckinStatus.ABORTED,
        },
        jobId,
      });
      dispatch(ReceiveAbortJob(jobId, result));
    },
    DropFiles: async (authKey: string, files: File[], jobType: IJobType) => {
      // parsing the files dropped
      dispatch(ParseDroppedFiles(files));

      const parsedFiles = await Promise.all(files.map(fileReader));
      dispatch(ReceiveDroppedFiles(parsedFiles));

      // producing create-document requests
      const createDocumentRequests: ICreateDocumentRequest[] = parsedFiles
        .filter(v => v.file.data !== null)
        .map<ICreateDocumentRequest>(v => {
          return { authKey, name: v.file.name, body: v.file.data! };
        });
      if (createDocumentRequests.length === 0) {
        return;
      }

      dispatch(RequestCreateDocuments(createDocumentRequests));

      const receivedDocuments = await Promise.all(createDocumentRequests.map(createDocument));
      dispatch(ReceiveCreateDocuments(receivedDocuments));

      // producing create-job requests
      const createJobRequests: ICreateJobRequest[] = receivedDocuments
        .filter(v => v.errors === null)
        .map<ICreateJobRequest>(v => {
          return {
            collaboration: {
              teams: [
                {
                  name: "iLabs Developers",
                  steps: ["*"],
                },
              ],
              users: [],
            },
            contents: [
              {
                name: v.document!.name,
                readonly: true,
                role: "input",
                size: v.document!.size,
                type: v.document!.type,
                uri: v.document!.contents_uri,
              },
            ],
            type: jobType.id,
          };
        });
      if (createJobRequests.length === 0) {
        return;
      }

      dispatch(RequestCreateJobs(createJobRequests));

      const receivedJobs = await Promise.all(createJobRequests.map(v => createJob(authKey, v)));
      dispatch(ReceiveCreateJobs(receivedJobs));
    },
    FetchBlockedJobs: async (
      authKey: string,
      blockingTaskKind: string,
      query?: Partial<IGetBlockedJobsRequest>,
    ) => {
      dispatch(RequestGetBlockedJobs());

      const jobLookup: IGetBlockedJobsOptions = {
        authKey,
        query: { blocking_task: blockingTaskKind, limit: 25, offset: 0, status: JobStatus.BLOCKED },
      };
      if (query) {
        jobLookup.query = {
          ...jobLookup.query,
          ...query,
        };
      }

      dispatch(ReceiveGetBlockedJobs(await getBlockedJobs(jobLookup)));
    },
    FetchCheckinJob: async (authKey: string, jobId: UUID, body: ICheckinJobRequest) => {
      dispatch(RequestCheckinJob());
      dispatch(ReceiveCheckinJob(await checkinJob({ authKey, body, jobId })));
    },
    FetchCheckoutJob: async (authKey: string, jobId: UUID) => {
      dispatch(RequestCheckoutJob());
      dispatch(ReceiveCheckoutJob(await checkoutJob({ authKey, jobId })));
    },
    FetchCheckoutNextJob: async (authKey: string, jobType: string) => {
      dispatch(RequestCheckoutNextJob());
      dispatch(ReceiveCheckoutNextJob(await checkoutNextJob({ authKey, jobType })));
    },
    FetchGetDocuments: async (authKey: string, refetching?: boolean) => {
      dispatch(RequestGetDocuments(refetching));
      dispatch(ReceiveGetDocuments(await getDocuments({ authKey })));
    },
    FetchGetJobContent: async (authKey: string, uri: string) => {
      dispatch(RequestGetJobContent());
      dispatch(ReceiveGetJobContent(await getJobContent({ authKey, uri })));
    },
    FetchGetJobs: async (authKey: string, ids: UUID[], refetching?: boolean) => {
      dispatch(RequestGetJobs(refetching));

      if (ids.length === 0) {
        dispatch(ReceiveGetJobs(await getJobs({ authKey })));
      } else {
        const fetchedJobs = await Promise.all(
          ids.map<Promise<IJob>>(v => getJob({ authKey, id: v })),
        );
        dispatch(ReceiveGetJobs(fetchedJobs));
      }
    },
    FetchGetJobtypes: async () => {
      dispatch(RequestGetJobtypes());
      dispatch(ReceiveGetJobtypes(await getJobTypes()));
    },
  };
};
