import { URI, UUID } from "../contracts";
import { IListedUser } from "./user";

export enum JobStatus {
  NOT_STARTED = "not_started",
  WAITING = "waiting",
  RUNNING = "running",
  CANCELLED = "cancelled",
  COMPLETED = "completed",
  FAILED = "failed",
  BLOCKED = "blocked",
}

export interface IJobProgress {
  current: number;
  total: number;
}

export const getJobMap = (jobs: IJob[]): IJobMap => {
  return jobs.reduce<IJobMap>((out, job) => {
    return { ...out, [job.id]: job };
  }, {});
};

export interface IJobMap {
  [id: string]: IJob;
}

export interface IListedJob {
  blocking_step: string | null;
  claimed_by:
    | (IListedUser & {
        claimed_until: number;
      })
    | null;
  date_created: number;
  error_message: string | null;
  id: UUID;
  name: string;
  owner: IListedUser;
  priority: number;
  progress: IJobProgress | null;
  status: JobStatus;
  type: string;
}

export type TeamCollaboration =
  | { id: string; steps: Array<string> }
  | { name: string; steps: Array<string> };

export type UserCollaboration =
  | { id: string; steps: Array<string> }
  | { email: string; steps: Array<string> };

export interface IJobCollaboration {
  teams: Array<TeamCollaboration>;
  users: Array<UserCollaboration>;
}

export interface IJobContent {
  id: UUID;
  name: string;
  readonly: boolean;
  role: string;
  size: number;
  type: string;
  uri: URI;
}

export interface IJob extends IListedJob {
  collaboration: IJobCollaboration;
  contents: Array<IJobContent>;
  error_message: string | null;
  metadata: {
    [key: string]: any;
  };
  owner: IListedUser;
}
