import { IDataPointGroup, IState } from "../../types";

export interface IToggleGroupCollapseAction {
  groupIndex: number;
  groupKey: string;
  type: "TOGGLE GROUP COLLAPSE";
}

export const toggleGroupCollapse = (state: IState, action: IToggleGroupCollapseAction): IState => {
  if (action.groupKey in state.dataPoints && state.dataPoints[action.groupKey].isGroup) {
    const group = state.dataPoints[action.groupKey] as IDataPointGroup;
    if (group.values[action.groupIndex] !== undefined) {
      let collapsedValues = group.collapsedValues;
      if (collapsedValues.includes(action.groupIndex)) {
        collapsedValues = collapsedValues.filter(v => v !== action.groupIndex);
      } else {
        collapsedValues = [...collapsedValues, action.groupIndex];
      }
      return {
        ...state,
        dataPoints: {
          ...state.dataPoints,
          [action.groupKey]: {
            ...group,
            collapsedValues,
          },
        },
      };
    }
  }
  return state;
};
