import { getJobMap, IDocument, IErrors, IJob } from "@ilabs/core";

import {
  JobActions,
  ParseDroppedFiles,
  ReceiveAbortJob,
  ReceiveCheckinJob,
  ReceiveCheckoutJob,
  ReceiveCheckoutNextJob,
  ReceiveCreateDocuments,
  ReceiveCreateJobs,
  ReceiveDroppedFiles,
  ReceiveGetBlockedJobs,
  ReceiveGetJobContent,
  ReceiveGetJobs,
  ReceiveGetJobtypes,
  RequestCreateDocuments,
  RequestCreateJobs,
  RequestGetJobs,
} from "../../actions/job";
import { ICreateJobResult } from "../../api/v1";
import { FetchLevel, ICreateJobErrors, IJobState } from "../../types";
import { IKindHandlers, Runner } from "./index";

const handlers: IKindHandlers<IJobState, JobActions> = {
  blockedjobs: {
    get: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveGetBlockedJobs>): IJobState => {
        return {
          ...state,
          blockedJobs: {
            ...state.blockedJobs,
            data: action.payload.list,
            fetchLevel: FetchLevel.success,
          },
          totalJobs: action.payload.total_count,
        };
      },
      request: (state: IJobState): IJobState => {
        const fetchLevel =
          state.blockedJobs.fetchLevel === FetchLevel.initial
            ? FetchLevel.fetching
            : FetchLevel.refetching;

        return { ...state, blockedJobs: { ...state.blockedJobs, fetchLevel } };
      },
    },
  },
  documents: {
    create: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveCreateDocuments>): IJobState => {
        const documents = action.payload
          .filter(v => v.document !== null)
          .map<IDocument>(v => v.document!);

        return {
          ...state,
          documents: {
            ...state.documents,
            createRequests: [],
            createResults: action.payload,
            list: {
              ...state.documents.list,
              data: [...state.documents.list.data, ...documents],
            },
          },
        };
      },
      request: (state: IJobState, action: ReturnType<typeof RequestCreateDocuments>): IJobState => {
        return {
          ...state,
          documents: {
            ...state.documents,
            createRequests: action.payload,
            createResults: [],
            dropped: {
              ...state.documents.dropped,
              files: [],
            },
          },
        };
      },
    },
  },
  files: {
    dropped: {
      parse: (state: IJobState, action: ReturnType<typeof ParseDroppedFiles>): IJobState => {
        return {
          ...state,
          documents: {
            ...state.documents,
            dropped: {
              ...state.documents.dropped,
              errors: {},
              files: action.payload,
            },
          },
        };
      },
      receive: (state: IJobState, action: ReturnType<typeof ReceiveDroppedFiles>): IJobState => {
        const errors = action.payload
          .filter(v => v.error !== null)
          .reduce<IErrors>((out, v) => {
            return { ...out, [v.file.name]: v.error! };
          }, {});
        if (Object.keys(errors).length > 0) {
          return {
            ...state,
            documents: {
              ...state.documents,
              dropped: { ...state.documents.dropped, errors, files: [] },
            },
          };
        }

        return {
          ...state,
          documents: {
            ...state.documents,
            dropped: { ...state.documents.dropped, errors: {} },
          },
        };
      },
    },
  },
  job: {
    abort: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveAbortJob>): IJobState => {
        let blockedJobs = state.blockedJobs;
        if (action.payload.errors === null) {
          blockedJobs = {
            ...state.blockedJobs,
            data: state.blockedJobs.data.reduce((jobs, job, index) => {
              if (
                state.checkedOutJob.data !== null &&
                state.checkedOutJob.data.id === action.jobId
              ) {
                return [
                  ...state.blockedJobs.data.slice(0, index),
                  {
                    ...job,
                    claimed_by: null,
                  },
                  ...state.blockedJobs.data.slice(index + 1),
                ];
              }
              return jobs;
            }, state.blockedJobs.data),
          };
        }
        if (state.checkedOutJob.data === null || state.checkedOutJob.data.id !== action.jobId) {
          return { ...state, blockedJobs };
        }
        return {
          ...state,
          blockedJobs,
          checkedInJob: {
            data: action.payload.response,
            errors: {},
            fetchLevel: FetchLevel.success,
          },
          checkedOutJob: {
            data: null,
            errors: {},
            fetchLevel: FetchLevel.success,
          },
          nextCheckedOutJob: {
            data: null,
            errors: {},
            fetchLevel: FetchLevel.success,
          },
        };
      },
    },
    checkin: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveCheckinJob>): IJobState => {
        if (action.payload.errors !== null) {
          return {
            ...state,
            checkedInJob: {
              ...state.checkedInJob,
              errors: action.payload.errors,
              fetchLevel: FetchLevel.failure,
            },
          };
        }

        return {
          ...state,
          blockedJobs: {
            ...state.blockedJobs,
            data: [...state.blockedJobs.data.filter(v => v.id !== state.checkedOutJob.data!.id)],
          },
          checkedInJob: {
            ...state.checkedInJob,
            data: action.payload.response,
            errors: {},
            fetchLevel: FetchLevel.success,
          },
          nextCheckedOutJob: {
            ...state.nextCheckedOutJob,
            data: null,
            errors: {},
            fetchLevel: FetchLevel.prompted,
          },
        };
      },
      request: (state: IJobState): IJobState => {
        return {
          ...state,
          checkedInJob: { ...state.checkedInJob, fetchLevel: FetchLevel.fetching, errors: {} },
        };
      },
    },
    checkout: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveCheckoutJob>): IJobState => {
        if (action.payload.errors !== null) {
          return {
            ...state,
            checkedOutJob: {
              ...state.checkedOutJob,
              errors: action.payload.errors,
              fetchLevel: FetchLevel.failure,
            },
            lastFailedCheckoutJobId: action.payload.jobId,
          };
        }

        return {
          ...state,
          blockedJobs: {
            ...state.blockedJobs,
            data: state.blockedJobs.data.reduce((jobs, job, index) => {
              if (job.id === action.payload.jobId) {
                return [
                  ...state.blockedJobs.data.slice(0, index),
                  {
                    ...job,
                    claimed_by: action.payload.response!.claimed_by,
                  },
                  ...state.blockedJobs.data.slice(index + 1),
                ];
              }
              return jobs;
            }, state.blockedJobs.data),
          },
          checkedInJob: {
            ...state.checkedInJob,
            data: null,
            errors: {},
            fetchLevel: FetchLevel.initial,
          },
          checkedOutJob: {
            ...state.checkedOutJob,
            data: action.payload.response!,
            fetchLevel: FetchLevel.success,
          },
          jobContent: {
            ...state.jobContent,
            data: null,
            errors: {},
            fetchLevel: FetchLevel.prompted,
          },
          lastFailedCheckoutJobId: null,
        };
      },
      request: (state: IJobState): IJobState => {
        return {
          ...state,
          checkedOutJob: { ...state.checkedOutJob, fetchLevel: FetchLevel.fetching, errors: {} },
        };
      },
    },
  },
  jobcontent: {
    get: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveGetJobContent>): IJobState => {
        if (action.payload.errors !== null) {
          return {
            ...state,
            jobContent: {
              ...state.jobContent,
              data: null,
              errors: action.payload.errors,
              fetchLevel: FetchLevel.failure,
            },
          };
        }

        return {
          ...state,
          jobContent: {
            ...state.jobContent,
            data: action.payload.data,
            fetchLevel: FetchLevel.success,
          },
        };
      },
      request: (state: IJobState): IJobState => {
        return {
          ...state,
          jobContent: { ...state.jobContent, errors: {}, fetchLevel: FetchLevel.fetching },
        };
      },
    },
  },
  jobs: {
    create: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveCreateJobs>): IJobState => {
        const jobs = action.payload
          .filter(v => v.response !== null)
          .map<IJob>((v: ICreateJobResult) => {
            return { ...v.response! };
          });
        const errors = action.payload
          .filter(v => v.errors !== null)
          .map<ICreateJobErrors>(v => {
            return { input: v.input, errors: v.errors! };
          });

        return {
          ...state,
          jobs: {
            ...state.jobs,
            createRequests: [],
            createResults: action.payload,
            errors,
            list: {
              ...state.jobs.list,
              data: { ...state.jobs.list.data, ...getJobMap(jobs) },
            },
          },
        };
      },
      request: (state: IJobState, action: ReturnType<typeof RequestCreateJobs>): IJobState => {
        return {
          ...state,
          documents: {
            ...state.documents,
            createResults: [],
          },
          jobs: {
            ...state.jobs,
            createRequests: action.payload,
            createResults: [],
            errors: [],
          },
        };
      },
    },
    get: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveGetJobs>): IJobState => {
        return {
          ...state,
          jobs: {
            ...state.jobs,
            list: {
              ...state.jobs.list,
              data: {
                ...state.jobs.list.data,
                ...getJobMap(action.payload),
              },
              fetchLevel: FetchLevel.success,
            },
          },
        };
      },
      request: (state: IJobState, action: ReturnType<typeof RequestGetJobs>): IJobState => {
        const fetchLevel = !action.payload ? FetchLevel.fetching : FetchLevel.refetching;

        return {
          ...state,
          jobs: {
            ...state.jobs,
            list: {
              ...state.jobs.list,
              fetchLevel,
            },
          },
        };
      },
    },
  },
  jobtypes: {
    get: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveGetJobtypes>): IJobState => {
        return {
          ...state,
          jobTypes: { ...state.jobTypes, fetchLevel: FetchLevel.success, data: action.payload },
        };
      },
      request: (state: IJobState): IJobState => {
        return {
          ...state,
          jobTypes: { ...state.jobTypes, fetchLevel: FetchLevel.fetching },
        };
      },
    },
  },
  nextjob: {
    checkout: {
      clear: (state: IJobState): IJobState => {
        if (state.nextCheckedOutJob.data === null) {
          return {
            ...state,
            nextCheckedOutJob: {
              ...state.nextCheckedOutJob,
              data: null,
              errors: {},
              fetchLevel: FetchLevel.initial,
            },
          };
        }

        return {
          ...state,
          checkedInJob: {
            ...state.checkedInJob,
            data: null,
            errors: {},
            fetchLevel: FetchLevel.initial,
          },
          checkedOutJob: {
            ...state.checkedOutJob,
            data: state.nextCheckedOutJob.data!,
            fetchLevel: FetchLevel.success,
          },
          jobContent: {
            ...state.jobContent,
            data: null,
            errors: {},
            fetchLevel: FetchLevel.prompted,
          },
          lastFailedCheckoutJobId: null,
          nextCheckedOutJob: {
            ...state.nextCheckedOutJob,
            data: null,
            errors: {},
            fetchLevel: FetchLevel.initial,
          },
        };
      },
      receive: (state: IJobState, action: ReturnType<typeof ReceiveCheckoutNextJob>): IJobState => {
        if (action.payload.errors !== null) {
          return {
            ...state,
            nextCheckedOutJob: {
              ...state.nextCheckedOutJob,
              errors: action.payload.errors,
              fetchLevel: FetchLevel.failure,
            },
          };
        }

        if (!action.payload.hasData) {
          return {
            ...state,
            nextCheckedOutJob: {
              ...state.nextCheckedOutJob,
              data: null,
              fetchLevel: FetchLevel.success,
            },
          };
        }

        return {
          ...state,
          nextCheckedOutJob: {
            ...state.nextCheckedOutJob,
            data: action.payload.response!,
            fetchLevel: FetchLevel.success,
          },
        };
      },
      request: (state: IJobState): IJobState => {
        return {
          ...state,
          nextCheckedOutJob: {
            ...state.nextCheckedOutJob,
            errors: {},
            fetchLevel: FetchLevel.fetching,
          },
        };
      },
    },
  },
};

export const run: Runner<IJobState, JobActions> = (
  state: IJobState,
  action: JobActions,
): IJobState => {
  const [kind, verb, task] = action.type
    .split("_")
    .reverse()
    .map(v => v.toLowerCase());
  if (!(kind in handlers)) {
    return state;
  }
  const kindHandlers = handlers[kind];

  if (!(verb in kindHandlers)) {
    return state;
  }
  const verbHandlers = kindHandlers[verb];

  if (!(task in verbHandlers)) {
    return state;
  }
  const taskHandler = verbHandlers[task];

  return taskHandler(state, action);
};
