import React from "react";
import { Rect } from "react-konva";

import { Point, Size } from "./types";

interface IProps {
  readonly label: string;
  readonly point: Point;
  readonly size: Size;
}

const COLOR_LEGEND = {
  image: {
    fill: "rgba(255.0, 0.0, 255.0, 0.15)",
    stroke: "purple",
  },
  math: {
    fill: "rgba(139.0, 69.0, 19.0, 0.15)",
    stroke: "rgba(139.0, 69.0, 19.0, 1.0)",
  },
  table: {
    fill: "rgba(255.0, 165.0, 0.0, 0.15)",
    stroke: "orange",
  },
  text: {
    fill: "rgba(0.0, 0.0, 255.0, 0.15)",
    stroke: "blue",
  },
  unknown: {
    fill: "rgba(0.0, 0.0, 0.0, 0.15)",
    stroke: "grey",
  },
};

export const Zone = ({ label, point, size }: IProps) => {
  let colors = COLOR_LEGEND.unknown;
  if (label in COLOR_LEGEND) {
    colors = COLOR_LEGEND[label];
  }
  return (
    <>
      <Rect strokeWidth={0.5} {...colors} {...point} {...size} />
    </>
  );
};
