import { IDataPoint, IDataPointGroup, IState, ITaxonomy, TaxonomyEntryType } from "../../types";
import { getActiveValue } from "../util";

export interface ISelectBlockAction {
  blockText: string;
  idRef: string;
  taxonomy: ITaxonomy;
  type: "SELECT BLOCK";
}

export const selectBlock = (state: IState, action: ISelectBlockAction): IState => {
  const activeValue = getActiveValue(state);
  if (activeValue === null) {
    return state;
  }
  const {
    activeDataPoint: { groupIndex, groupKey, index, key },
    dataPoints,
  } = state;
  if (!(key in action.taxonomy)) {
    return state;
  }
  if (dataPoints[groupKey].isGroup) {
    const group = dataPoints[groupKey] as IDataPointGroup;
    let updatedValue = {
      ...group.values[groupIndex][key][index],
      idRef: action.idRef,
    };
    if (action.taxonomy[key].type === TaxonomyEntryType.Pointer) {
      updatedValue = {
        ...updatedValue,
        value: action.blockText,
        valueRaw: action.blockText,
      };
    }
    return {
      ...state,
      dataPoints: {
        ...dataPoints,
        [groupKey]: {
          ...group,
          values: [
            ...group.values.slice(0, groupIndex),
            {
              ...group.values[groupIndex],
              [key]: [
                ...group.values[groupIndex][key].slice(0, index),
                updatedValue,
                ...group.values[groupIndex][key].slice(index + 1),
              ],
            },
            ...group.values.slice(groupIndex + 1),
          ],
        },
      },
    };
  } else if (groupKey === key) {
    const nonGroup = dataPoints[key] as IDataPoint;
    let updatedValue = {
      ...nonGroup.values[index],
      idRef: action.idRef,
    };
    if (action.taxonomy[key].type === TaxonomyEntryType.Pointer) {
      updatedValue = {
        ...updatedValue,
        value: action.blockText,
        valueRaw: action.blockText,
      };
    }
    return {
      ...state,
      dataPoints: {
        ...dataPoints,
        [key]: {
          ...nonGroup,
          values: [
            ...nonGroup.values.slice(0, index),
            updatedValue,
            ...nonGroup.values.slice(index + 1),
          ],
        },
      },
    };
  }
  return state;
};
