import { IUser } from "@ilabs/core";
import { withFormik, WithFormikConfig } from "formik";
import * as Yup from "yup";

import { registerUser } from "../../../../api";
import {
  IFormValues,
  RegisterForm,
} from "../../../../components/Workbench/App/Viewport/RegisterForm";
import { UserRules } from "../../../../validator-rules";

interface IFormProps {
  onUserRegister: (user: IUser) => void;
}

const config: WithFormikConfig<IFormProps, IFormValues> = {
  handleSubmit: async (values, { setSubmitting, setErrors, props }) => {
    const { user, errors } = await registerUser({ ...values, demo: false });
    if (errors !== null) {
      setErrors(errors);
      setSubmitting(false);

      return;
    }

    setSubmitting(false);
    props.onUserRegister(user!);
  },
  mapPropsToValues: () => {
    return {
      email: "",
      name: "",
      password: "",
    };
  },
  validationSchema: Yup.object().shape<IFormValues>({
    email: UserRules.email,
    name: UserRules.name,
    password: UserRules.password,
  }),
};

export const RegisterFormFormContainer = withFormik(config)(RegisterForm);
