import { IState } from "../../types";

export interface ILoadPersistedStateAction {
  persistedState: IState["dataPoints"];
  type: "LOAD PERSISTED STATE";
}

export const loadPersistedState = (state: IState, action: ILoadPersistedStateAction): IState => {
  return {
    ...state,
    dataPoints: action.persistedState,
  };
};
