import cx from "classnames";
import React from "react";

interface IProps {
  disabled: boolean;
  onClick: () => void;
}

export const AddButton = ({ disabled, onClick }: IProps) => (
  <a
    className={cx({ "text-success": !disabled, "text-muted": disabled })}
    href="#"
    onClick={e => {
      e.preventDefault();
      if (!disabled) {
        onClick();
      }
    }}
    title="Add"
  >
    <i className="fa fa-plus" />
  </a>
);
AddButton.displayName = "AddButton";
