import React from "react";
import { Col, Form, Row } from "reactstrap";
import { StateCallbacks } from "../../state";
import { IDataPoint, IState, ITaxonomy } from "../../types";
import { Answer } from "../Answer";
import { QuestionTitle } from "./QuestionTitle";

interface IProps {
  readonly active: IState["activeDataPoint"];
  readonly dataPoint: IDataPoint;
  readonly index: number;
  readonly stateCallbacks: StateCallbacks;
  readonly taxonomy: ITaxonomy;
}

export const ActiveQuestion = ({
  active,
  dataPoint,
  index,
  stateCallbacks: {
    onAddNewNonGroupedAnswer,
    onDuplicateNonGroupedAnswer,
    onRemoveNonGroupedAnswer,
    onSelectActiveNonGroupedDataPoint,
    onUpdateNonGroupedAnswer,
  },
  taxonomy,
}: IProps) => (
  <Form
    onSubmit={e => {
      e.preventDefault();
    }}
  >
    <QuestionTitle index={index} label={dataPoint.label} />
    <Row>
      <Col className="grouped-container" xs={12}>
        {dataPoint.values.map((v, i) => (
          <Answer
            active={active.key === dataPoint.key && active.index === i}
            dataPoint={dataPoint}
            disableAdd={!dataPoint.multiple}
            disableRemove={!dataPoint.multiple || dataPoint.values.length < 2}
            key={`answer-${i}`}
            index={i}
            label={dataPoint.label}
            onAddNewAnswer={() => {
              onAddNewNonGroupedAnswer(dataPoint.key);
            }}
            onDuplicateAnswer={() => {
              onDuplicateNonGroupedAnswer(dataPoint.key, v);
            }}
            onFocus={() => {
              onSelectActiveNonGroupedDataPoint(dataPoint.key, i);
            }}
            onRemoveAnswer={() => {
              onRemoveNonGroupedAnswer(dataPoint.key, i, dataPoint.type);
            }}
            onUpdateAnswer={(value: any, valueRaw: string) => {
              onUpdateNonGroupedAnswer(dataPoint.key, i, value, valueRaw);
            }}
            showLabel={false}
            showMultiple={dataPoint.multiple}
            taxonomyEntry={taxonomy[dataPoint.key]}
            value={v}
          />
        ))}
      </Col>
    </Row>
  </Form>
);
ActiveQuestion.displayName = "ActiveQuestion";
