import React, { useContext, useEffect } from "react";

import { Link, Redirect, RouteComponentProps } from "react-router-dom";

import { SetCurrentUser } from "../../../../actions/user";
import { UserContext } from "../../../../context-containers";
// tslint:disable-next-line:max-line-length
import { RegisterFormFormContainer } from "../../../../form-containers/Workbench/App/Viewport/RegisterForm";
import { AuthLevel } from "../../../../types";

export interface IOwnProps extends RouteComponentProps<{}> {}

export type Props = Readonly<IOwnProps>;

export function Register(props: Props) {
  const {
    actions: { SetTitle },
    dispatch,
    state: { authLevel },
  } = useContext(UserContext)!;

  useEffect(() => SetTitle("Register"), []);

  if (authLevel === AuthLevel.authenticated) {
    return <Redirect to="/" />;
  }

  return (
    <div className="flex-fill p-3">
      <div className="alert alert-secondary">
        <h4>Welcome!</h4>
        <p>Already have an account?</p>
        <hr />
        <Link tabIndex={1} to="/login" className="btn btn-secondary">
          Log In
        </Link>
      </div>
      <RegisterFormFormContainer onUserRegister={v => dispatch(SetCurrentUser({ user: v }))} />
    </div>
  );
}
