import { withHistory } from "../../../../util/withHistory";
import {
  autoShrinkZone,
  doZonesOverlap,
  isPointInZone,
  isZoneInSelection,
  mapFromApiZone,
} from "../../helpers";
import { IZone } from "../../types";
import { Action, CurrentAction, IPageState, ResizeHandlerPosition } from "../state";

// tslint:disable-next-line: cyclomatic-complexity
export const page = (autoShrink: boolean, currentLabel: string) => (
  state: IPageState,
  action: Action,
): IPageState => {
  switch (action.type) {
    case "KEY PRESSED": {
      switch (action.key) {
        case "backspace":
        case "delete":
        case "d": {
          action.preventDefault();
          // delete the active zones
          if (state.activeZones.length < 1) {
            return state;
          }
          return {
            ...state,
            activeZones: [],
            zones: state.zoneHistory.push(
              state.zones.filter((z, i) => state.activeZones.indexOf(i) < 0),
            ),
          };
        }
        case "y":
        case "z": {
          if (action.ctrlKey) {
            action.preventDefault();
            if ((action.key === "z" && action.shiftKey) || action.key === "y") {
              // redo action is Ctrl+Y or Shift+Ctrl+Z
              const nextZones = state.zoneHistory.redo();
              if (nextZones === state.zones) {
                return state;
              }
              return { ...state, activeZones: [], zones: nextZones };
            }
            // undo action is Ctrl+Z
            const previousZones = state.zoneHistory.undo();
            if (previousZones === state.zones) {
              return state;
            }
            return { ...state, activeZones: [], zones: previousZones };
          }
          return state;
        }
        case "a":
          if (action.ctrlKey) {
            action.preventDefault();
            const activeZones = [];
            for (let i = 0; i < state.zones.length; i++) {
              activeZones.push(i);
            }
            return {
              ...state,
              activeZones,
            };
          }
          return state;
        default:
          return state;
      }
    }
    case "MOUSE DOWN": {
      if (state.activeZones.length === 1 && action.resizeHandlerPosition !== null) {
        switch (action.resizeHandlerPosition) {
          case ResizeHandlerPosition.BOTTOM:
            return {
              ...state,
              currentAction: CurrentAction.RESIZING_BOTTOM,
              firstDragEvent: true,
              previousMouseLocation: action.location,
            };
          case ResizeHandlerPosition.LEFT:
            return {
              ...state,
              currentAction: CurrentAction.RESIZING_LEFT,
              firstDragEvent: true,
              previousMouseLocation: action.location,
            };
          case ResizeHandlerPosition.RIGHT:
            return {
              ...state,
              currentAction: CurrentAction.RESIZING_RIGHT,
              firstDragEvent: true,
              previousMouseLocation: action.location,
            };
          case ResizeHandlerPosition.TOP:
            return {
              ...state,
              currentAction: CurrentAction.RESIZING_TOP,
              firstDragEvent: true,
              previousMouseLocation: action.location,
            };
          default:
            break;
        }
      }
      // check if we are clicking on a zone to move it
      const selectedZoneIndex = state.zones.findIndex(isPointInZone(action.location));
      if (selectedZoneIndex >= 0) {
        if (state.activeZones.indexOf(selectedZoneIndex) >= 0) {
          // if ctrl is pressed, deselect this zone
          if (action.ctrlKey) {
            const activeZones = state.activeZones.filter(z => z !== selectedZoneIndex);
            return {
              ...state,
              activeZones,
              currentAction: CurrentAction.HOVERING_ZONE,
              previousMouseLocation: action.location,
            };
          }
          return {
            ...state,
            currentAction: CurrentAction.MOVING,
            firstDragEvent: true,
            previousMouseLocation: action.location,
          };
        }
        if (action.ctrlKey || action.shiftKey) {
          return {
            ...state,
            activeZones: state.activeZones.concat([selectedZoneIndex]),
            currentAction: CurrentAction.MOVING,
            firstDragEvent: true,
            previousMouseLocation: action.location,
          };
        }
        return {
          ...state,
          activeZones: [selectedZoneIndex],
          currentAction: CurrentAction.MOVING,
          firstDragEvent: true,
          previousMouseLocation: action.location,
        };
      }
      return {
        ...state,
        activeZones: [],
        currentAction: CurrentAction.CREATING_ZONE,
        previousMouseLocation: action.location,
        selection: {
          endx: action.location.x,
          endy: action.location.y,
          startx: action.location.x,
          starty: action.location.y,
        },
      };
    }
    case "MOUSE LEAVE": {
      if (
        state.currentAction !== CurrentAction.HOVERING_NOTHING ||
        state.previousMouseLocation ||
        state.selection
      ) {
        return {
          ...state,
          currentAction: CurrentAction.HOVERING_NOTHING,
          firstDragEvent: false,
          hoveredZone: -1,
          previousMouseLocation: null,
          selection: null,
        };
      }
      return state;
    }
    case "MOUSE MOVE": {
      switch (state.currentAction) {
        case CurrentAction.CREATING_ZONE:
        case CurrentAction.SELECTING: {
          if (!state.selection) {
            if (state.firstDragEvent) {
              return { ...state, firstDragEvent: false };
            }
            return state;
          }
          const newSelection = {
            ...state.selection,
            endx: action.location.x,
            endy: action.location.y,
          };
          const intersectingZones: Array<number> = [];
          for (let i = 0; i < state.zones.length; i++) {
            if (isZoneInSelection(newSelection)(state.zones[i])) {
              intersectingZones.push(i);
            }
          }
          return {
            ...state,
            activeZones: intersectingZones,
            currentAction:
              intersectingZones.length > 0 ? CurrentAction.SELECTING : CurrentAction.CREATING_ZONE,
            firstDragEvent: false,
            previousMouseLocation: action.location,
            selection: newSelection,
          };
        }
        case CurrentAction.MOVING: {
          if (state.previousMouseLocation && state.activeZones.length > 0) {
            const diff = {
              x: action.location.x - state.previousMouseLocation.x,
              y: action.location.y - state.previousMouseLocation.y,
            };
            const staticZones: Array<IZone> = [];
            const updatedZones = state.zones.map((zone, index) => {
              if (state.activeZones.indexOf(index) < 0) {
                staticZones.push(zone);
                return zone;
              }
              return {
                ...zone,
                maxX: zone.maxX + diff.x,
                maxY: zone.maxY + diff.y,
                minX: zone.minX + diff.x,
                minY: zone.minY + diff.y,
              };
            });
            for (const activeIndex of state.activeZones) {
              const activeZone = updatedZones[activeIndex];
              if (
                activeZone.minX < 0 ||
                activeZone.minY < 0 ||
                activeZone.maxX > action.imageSize.width ||
                activeZone.maxY > action.imageSize.height
              ) {
                return {
                  ...state,
                  firstDragEvent: false,
                  previousMouseLocation: action.location,
                };
              }
              const intersection = !!staticZones.find(doZonesOverlap(activeZone));
              if (intersection) {
                return {
                  ...state,
                  firstDragEvent: false,
                  previousMouseLocation: action.location,
                };
              }
            }
            return {
              ...state,
              firstDragEvent: false,
              previousMouseLocation: action.location,
              zones: state.firstDragEvent
                ? state.zoneHistory.push(updatedZones)
                : state.zoneHistory.replace(updatedZones),
            };
          }
          return {
            ...state,
            firstDragEvent: false,
            previousMouseLocation: action.location,
          };
        }
        case CurrentAction.HOVERING_NOTHING: {
          const intersectingZoneIndex = state.zones.findIndex(isPointInZone(action.location));
          if (intersectingZoneIndex >= 0) {
            return {
              ...state,
              currentAction: CurrentAction.HOVERING_ZONE,
              firstDragEvent: false,
              hoveredZone: intersectingZoneIndex,
            };
          }
          return state;
        }
        case CurrentAction.HOVERING_ZONE: {
          const intersectingZoneIndex = state.zones.findIndex(isPointInZone(action.location));
          if (intersectingZoneIndex < 0) {
            return {
              ...state,
              currentAction: CurrentAction.HOVERING_NOTHING,
              firstDragEvent: false,
              hoveredZone: -1,
            };
          } else if (intersectingZoneIndex !== state.hoveredZone) {
            return {
              ...state,
              firstDragEvent: false,
              hoveredZone: intersectingZoneIndex,
            };
          }
          return state;
        }
        case CurrentAction.RESIZING_BOTTOM: {
          if (state.previousMouseLocation) {
            const activeZone = state.zones[state.activeZones[0]];
            const updatedZone = {
              ...activeZone,
              maxY: activeZone.maxY - (state.previousMouseLocation.y - action.location.y),
            };
            if (updatedZone.maxY - updatedZone.minY < 10) {
              return { ...state, previousMouseLocation: action.location };
            }
            // check for intersection
            const overlapFunction = doZonesOverlap(updatedZone);
            for (let i = 0; i < state.zones.length; i++) {
              if (i === state.activeZones[0]) {
                continue;
              }
              if (overlapFunction(state.zones[i])) {
                return {
                  ...state,
                  previousMouseLocation: action.location,
                };
              }
            }
            const updatedZones = [
              ...state.zones.slice(0, state.activeZones[0]),
              updatedZone,
              ...state.zones.slice(state.activeZones[0] + 1),
            ];
            return {
              ...state,
              firstDragEvent: false,
              previousMouseLocation: action.location,
              zones: state.firstDragEvent
                ? state.zoneHistory.push(updatedZones)
                : state.zoneHistory.replace(updatedZones),
            };
          }
        }
        case CurrentAction.RESIZING_LEFT: {
          if (state.previousMouseLocation) {
            const activeZone = state.zones[state.activeZones[0]];
            const updatedZone = {
              ...activeZone,
              minX: activeZone.minX - (state.previousMouseLocation.x - action.location.x),
            };
            if (updatedZone.maxX - updatedZone.minX < 10) {
              return { ...state, previousMouseLocation: action.location };
            }
            // check for intersection
            const overlapFunction = doZonesOverlap(updatedZone);
            for (let i = 0; i < state.zones.length; i++) {
              if (i === state.activeZones[0]) {
                continue;
              }
              if (overlapFunction(state.zones[i])) {
                return {
                  ...state,
                  previousMouseLocation: action.location,
                };
              }
            }
            const updatedZones = [
              ...state.zones.slice(0, state.activeZones[0]),
              updatedZone,
              ...state.zones.slice(state.activeZones[0] + 1),
            ];
            return {
              ...state,
              firstDragEvent: false,
              previousMouseLocation: action.location,
              zones: state.firstDragEvent
                ? state.zoneHistory.push(updatedZones)
                : state.zoneHistory.replace(updatedZones),
            };
          }
        }
        case CurrentAction.RESIZING_RIGHT: {
          if (state.previousMouseLocation) {
            const activeZone = state.zones[state.activeZones[0]];
            const updatedZone = {
              ...activeZone,
              maxX: activeZone.maxX - (state.previousMouseLocation.x - action.location.x),
            };
            if (updatedZone.maxX - updatedZone.minX < 10) {
              return { ...state, previousMouseLocation: action.location };
            }
            // check for intersection
            const overlapFunction = doZonesOverlap(updatedZone);
            for (let i = 0; i < state.zones.length; i++) {
              if (i === state.activeZones[0]) {
                continue;
              }
              if (overlapFunction(state.zones[i])) {
                return {
                  ...state,
                  previousMouseLocation: action.location,
                };
              }
            }
            const updatedZones = [
              ...state.zones.slice(0, state.activeZones[0]),
              updatedZone,
              ...state.zones.slice(state.activeZones[0] + 1),
            ];
            return {
              ...state,
              firstDragEvent: false,
              previousMouseLocation: action.location,
              zones: state.firstDragEvent
                ? state.zoneHistory.push(updatedZones)
                : state.zoneHistory.replace(updatedZones),
            };
          }
        }
        case CurrentAction.RESIZING_TOP: {
          if (state.previousMouseLocation) {
            const activeZone = state.zones[state.activeZones[0]];
            const updatedZone = {
              ...activeZone,
              minY: activeZone.minY - (state.previousMouseLocation.y - action.location.y),
            };
            if (updatedZone.maxY - updatedZone.minY < 10) {
              return { ...state, previousMouseLocation: action.location };
            }
            // check for intersection
            const overlapFunction = doZonesOverlap(updatedZone);
            for (let i = 0; i < state.zones.length; i++) {
              if (i === state.activeZones[0]) {
                continue;
              }
              if (overlapFunction(state.zones[i])) {
                return {
                  ...state,
                  previousMouseLocation: action.location,
                };
              }
            }
            const updatedZones = [
              ...state.zones.slice(0, state.activeZones[0]),
              updatedZone,
              ...state.zones.slice(state.activeZones[0] + 1),
            ];
            return {
              ...state,
              firstDragEvent: false,
              previousMouseLocation: action.location,
              zones: state.firstDragEvent
                ? state.zoneHistory.push(updatedZones)
                : state.zoneHistory.replace(updatedZones),
            };
          }
        }
      }
      return state;
    }
    case "MOUSE UP": {
      const intersectingZoneIndex = state.zones.findIndex(isPointInZone(action.location));
      const newCurrentAction =
        intersectingZoneIndex >= 0 ? CurrentAction.HOVERING_ZONE : CurrentAction.HOVERING_NOTHING;
      const newZones: Array<IZone> = [];
      if (state.selection && state.activeZones.length < 1 && state.image) {
        const newZone = autoShrinkZone(
          {
            confidence: 1.0,
            humanGenerated: true,
            label: currentLabel.length > 0 ? currentLabel : "text",
            maxX: Math.max(state.selection.endx, state.selection.startx),
            maxY: Math.max(state.selection.endy, state.selection.starty),
            minX: Math.min(state.selection.endx, state.selection.startx),
            minY: Math.min(state.selection.endy, state.selection.starty),
          },
          state.image,
          state.noiseReductionLevel >= 0 ? state.noiseReductionLevel : 2,
          autoShrink,
        );
        const height = Math.ceil(newZone.maxY) - Math.floor(newZone.minY);
        const width = Math.ceil(newZone.maxX) - Math.floor(newZone.minX);
        if (width > 1 && height > 1) {
          // add a new zone
          newZones.push(newZone);
        }
      }
      if (
        state.selection ||
        state.currentAction !== newCurrentAction ||
        state.previousMouseLocation ||
        newZones.length > 0
      ) {
        return {
          ...state,
          activeZones: newZones.length > 0 ? [state.zones.length] : state.activeZones,
          currentAction: newCurrentAction,
          firstDragEvent: false,
          previousMouseLocation: null,
          selection: null,
          zones:
            newZones.length > 0
              ? state.zoneHistory.push(state.zones.concat(newZones))
              : state.zones,
        };
      }
      return state;
    }
    case "CHANGE CURRENT LABEL": {
      if (state.activeZones.length < 1) {
        return state;
      }
      const newZones = state.zones.reduce((prev, next, index) => {
        if (state.activeZones.indexOf(index) < 0) {
          return prev;
        }
        const newZone = { ...next, label: action.label };
        if (prev !== state.zones) {
          prev[index] = newZone;
          return prev;
        }
        return [...prev.slice(0, index), newZone, ...prev.slice(index + 1)];
      }, state.zones);
      if (newZones === state.zones) {
        return state;
      }
      return {
        ...state,
        zones: state.zoneHistory.push(newZones),
      };
    }
    case "POPULATE ZONES": {
      if (action.index !== state.index || state.loaded) {
        return state;
      }
      const zones = action.zones.map(mapFromApiZone);
      return {
        ...state,
        loaded: true,
        zoneHistory: withHistory(zones),
        zones,
      };
    }
    case "POPULATE PERSISTED ZONES": {
      if (action.index !== state.index) {
        return state;
      }
      return {
        ...state,
        loaded: true,
        zoneHistory: withHistory(action.zones),
        zones: action.zones,
      };
    }
    case "SET NOISE REDUCTION LEVEL":
      return {
        ...state,
        noiseReductionLevel: action.noiseReduction,
      };
    default:
      return state;
  }
};
