import React from "react";

interface IProps {
  currentPageCount: number;
  totalCount: number;
  visible: boolean;
}

export const JobCount = ({ currentPageCount, totalCount, visible }: IProps) => {
  if (!visible || currentPageCount < 1) {
    return null;
  }
  return (
    <div className="text-center">
      <small>
        Showing {currentPageCount} of {totalCount} job{totalCount > 1 ? "s" : ""}
      </small>
    </div>
  );
};
JobCount.displayName = "JobCount";
