import { IDataPoint, IDataPointGroup, IState, TaxonomyEntryType } from "../../types";
import { newDefaultValue } from "../util";

export interface IRemoveAnswerAction {
  groupIndex: number;
  groupKey: string;
  index: number;
  key: string;
  taxonomyEntry: TaxonomyEntryType;
  type: "REMOVE ANSWER";
}
export const removeAnswer = (state: IState, action: IRemoveAnswerAction): IState => {
  if (action.groupKey in state.dataPoints) {
    let activeDataPoint = state.activeDataPoint;
    let dataPoints = state.dataPoints;
    if (state.dataPoints[action.groupKey].isGroup) {
      let group = state.dataPoints[action.groupKey] as IDataPointGroup;
      if (
        group.values[action.groupIndex] === undefined ||
        !(action.key in group.values[action.groupIndex]) ||
        group.values[action.groupIndex][action.key][action.index] === undefined
      ) {
        return state;
      }
      if (group.values[action.groupIndex][action.key].length < 2) {
        activeDataPoint = {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index: 0,
          key: action.key,
        };
        group = {
          ...group,
          values: [
            ...group.values.slice(0, action.groupIndex),
            {
              ...group.values[action.groupIndex],
              [action.key]: [newDefaultValue(action.taxonomyEntry)],
            },
            ...group.values.slice(action.groupIndex + 1),
          ],
        };
      } else {
        activeDataPoint = {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index:
            state.activeDataPoint.index >= action.index
              ? state.activeDataPoint.index - 1
              : state.activeDataPoint.index,
          key: action.key,
        };
        group = {
          ...group,
          values: [
            ...group.values.slice(0, action.groupIndex),
            {
              ...group.values[action.groupIndex],
              [action.key]: [
                ...group.values[action.groupIndex][action.key].slice(0, action.index),
                ...group.values[action.groupIndex][action.key].slice(action.index + 1),
              ],
            },
            ...group.values.slice(action.groupIndex + 1),
          ],
        };
      }
      dataPoints = {
        ...dataPoints,
        [action.groupKey]: group,
      };
    } else if (action.groupKey === action.key) {
      let nonGroup = state.dataPoints[action.key] as IDataPoint;
      if (nonGroup.values[action.index] === undefined) {
        return state;
      }
      if (nonGroup.values.length < 2) {
        activeDataPoint = {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index: 0,
          key: action.key,
        };
        nonGroup = {
          ...nonGroup,
          values: [newDefaultValue(action.taxonomyEntry)],
        };
      } else {
        activeDataPoint = {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index:
            state.activeDataPoint.index >= action.index
              ? state.activeDataPoint.index - 1
              : state.activeDataPoint.index,
          key: action.key,
        };
        nonGroup = {
          ...nonGroup,
          values: [
            ...nonGroup.values.slice(0, action.index),
            ...nonGroup.values.slice(action.index + 1),
          ],
        };
      }
      dataPoints = {
        ...state.dataPoints,
        [action.key]: nonGroup,
      };
    }
    return {
      ...state,
      activeDataPoint,
      dataPoints,
    };
  }
  return state;
};
